import * as React from 'react';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import './SelectBox.sass'

export default function SelectBox({inputLabel = '', Option = '', OptionValue = '', SelectValue = '', SelectLabel = '', MinWidth = 80, HolderArray = []}) {
    const [age, setAge] = React.useState('');

    const handleChange = (event) => {
        setAge(event.target.value);
    };
    console.log(age);

    const SelectBoxTemp = () => {

        return (
            <div>
                <FormControl className="x" sx={{ m: '0 24px', minWidth: MinWidth }}>
                    <InputLabel id="demo-simple-select-autowidth-label" classes={`cCT-main-text`}>{inputLabel}</InputLabel>
                    <Select
                    className={` cCT-orange-text `}
                    labelId="demo-simple-select-autowidth-label"
                    id="demo-simple-select-autowidth"
                    value={age}
                    onChange={handleChange}
                    autoWidth
                    label={SelectLabel}
                    >
                        <MenuItem value="" className={`px-3 py-2  d-flex`}>
                            <em>None</em>
                        </MenuItem>
                        {HolderArray?.map((elem, index) => {
                            return <MenuItem className={`px-3 py-2 cCT-main-text~ d-flex align-items-center~`}  value={elem.address}>{elem.address}</MenuItem>
                        })}
                    </Select>
                </FormControl>
            </div>
        )
    }
    return SelectBoxTemp()
}
