import Icon from 'view/features/shared/Icon/Icon'
export default function MiniAgentCardWireFrame() {
    const miniAgentCardWireFrameTemp = () => {
        return(
            <div className='uCT-wire-frame-opacity-5 CT-mini-card uCT-scale-in-animation uCT-click'>
                <div className="position-relative uCT-f-radius-sB d-flex h-100 flex-column align-items-center justify-content-end CT-transform-scale">
                    {/* agent image */}
                    <div className='d-flex justify-content-center position-absolute CT-Image-Holder rounded-circle'>
                        <i className='cCT-layout-II rounded-circle CT-profile-img uCT-img-cover'></i>
                    </div>
                    <div className='cCT-layout-I shadow w-100 uCT-f-radius-sA CT-Holder-Content d-flex flex-column align-items-center justify-content-end'>
                        {/* Tags Section */}
                        <div className='d-flex mt-2'>
                            <div style={{ width: '48px', height: '34px' }} className='uCT-f-radius mr-2 shadow-sm aCT-wire-frame-loading cCT-layout-I'></div>
                            <div style={{ width: '80px', height: '34px' }} className='uCT-f-radius mr-2 shadow-sm aCT-wire-frame-loading cCT-layout-I'></div>
                        </div>
                        {/* agent Role */}
                        <p></p>
                        <p style={{ width: '180px' }} className={`aCT-wire-frame-loading fCT-wireFrame-hG cCT-layout-I uCT-f-radius`}></p>
                        {/* BTN Section 3rd */}
                        <div className="d-flex mb-4 px-4~">
                            {/* Message Icon */}
                            <div style={{ width: '257px', height: '39px' }} className="d-flex uCT-f-radius-sA  pr-sm-4 pr-xl-5 aCT-wire-frame-loading cCT-layout-I">
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
    return miniAgentCardWireFrameTemp()
}