import './OurPortfolio.feature.scss'
export default function OurPortfolioFeature(){

    const ourPortfolioTemp = () => {
        return(
            <div className="CT-our-portfolio-iFrame">
                <div className="position-absolute w-100" style={{height: "calc(100vh - 80px)"}}></div>
                <iframe id="aboutVideo" width="560" height="315" src="https://www.youtube-nocookie.com/embed/IdejM6wCkxA?modestbranding=1&amp;rel=0&amp;controls=0&amp;autoplay=1" title="YouTube video player" frameBorder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowFullScreen className='CT-our-portfolio'></iframe>
            </div>
        )
    }
    return ourPortfolioTemp()
}