import './HorizentalLine.sass'
export default function HorizentalLine({ classes = '' }) {
    const HorizentalLineTemp = () => {
        return (
            <div className='d-flex w-100 justify-content-center'>
                {classes == '' ? <div className="col-9 CT-Horizental-Line cCT-Gradian-Linear-Main p-0"></div> : false}
                {classes == 'sm' ? <div className="col-9 d-block CT-horizental-linear cCT-Gradian-Linear-Main p-0"></div> : false}
                {classes == 'md' ? <div className="col-9 d-block CT-horizental-md-linear cCT-Gradian-Linear-Main p-0"></div> : false}
            </div>
        )
    }
    return HorizentalLineTemp()
}