import './PropertyOverview.sass'
import Icon from '../../../shared/Icon/Icon'
import React, { useState, useEffect } from 'react';
export default function PropertyOverview({APIs={}}) {
    const [APIsBundles, setAPIsBundles] = useState({
          available_from: String,
          area: String,
          garage: String,
          bedroom: String,
          bathroom: String,
      });

      useEffect(() => {
          switch(APIs.APIsKey){
              case "PropertyDetails" :
                  setAPIsBundles(prevVal =>({...prevVal, 
                      available_from : APIs?.data?.overview?.available_from,
                      area : APIs?.data?.overview?.area,
                      garage : APIs?.data?.overview?.garage,
                      bedroom : APIs?.data?.overview?.bedroom,
                      bathroom : APIs?.data?.overview?.bathroom
                  }))
                  break;
          }
      }, [APIs]);
    const propertyOverviewTemp = () => {
        return (
            <div className="row m-0 d-flex flex-wrap fCT-Regular-sB flex-wrap cCT-gray-text pl-2 pl-md-5 h-100 text-capitalize text-nowrap justify-content-start">
                <div className="col-9 col-md-4 col-lg px-0">
                    <Icon iconName='iCT-updatedmoove' iconSize='iCT-sB mr-3' textPosition='right' spaces='pr-lg-5 pb-3 pb-md-4' innerText={APIsBundles.available_from}/>
                </div>
                <div className="col-6 col-md-4 col-lg px-0">
                    <Icon iconName='iCT-squarePerfootgreen' iconSize='iCT-sB mr-3' textPosition='right' spaces='pr-3 pb-3 pb-md-4' innerText={APIsBundles.area} />
                </div>
                <div className="col-6 col-md-4 col-lg px-0">
                    <Icon iconName='iCT-garagesblue' iconSize='iCT-sB mr-3' textPosition='right' spaces='pr-3 pb-3 pb-md-4' innerText={APIsBundles.garage} />
                </div>
                <div className="col-6 col-md-4 col-lg px-0">
                    <Icon iconName='iCT-bedRoomsblue' iconSize='iCT-sB mr-3' textPosition='right' spaces='pr-3 pb-3 pb-md-4' innerText={APIsBundles.bedroom} />
                </div>
                <div className="col-6 col-md-4 col-lg px-0">
                    <Icon iconName='iCT-bathRoomsgreen' iconSize='iCT-sB mr-3' textPosition='right' spaces='pr-3  pb-3 pb-md-4' innerText={APIsBundles.bathroom} />
                </div>
            </div>
        )
    }
    return propertyOverviewTemp()
}