import './top-center.popups.sass'
import { MediaCT } from 'view/viewLogic/view-logic'
import { useState, useEffect } from 'react'
import $ from 'jquery';
export default function TopCenterPopup({ advancedSearch, toggleOC=false, content}){
    // const [toggleBlurView , setToggleBlurView] = useState('hide')
    // const [toggleBlurPosition , setToggleBlurPosition] = useState('static')
    const [toggleOpenClose, setToggleOpenClose] = useState(false)
    const [removeContent, setRemoveContent] = useState(false)
    useEffect(() => { 
        setToggleOpenClose(toggleOC)
        toggleOC ? $('body').css('overflow', 'hidden') : $('body').css('overflow', 'visible')
        toggleOC ? setRemoveContent(true) : setTimeout(() => { setRemoveContent(false)}, 800)
    }, [toggleOC]);
    const TopCenterPopupTemp = ()=>{
        return(
            // <div className={`CT-z-Index~`} >
            //     <div onClick={() => animateTop == 0 ? closeFilter() : false} 
            //     className={`${animateTop == 0 ? `CT-show-blur CT-blur-Z-index` : `${toggleBlurView == 'hide' ? '' : 'CT-blur-Z-index'} CT-hide-blur`} uCT-t-6-all`}
            //     style={{position: toggleBlurPosition}}></div>
            //     <div  className={`CT-z-Index uCT-b-radius-sB position-relative uCT-t-6-all overflow-auto~ container-fluid`} style={{top: MediaCT('xl') ? animateTop == 0 ? 0 : -1000 : animateTop + 'px', height: '0px'}}>
            //         <div className="CT-height-holder col-9~ container uCT-t-2-all p-0 position-fixed uCT-b-radius-sB overflow-auto~" >
            //             {content}
            //         </div>
            //     </div>
            // </div>
            <>
                {removeContent ? <div id='topCenterPopup'  onClick={() => { advancedSearch()}}
                    className={`${toggleOpenClose ? 'CT-show-blur overflow-auto' : 'CT-hide-blur overflow-hidden' }  position-fixed  w-100 h-100 uCT-t-6-all`}
                >
                    <div onClick={(e) => { e.stopPropagation() }} className={`${toggleOpenClose ? 'CT-show-holder' : 'CT-hide-holder'} container px-md-4 p-0 position-relative uCT-t-6-all`}>
                        {removeContent ? <div>{content}</div> : false }
                    </div>
                </div> :false}
            </>
        )
    }
    return TopCenterPopupTemp()
}
// style = {{ minWidth: MediaCT('md') ? '100vw' : '0', , marginLeft: MediaCT('md') ? '0%' : '12.8%'}}
