import '../home.sass'
import MiniAgentCard from 'view/features/profiles/Mini-Agent-Card/MiniAgentCard'
import ContentSlider from 'view/layout/Components/sliders/ContentSlider/ContentSlider'
import { MediaCT } from 'view/viewLogic/view-logic'

export default function OurAgents({ APIs = new Object() }){
    const OurAgentsTemp = ()=>{
        return(
          <div className='pb-4 px-3'>
            <div className={`m-0 ${MediaCT('md') ? 'col pt-3 mt-3' : 'col-8 p-0'}`}>
              <p className={`${MediaCT('md') ? 'fCT-bold-sE mb-3' : 'mb-4 fCT-bold-54 pb-2'}`}>Our agents at your {MediaCT('md') ? '' : <br />} service</p>
              <p className={`${MediaCT('md') ? 'fCT-Regular-sB pl-1 mb-3' : 'mb-4 pl-4 fCT-Regular-sC '} cCT-gray-text`}>Making sure that you got every wish you had with finding the right house, amazing facilities, and live in a great city, is our ultimate goal.</p>
            </div>
            <div className='d-flex mt-4'>
              <ContentSlider content={
                  APIs?.data?.agents?.data?.data?.agent?.map((agents) => {
                    return <MiniAgentCard APIs={{ from: 'agents', data: { agents } }} key={agents.id} />
                  })
                } />
            </div>
          </div>
        )
    }
    return OurAgentsTemp()
}