import './PropertyDescription.sass'
import React, { useState, useEffect } from 'react';

export default function PropertyDescription({APIs={}}) {

      const [APIsBundles, setAPIsBundles] = useState({
          description: String

      });

      useEffect(() => {
          switch(APIs.APIsKey){
              case "PropertyDetails" :
                  setAPIsBundles(prevVal =>({...prevVal, 
                      description : APIs?.data?.description
                  }))
                  break;
          }
      }, [APIs]);
    const propertyDescriptionTemp = () => {
        return (
            <div className="fCT-Light-sB cCT-gray-text">
                <p className="m-0 w-100 pl-2 text-break pr-3 pl-md-5">
                    {APIsBundles.description}
                </p>
            </div>
        )
    }
    return propertyDescriptionTemp()
}