import '../home.sass'
import PropertyGridCard from 'view/features/property/Property-Grid-Card/PropertyGridCard'
import ContentSlider from 'view/layout/Components/sliders/ContentSlider/ContentSlider'
import { MediaCT } from 'view/viewLogic/view-logic'
import { useEffect, useState } from 'react'
// import ButtonRightIcon from 'view/features/shared/buttons/Button-Right-Icon/ButtonRightIcon'
export default function Portfolio({ APIs = new Object() }){
  // let ContentSliderArr = [<PropertyGridCard />, <PropertyGridCard />, <PropertyGridCard />, <PropertyGridCard />]
    const [hotPropertiesList, setHotPropertiesList] = useState()
    useEffect(()=>{
      setHotPropertiesList(APIs)
    }, [])
    const [APIsKey, setAPIsKey] = useState('portfolioHome')
    const PortfolioTemp = ()=>{
        return(
          <div className={`pb-4 m-0 ${MediaCT('md') ? 'pt-3 mt-3' : 'pt-0'} px-3`}>
            <div className={`p-0 ${MediaCT('md') ? 'col' : 'col-6'}`}>
              <p className={`${MediaCT('md') ? 'fCT-bold-sE mb-3' : 'fCT-bold-54 mb-4 pb-2'}`}>Our Listings Portfolio</p>
              <p className={`${MediaCT('md') ? 'mb-3 fCT-Regular-sB pl-1' : 'mb-4 fCT-Regular-sC pl-4'} cCT-gray-text`}>
                These are the latest properties in the Sales category. You can create the list using the latest listing shortcode and show items by specific categories.
              </p>
            </div>
            {/* <div key='1' className='d-flex justify-content-between'>
                <ContentSlider content={ContentSliderArr} />
              
            </div> */}
            <div>
              <>
                <ContentSlider content={
                  APIs?.data?.property?.data?.data?.properties?.map((property) => {
                    return <PropertyGridCard key={property?.id} APIs={{ APIsKey, data: { property } }} />
                  })
                } />
              </>
            </div>
          </div>
        )
    }
    return PortfolioTemp()
}