import { MediaCT } from 'view/viewLogic/view-logic'

export default function FluidAds(){
  const FluidAdTemp = ()=>{
  return (
            <div className={`${MediaCT('md') ? 'pt-4 mt-3 CT-holderFluidAd-IMG~ ' : 'pt-0 mb-5'}`}>
                 <i className='d-block imCT-fluidAdImg CT-holderFluidAd-IMG  uCT-img-cover'></i>
            </div>
        )
    }
    return FluidAdTemp()
}