export default function PageTitleWireframe(){
    const pageTitleTemp = () => {
        return (
            <div className='pt-md-5 pt-4 px-md-4 pb-md-4 pb-3 m-0 uCT-wire-frame-opacity-5'>
                <p style={{width: '300px' }} className='aCT-wire-frame-loading uCT-f-radius fCT-wireFrame-hG'></p>
                <p style={{ width: '150px' }} className=' mt-3 aCT-wire-frame-loading uCT-f-radius fCT-wireFrame-hB ml-2 mb-0 mt-2'></p>
                <div style={{ height: "2px" }} className='cCT-main-bg CT-underLine-title d-inline-block px-3'></div>
            </div>
        )
    }
    return pageTitleTemp()
}