import './PropertyGridCard.sass'
import Icon from 'view/features/shared/Icon/Icon'
import React, { useState, useEffect } from 'react';
import { Link } from "react-router-dom";
import ImagesSlider from 'view/layout/Components/sliders/ImagesSlider/images-slider.layout'

import { useParams } from "react-router-dom";


export default function PropertyGridCard({APIs = {}}) {


    const [APIsBundles, setAPIsBundles] = useState({
        id: Number,
        image: String,
        fullname: String,
        title: String,
        country_title: String,
        type_title : String,
        category_title : String,
        high_light_title : Number,
        type_title : String,
        bathroom : Number,
        bedroom : Number,
        area : Number,
        bathroom : Number,
        currency_title : Number,
        price : Number
    });
    useEffect(() => {
        switch(APIs.APIsKey){
            case "PropertiesList" :
            case "portfolioHome" :
                setAPIsBundles(prevVal =>({...prevVal, 
                    id : APIs?.data?.property?.id,
                    image : APIs?.data?.property?.user?.image,
                    properties_images: APIs?.data?.property?.images,
                    fullname : APIs?.data?.property?.user?.fullname,
                    title : APIs?.data?.property?.title,
                    country_title : APIs?.data?.property?.city?.title,
                    type_title  : APIs?.data?.property?.type?.title ,
                    category_title : APIs?.data?.property?.category?.title,
                    high_light_title : APIs?.data?.property?.high_light?.title,
                    bathroom : APIs?.data?.property?.bathroom,
                    bedroom : APIs?.data?.property?.bedroom,
                    area : APIs?.data?.property?.area,
                    currency_title : APIs?.data?.property?.currency?.title,
                    price : APIs?.data?.property?.price
                }))
                break;
            case "AgentProfile" :
                setAPIsBundles(prevVal =>({...prevVal, 
                    image : APIs?.data?.properties?.user?.image,
                    properties_images: APIs?.data?.properties?.images,
                    id : APIs?.data?.properties?.id,
                    fullname : APIs?.data?.properties?.user?.fullname,
                    title : APIs?.data?.properties?.title,
                    country_title : APIs?.data?.properties?.city?.country?.title,
                    type_title  : APIs?.data?.properties?.type?.title ,
                    category_title : APIs?.data?.properties?.category?.title,
                    high_light_title : APIs?.data?.properties?.high_light?.title,
                    bathroom : APIs?.data?.properties?.bathroom,
                    bedroom : APIs?.data?.properties?.bedroom,
                    area : APIs?.data?.properties?.area,
                    currency_title : APIs?.data?.properties?.currency?.title,
                    price : APIs?.data?.properties?.price
                }))
                break;
            case "AgencyProfile" :
                setAPIsBundles(prevVal =>({...prevVal, 
                    image : APIs?.data?.agencyProperties?.user?.image,
                    properties_images: APIs?.data?.agencyProperties?.images,
                    id : APIs?.data?.agencyProperties?.id,
                    fullname : APIs?.data?.agencyProperties?.user?.fullname,
                    title : APIs?.data?.agencyProperties?.title,
                    country_title : APIs?.data?.agencyProperties?.city?.country?.title,
                    type_title  : APIs?.data?.agencyProperties?.type?.title ,
                    category_title : APIs?.data?.agencyProperties?.category?.title,
                    high_light_title : APIs?.data?.agencyProperties?.high_light?.title,
                    bathroom : APIs?.data?.agencyProperties?.bathroom,
                    bedroom : APIs?.data?.agencyProperties?.bedroom,
                    area : APIs?.data?.agencyProperties?.area,
                    currency_title : APIs?.data?.agencyProperties?.currency?.title,
                    price : APIs?.data?.agencyProperties?.price
                }))
                break;
            case "PropertyDetails" :
                setAPIsBundles(prevVal =>({...prevVal, 
                    image : APIs?.data?.property?.user?.image,
                    properties_images: APIs?.data?.property?.images,
                    id : APIs?.data?.property?.id,
                    fullname : APIs?.data?.property?.user?.fullname,
                    title : APIs?.data?.property?.title,
                    country_title : APIs?.data?.property?.city?.country?.title,
                    type_title  : APIs?.data?.property?.type?.title ,
                    category_title : APIs?.data?.property?.category?.title,
                    high_light_title : APIs?.data?.property?.high_light?.title,
                    bathroom : APIs?.data?.property?.bathroom,
                    bedroom : APIs?.data?.property?.bedroom,
                    area : APIs?.data?.property?.area,
                    currency_title : APIs?.data?.property?.currency?.title,
                    price : APIs?.data?.property?.price
                }))
                break;
            case "MapView" :
                setAPIsBundles(prevVal =>({...prevVal, 
                    image : APIs?.data?.property?.user?.image,
                    properties_images: APIs?.data?.property?.images,
                    id : APIs?.data?.property?.id,
                    fullname : APIs?.data?.property?.user?.fullname,
                    title : APIs?.data?.property?.title,
                    country_title : APIs?.data?.property?.city?.country?.title,
                    type_title  : APIs?.data?.property?.type?.title ,
                    category_title : APIs?.data?.property?.category?.title,
                    high_light_title : APIs?.data?.property?.high_light?.title,
                    bathroom : APIs?.data?.property?.bathroom,
                    bedroom : APIs?.data?.property?.bedroom,
                    area : APIs?.data?.property?.area,
                    currency_title : APIs?.data?.property?.currency?.title,
                    price : APIs?.data?.property?.price
                }))
                break;
            }
    }, []);


    const PropertyCardTemp = () => {
        return (
            <Link key={APIsBundles?.id} to={`/property-details/${APIsBundles?.id}`}  className='uCT-mini-card cCT-dark-text LinkElemStyle'>
                {/* <PropertyCardWireFrame /> */}
                <div className="pb-2 cCT-white-bg uCT-f-radius-sB d-flex h-100 flex-column uCT-scale-in-animation shadow">
                    {/* Property Images Section 1st */}
                    <div className="d-flex align-items-center justify-content-center position-relative">
                        {/* Slider Holder */}
                        <div className="h-100 w-100 " >
                            {/* <ImagesSlider _ID={APIsBundles.id} propertyImages={APIs?.data?.agencyProperties?.images}/> */}
                            <ImagesSlider _ID={APIsBundles.id} propertyImages={APIsBundles.properties_images}/>
                        </div>
                        {/* Property Name And Icon Holder 2nd */}
                        <div className="d-flex px-2 px-sm-3 justify-content-center CT-Holder-Agency  position-absolute CT-property-section-holder CT-z-index">
                            {/* Logo Holder 1st Section */}
                            <div className="shadow-sm cCT-white-bg mr-2 d-flex w-100 align-items-center justify-content-start~ uCT-f-radius-sC p-2">
                                {APIsBundles.image ? 
                                <i style={{ backgroundImage: `url(${APIsBundles.image })` }}  className='uCT-img-contain iCT-sC'></i>
                                :
                                <i className='uCT-img-contain iCT-agencyPlaceholder iCT-sC'></i>
                                }
                                <p className="d-flex mb-0 ml-2 fCT-bold-sA uCT-Text-ellipsis" style={{textTransform: 'capitalize'}}>{APIsBundles.fullname ? APIsBundles.fullname : 'Agency Name'}</p>
                                {/* <div className='d-flex cCT-opacity-green-bg-20 rounded-circle p-2 ml-0 ml-auto uCT-click uCT-brightness'>
                                    <i className='uCT-img-cover iCT-sB iCT-callRotategreen'></i>
                                </div> */}
                            </div>
                            {/* Actions Icons Holder 2nd Section */}
                            <div className="shadow-sm cCT-white-bg d-flex align-items-center uCT-f-radius-sC p-2">
                                <Icon iconName='iCT-callRotategreen' spaces='p-2' bgColor='cCT-opacity-secondary-bg-20' radiusSize='rounded-circle' iconSize='iCT-sB' />
                                <div className='mx-2'></div>
                                <Icon iconName='iCT-comparemoove' spaces='p-2' bgColor='cCT-opacity-main-bg-20' radiusSize='rounded-circle' iconSize='iCT-sB' />
                            </div>
                        </div>
                    </div>
                    {/* Details Section 2nd */}
                    <div className='w-100'>
                        {/* Title Section */}
                        <div className="d-flex mt-3 justify-content-center flex-column px-4 pt-4" >
                            <p className='mb-2 fCT-Regular-sC CT-Text-ellipsis-2Line' style={{height: '47.4px', textTransform: 'capitalize'}}>{APIsBundles.title}</p>
                            <Icon iconName='iCT-locationblue' spaces='fCT-Regular-sB cCT-gray-text' radiusSize='rounded-circle' iconSize='iCT-sB mr-2' textPosition='right' innerText={APIsBundles.country_title} />
                        </div>
                        <div className="d-flex px-4">
                            {APIsBundles.type_title ? <div className='px-3 py-1 mt-2 cCT-main-text shadow-sm fCT-Light-sA cCT-support2-bg uCT-f-radius-sA mr-2'>{APIsBundles.type_title}</div> : false }
                            {APIsBundles.high_light_title ? <div className='px-3 py-1 mt-2 mr-2 cCT-white-text shadow-sm fCT-Light-sA cCT-opacity-main-bg-40 uCT-f-radius-sA ' style={{backgroundColor: 'orange', textTransform: 'capitalize' }}>{APIsBundles.high_light_title}</div> : false}
                            {APIsBundles.category_title ? <div className='px-3 py-1 mt-2 cCT-main-text shadow-sm fCT-Light-sA cCT-support2-bg uCT-f-radius-sA'>{APIsBundles.category_title}</div>: false}
                        </div>
                        {/* Ameneties Section */}
                        <div className='d-flex my-2 align-items-center justify-content-center fCT-Light-sA py-2 cCT-opacity-main-bg-10  cCT-gray-text'>
                                <Icon iconName='iCT-bathroom' radiusSize='rounded-circle' iconSize='iCT-sB' textPosition='bottom' innerText={'Bathroom ' + APIsBundles.bathroom} />
                                <div className='mb-0 CT-Horizontal-Line cCT-white-bg mx-3'></div>
                                <Icon iconName='iCT-bedroom' radiusSize='rounded-circle' iconSize='iCT-sB' textPosition='bottom' innerText={'Bedroom ' + APIsBundles.bedroom}  />
                                <div className='mb-0 CT-Horizontal-Line cCT-white-bg mx-3'></div>
                                <Icon iconName='iCT-size' radiusSize='rounded-circle' iconSize='iCT-sB' textPosition='bottom' innerText={'Sizeroom ' + APIsBundles.area}  />
                        </div>
                        {/* BTN Section */}
                        <div className='d-flex justify-content-center align-items-center w-100'>
                            <Icon iconName='iCT-pricemoove' spaces='cCT-main-text fCT-bold-sE mr-1' iconSize='iCT-sC mr-2' textPosition='right' innerText={APIsBundles.price}/>
                            <p className='mb-0 mt-1 cCT-gray-text fCT-bold-sC'>{APIsBundles.currency_title}</p>
                        </div>
                    </div>
                </div>
            </Link>
        )
    }
    return PropertyCardTemp()
}

