import './review-card.sass'
import React, { useState, useEffect } from 'react';
import { Link } from "react-router-dom";


export default function ReviewCard({APIs = {}}){

    const [APIsBundles, setAPIsBundles] = useState({
        id: String,
        description: String,
        fullname: String,
        dob: String,
        image: String,
        rating: Number,
    });

      useEffect(() => {
          switch(APIs.APIsKey){
              case "PropertyDetails" :
                  setAPIsBundles(prevVal =>({...prevVal, 
                      id : APIs?.data?.all_reviews?.user?.id,
                      description : APIs?.data?.all_reviews?.description,
                      fullname : APIs?.data?.all_reviews?.user?.fullname,
                      dob : APIs?.data?.all_reviews?.user?.dob,
                      image : APIs?.data?.all_reviews?.user?.image,
                      rating : APIs?.data?.all_reviews?.rating,
                  }))
                  break;
              case "AgencyProfile" :
                  setAPIsBundles(prevVal =>({...prevVal, 
                      id : APIs?.data?.agencyReviews?.user?.id,
                      description : APIs?.data?.agencyReviews?.description,
                      fullname : APIs?.data?.agencyReviews?.user?.fullname,
                      dob : APIs?.data?.agencyReviews?.user?.dob,
                      image : APIs?.data?.agencyReviews?.user?.image,
                      rating : APIs?.data?.agencyReviews?.rating,
                  }))
                  break;
              case "AgentProfile" :
                  setAPIsBundles(prevVal =>({...prevVal, 
                      id : APIs?.data?.agentReviews?.user?.id,
                      description : APIs?.data?.agentReviews?.description,
                      fullname : APIs?.data?.agentReviews?.user?.fullname,
                      dob : APIs?.data?.agentReviews?.user?.dob,
                      image : APIs?.data?.agentReviews?.user?.image,
                      rating : APIs?.data?.agentReviews?.rating,
                  }))
                  break;
          }
      }, [APIs]);
      

    // for (let rate = 0; rate <= APIsBundles.rating; rate++) {
    //     rateGold.push(<i key={rate} className='iCT-goldStar CT-star uCT-img-cover mr-1 my-1'></i>)
    // }
        
            const ReviewCardTemp = ()=>{
                return (
                <Link to={`/agent/${APIsBundles.id}`} className='d-flex CT-review-card-holder pb-4 uCT-scale-in-animation uCT-click '>
                    {/* image */}
                    <div className='d-flex position-relative CT-review-img-holder'>
                        <i className='shadow-sm uCT-border-sB-main iCT-x-agentProfile position-absolute rounded-circle CT-review-img cCT-smoke-bg uCT-img-cover' style={{ backgroundImage: `url(${APIsBundles.image})`}}></i>
                    </div>
                    {/* content */}
                    <div className='cCT-Gradian-Linear-supp shadow CT-review-card-content-holder ml-auto mt-auto d-flex flex-column uCT-f-radius-sB'>
                        <div className='CT-review-card-content ml-auto pt-3 pl-3 pr-3 d-flex'>
                            <div className={'mr-2~'}>
                                {/* name */}
                                <p className='m-0 h6'>{APIsBundles.fullname}</p>
                                {/* time */}
                                <p className='m-0 fCT-Light-sA pl-2 cCT-gray-text pt-1'>{APIsBundles.dob} </p>
                            </div>
                            {/* stars */}
                            <div className='d-flex ml-auto'>
                                {Array(APIsBundles?.rating).fill().map((elem, i) => {
                                    return <i key={i} className='iCT-goldStar CT-star uCT-img-cover mr-1 my-1'></i> 
                                })}
                                { typeof(APIsBundles?.rating) != 'function' ?  Array( 5 - APIsBundles?.rating ).fill().map((elem, i) => {
                                    return <i key={i} className='iCT-rategray CT-star uCT-img-cover mr-1 my-1'></i>
                                }):false}
                            </div>
                        </div>
                        {/* review comment */}
                        <p className='m-0 p-4 fCT-Light-sA cCT-dark-text'>
                            {APIsBundles.description}
                        </p>
                    </div>
                </Link>
        )
    }
    return ReviewCardTemp()
}