import Icon from '../../shared/Icon/Icon'
import './RatingOverview.sass'
import React, { useState, useEffect } from 'react';

export default function RatingOverview({APIs = {}}){
    const [reviewFeedBack, setReviewFeedBack] = useState()
    const [APIsBundles, setAPIsBundles] = useState({
      avg_review: Number,
      count_review: Number,
      count_review_5: Number,
      count_review_4: Number,
      count_review_3: Number,
      count_review_2: Number,
      count_review_1: Number
    });

    useEffect(() => {
        switch(APIs.APIsKey){
            case "AgencyProfile" :
                setAPIsBundles(prevVal =>({...prevVal, 
                    avg_review : APIs?.data?.reviews?.avg_review,
                    count_review : APIs?.data?.reviews?.count_review,
                    count_review_5 : APIs?.data?.reviews?.count_review_5,
                    count_review_4 : APIs?.data?.reviews?.count_review_4,
                    count_review_3 : APIs?.data?.reviews?.count_review_3,
                    count_review_2 : APIs?.data?.reviews?.count_review_2,
                    count_review_1 : APIs?.data?.reviews?.count_review_1
                }))
                break;
            case "AgentProfile" :
                setAPIsBundles(prevVal =>({...prevVal, 
                    avg_review : APIs?.data?.reviews?.avg_review,
                    count_review : APIs?.data?.reviews?.count_review,
                    count_review_5 : APIs?.data?.reviews?.count_review_5,
                    count_review_4 : APIs?.data?.reviews?.count_review_4,
                    count_review_3 : APIs?.data?.reviews?.count_review_3,
                    count_review_2 : APIs?.data?.reviews?.count_review_2,
                    count_review_1 : APIs?.data?.reviews?.count_review_1
                }))
                break;
            case "PropertyDetails" :
                setAPIsBundles(prevVal =>({...prevVal, 
                    avg_review : APIs?.data?.reviews?.avg_review,
                    count_review : APIs?.data?.reviews?.count_review,
                    count_review_5 : APIs?.data?.reviews?.count_review_5,
                    count_review_4 : APIs?.data?.reviews?.count_review_4,
                    count_review_3 : APIs?.data?.reviews?.count_review_3,
                    count_review_2 : APIs?.data?.reviews?.count_review_2,
                    count_review_1 : APIs?.data?.reviews?.count_review_1
                }))
                break;
        }
    }, [APIs]);
    
    // Review Feedback Handler
    useEffect(() => {
        switch (Math.round(APIsBundles.avg_review)) {
          case 5:
            setReviewFeedBack('Excellent')
            break;
          case 4:
            setReviewFeedBack('Very Good')
            break;
          case 3:
            setReviewFeedBack('Good')
            break;
          case 2:
            setReviewFeedBack('Normal')
            break;
          case 1:
            setReviewFeedBack('Poor')
            break;
          default:
            setReviewFeedBack('Poor')
            break;
        }

    }, [APIsBundles?.avg_review]);

  const RatingOverviewTemp = ()=>{
  return(
          <div className='pl-md-4 pl-2 d-flex align-items-start flex-column flex-xl-row'>
            <div className='pr-5 pb-xl-0 pb-4 pl-md-4'>
              <div className='d-flex pb-3 align-items-center cCT-gray-text'>
                <p className='mb-0 pr-4 fCT-bold-64'>{APIsBundles.avg_review}</p>
                <div className='d-flex flex-column'>
                  <p className='mb-0 fCT-bold-sF'>{reviewFeedBack }</p>
                  <p className='mb-0 fCT-bold-sB'>{APIsBundles.count_review}</p>
                </div>
              </div>
              {/* Icons Holder */}
              <div className='d-flex '>
                { typeof(APIsBundles.avg_review) != 'function' ? Array(Math.round(APIsBundles.avg_review)).fill().map((elem, i) => {
                    return <Icon iconName='iCT-rategold' iconSize='iCT-sC uCT-img-100'spaces='mr-2 fCT-Regular-sC'/>
                }) : false}
                { typeof(APIsBundles.avg_review) != 'function' ? Array( 5 - Math.round(APIsBundles.avg_review) ).fill().map((elem, i) => {
                    return <Icon iconName='iCT-rategray' iconSize='iCT-sC uCT-img-100'spaces='mr-2 fCT-Regular-sC'/>
                }) : false}
              </div>
            </div>
            {/* Progress Bar Section */}
            <div className='d-flex flex-column w-100'>
              {/* Start 5 Star */}
              <div className='d-flex align-items-center cCT-gray-text mb-2'>
                {/* Number of Rate Star */}
                <div className='d-flex align-items-center '>
                    <Icon textPosition='left' iconName='iCT-rategray'iconSize='ml-1 iCT-sA uCT-img-100'spaces='fCT-bold-sB' innerText='5'/> 
                </div>
                {/* Progress Bar */}
                <div className='CT-progress-bar w-100 uCT-f-radius-sA mx-2 shadow-sm'>
                  <div className='CT-rate-bar cCT-Gradian-Linear-Gold uCT-f-radius-sA uCT-img-100' style={{maxWidth: APIsBundles?.count_review_5 ? (APIsBundles?.count_review_5 * 100 / APIsBundles.count_review) : 0 + '%'}}></div>
                </div>
                {/* Count Of People */}
                <p className='mb-0 fCT-bold-sB'>{APIsBundles.count_review_5}</p>
              </div>
              {/* End 5 Start */}

              {/* Start 4 Star */}
              <div className='d-flex align-items-center cCT-gray-text mb-2'>
              {/* Number of Rate Star */}
              <div className='d-flex align-items-center '>
                  <Icon textPosition='left' iconName='iCT-rategray'iconSize='ml-1 iCT-sA uCT-img-100'spaces='fCT-bold-sB' innerText='4'/> 
              </div>
              {/* Progress Bar */}
              <div className='CT-progress-bar w-100 uCT-f-radius-sA mx-2 shadow-sm'>
                <div className='CT-rate-bar cCT-Gradian-Linear-Gold uCT-f-radius-sA uCT-img-100' style={{maxWidth: APIsBundles?.count_review_4 ? (APIsBundles?.count_review_4 * 100 / APIsBundles.count_review) : 0 + '%'}}></div>
              </div>
              {/* Count Of People */}
              <p className='mb-0 fCT-bold-sB'>{APIsBundles.count_review_4}</p>
              </div>
              {/* End 4 Start */}

              {/* Start 3 Star */}
              <div className='d-flex align-items-center cCT-gray-text mb-2'>
              {/* Number of Rate Star */}
              <div className='d-flex align-items-center '>
                  <Icon textPosition='left' iconName='iCT-rategray'iconSize='ml-1 iCT-sA uCT-img-100'spaces='fCT-bold-sB' innerText='3'/> 
              </div>
              {/* Progress Bar */}
              <div className='CT-progress-bar w-100 shadow-sm uCT-f-radius-sA mx-2'>
                <div className='CT-rate-bar cCT-Gradian-Linear-Gold uCT-f-radius-sA uCT-img-100' style={{maxWidth: APIsBundles?.count_review_3 ? (APIsBundles?.count_review_3 * 100 / APIsBundles.count_review) : 0 + '%'}}></div>
              </div>
              {/* Count Of People */}
              <p className='mb-0 fCT-bold-sB'>{APIsBundles.count_review_3}</p>
              </div>
              {/* End 3 Start */}

              {/* Start 2 Star */}
              <div className='d-flex align-items-center cCT-gray-text mb-2'>
              {/* Number of Rate Star */}
              <div className='d-flex align-items-center '>
                  <Icon textPosition='left' iconName='iCT-rategray'iconSize='ml-1 iCT-sA uCT-img-100'spaces='fCT-bold-sB' innerText='2'/> 
              </div>
              {/* Progress Bar */}
              <div className='CT-progress-bar w-100 shadow-sm CT-Holder-ProgressBar uCT-f-radius-sA mx-2 shadow-sm'>
                <div className='CT-rate-bar cCT-Gradian-Linear-Gold uCT-f-radius-sA uCT-img-100 CT-Holder-Bar ' style={{maxWidth: APIsBundles?.count_review_2 ? (APIsBundles?.count_review_2 * 100 / APIsBundles.count_review) : 0 + '%'}}></div>
              </div>
              {/* Count Of People */}
              <p className='mb-0 fCT-bold-sB'>{APIsBundles.count_review_2}</p>
              </div>
              {/* End 2 Start */}
              
              {/* Start 1 Star */}
              <div className='d-flex align-items-center cCT-gray-text mb-2'>
              {/* Number of Rate Star */}
              <div className='d-flex align-items-center '>
                  <Icon textPosition='left' iconName='iCT-rategray'iconSize='ml-1 iCT-sA uCT-img-100'spaces='fCT-bold-sB' innerText='1'/> 
              </div>
              {/* Progress Bar */}
              <div className='CT-progress-bar w-100 shadow-sm uCT-f-radius-sA mx-2'>
                <div className='CT-rate-bar cCT-Gradian-Linear-Gold uCT-f-radius-sA uCT-img-100' style={{maxWidth: APIsBundles?.count_review_1 ? (APIsBundles?.count_review_1 * 100 / APIsBundles.count_review) : 0 + '%'}}></div>
              </div>
              {/* Count Of People */}
              <p className='mb-0 fCT-bold-sB'>{APIsBundles.count_review_1}</p>
              </div>
              {/* End 1 Start */}
            </div>
          </div>
        )
    }
    return RatingOverviewTemp()
}