import './ConnectWithProperty.scss'
import Icon from '../../shared/Icon/Icon'
export default function ConnectWithProperty() {
    const connectWithPropertyTemp = () => {
        return (
            // property-holder 
            <div className="CT-property-holder h-100">
                {/* text-holder */}
                <div className="CT-connect-text-holder ">
                    <p className='mb-0 CT-font-bold cCT-dark-text'>Connect With </p>
                    <p className='mb-0 CT-font-bold cCT-dark-text imCT-line-group CT-connect-line'>Property Seekers Immediately</p>
                    <div className="mb-5">
                        <p className='mb-0 fCT-Regular-sE cCT-gray-text' > Join the biggest real estate portal in Dubai</p>
                    </div>
                    {/* Button Holder*/}
                    <div className="d-block d-sm-flex align-items-center CT-button-holder">
                        {/* Play Icon */}
                        <div className="">
                            <Icon iconName='iCT-play-black' iconSize='iCT-sC mr-2' textPosition='right' spaces='mb-4 mb-sm-0 justify-content-center' innerText='Play Our Showreel' textSpaces='fCT-Regular-sC cCT-gray-text' />
                        </div>
                        {/* Property Button */}
                        <div className="d-flex justify-content-center align-items-center ml-0 ml-sm-4">
                            <a href="#" className="px-4 uCT-f-radius-sC border-0 shadow btn CT-BTN-Holder cCT-Gradian-Linear-Main cCT-white-text d-flex align-items-center justify-content-center">
                                <i className="iCT-add-white uCT-img-cover iCT-sB "></i>
                                <p className='mb-0 ml-2 text-nowrap'> List Your Property </p>
                            </a>
                        </div>
                    </div>
                </div>
                {/* Image-holder */}
                <div className="CT-connect-image-holder imCT-CT-layer-I CT-layer-IV uCT-img-contain position-relative">
                    {/* Main Image */}
                    <div className="imCT-how-work-img CT-layer-I uCT-img-contain position-absolute"></div>
                    {/* Layer Two */}
                    <div className="imCT-how-work-bg-blue-2 CT-layer-II uCT-img-contain position-absolute"></div>
                    {/* Layer Three */}
                    <div className="imCT-how-work-bg-pink CT-layer-III uCT-img-contain position-absolute"></div>
                </div>
            </div>
        )
    }
    return connectWithPropertyTemp()
}