import './home.sass'
import HorizentalLine from 'view/features/shared/HorizentalLine/HorizentalLine'
import ButtonRightIcon from 'view/features/shared/buttons/Button-Right-Icon/ButtonRightIcon'
import CoverPage from 'view/features/deal360/home/CoverPage/CoverPage'
import AboutUs from 'view/features/deal360/home/AboutUs/AboutUs'
import Portfolio from 'view/features/deal360/home/Portfolio/Portfolio'
import FluidAds from 'view/features/advertisement/Fluid-Ads/FluidAds'
import Browse360VR from 'view/features/deal360/home/Browse360VR/Browse360VR'
import ExceptionalDevelopers from 'view/features/deal360/home/ExceptionalDevelopers/ExceptionalDevelopers'
import OurAgents from 'view/features/deal360/home/OurAgents/OurAgents'
import BecomeAgent from 'view/features/deal360/home/BecomeAgent/BecomeAgent'
import GoogleMapView from 'view/features/google-map/Google-Map/GoogleMapView'
import NearbyCities from 'view/features/deal360/home/NearbyCities/NearbyCities'
import ContactUsFeatures from 'view/features/deal360/home/Contact-Us/ContactUs.features'
import ControllersMapViewFeature from 'view/features/shared/ControllersMapView/ControllersMapView.feature'
import { MediaCT } from 'view/viewLogic/view-logic'
import { useEffect, useState } from 'react'
import { _GET_AllProperties } from 'Controllers/services/property/property.services'
import { _GET_AllAgencies } from "Controllers/services/agency/agency.services";
import { _GET_AllAgents } from "Controllers/services/agent/agent.services";

export default function Home(){
    const [APIsKey, setAPIsKey] = useState('homePage')
    const [hotPropertiesList, setHotPropertiesList] = useState()
    const [topAgenciesList, setTopAgenciesList] = useState()
    const [topAgentsList, setTopAgentsList] = useState()
     const [geoGraph, setGeoGraph] = useState({
        latitude : String,
        longitude : String,
        center_latitude : String,
        center_longitude : String,
    })   

    useEffect(() => {
      _GET_AllProperties('en', 8)
        .then(res => {
          setHotPropertiesList(res)
          window.scrollTo({
                top: 0,
                behavior: 'smooth'
            })
        }).catch()
    }, [])
    useEffect(() => {
      _GET_AllAgencies('en', 8)
        .then(res => {
          setTopAgenciesList(res)
        }).catch()
    }, [])
    useEffect(() => {
      _GET_AllAgents('en', 8)
        .then(res => {
          setTopAgentsList(res)
        }).catch()
    }, [])
    const cities = [<NearbyCities/>, <NearbyCities/>, <NearbyCities/>, <NearbyCities/>]
    const homeTemp = ()=>{
        return(
            <div className='container-fluid d-flex flex-column align-items-center p-0'>
                {/* CoverPage Section */}
                <section className='container-fluid p-0 m-0'>
                     <CoverPage />
                  {/* <iframe src="https://app.anyvision.com/viewer?o=I76w12hp&mode=thumbnail&autorotation=false&icon=360" allowFullScreen loading="lazy" className='d-flex border-0 uCT-f-radius-sC~ CT-holderMapView-IMG'>
                  </iframe> */}
                </section>
                {/* AboutUs Section */}
                <section className='container p-0'>
                    <AboutUs/>  
                    <ButtonRightIcon innerText='MORE ABOUT US' btnICON='iCT-ArrowRight-White' btnCOLOR='cCT-Gradian-Linear-Main' iconBgCOLOR='cCT-inherit-bg' position='center' />    
                    <div className='d-flex justify-content-center'>
                      <HorizentalLine classes={`${MediaCT('md') ? 'sm' : ''}`}/>
                    </div>
                </section>
                {/* Portfolio Section */}
                <section className='container p-0'>
                    <Portfolio APIs={{APIsKey, data: {property : hotPropertiesList}}}/>  
                    <ButtonRightIcon innerText='Load More Properties' btnICON='iCT-ArrowRight-White' btnCOLOR='cCT-Gradian-Linear-Main' iconBgCOLOR='cCT-inherit-bg' position='center' />      
                    <div className='d-flex justify-content-center'>
                      <HorizentalLine classes={`${MediaCT('md') ? 'sm' : ''}`}/>
                    </div>  
                </section>
                {/* FluidAds Section */}
                <section className='container-fluid p-0'>
                    <FluidAds/>
                     <div className='d-flex justify-content-center'>
                      <HorizentalLine classes={`${MediaCT('md') ? 'sm' : ''}`}/>
                    </div>    
                </section>
                {/* Browse360VR Section */}
                <section className='container p-0'>
                    <Browse360VR/>
                    <div className='d-flex justify-content-center'>
                      <HorizentalLine classes={`${MediaCT('md') ? 'sm' : ''}`}/>
                    </div>  
                </section>
                {/* Exceptional Developers Section */}
                <section className='container p-0'>
                    <ExceptionalDevelopers APIs={{APIsKey, data: {agencies : topAgenciesList}}}/>  
                    <ButtonRightIcon innerText='Load More Agencies' btnICON='iCT-ArrowRight-White' btnCOLOR='cCT-Gradian-Linear-Main' iconBgCOLOR='cCT-inherit-bg' position='center' />    
                     <div className='d-flex justify-content-center'>
                      <HorizentalLine classes={`${MediaCT('md') ? 'sm' : ''}`}/>
                    </div>
                </section>
                {/* OurAgents Section */}
                <section className='container  p-0'>
                    <OurAgents APIs={{APIsKey, data: {agents : topAgentsList}}}/>  
                    <ButtonRightIcon innerText='Load More Agents' btnICON='iCT-ArrowRight-White' btnCOLOR='cCT-Gradian-Linear-Main' iconBgCOLOR='cCT-inherit-bg' position='center' />    
                    <div className='d-flex justify-content-center'>
                      <HorizentalLine classes={`${MediaCT('md') ? 'sm' : ''}`}/>
                    </div>  
                </section>
                {/* BecomeAgent Section */}
                <section className='container d-flex flex-column  p-0 m-0'>
                  <BecomeAgent  />
                  <div className='d-flex justify-content-center'>
                    <HorizentalLine classes={`${MediaCT('md') ? 'sm' : ''}`}/>
                  </div>   
                </section>
                {/* MapView Section */}
                <section className='container w-100 overflow-hidden d-flex p-0 m-0' style={{height: '700px'}}>
                  {/* <MapView  /> */}
                    {/* <GoogleMapView APIs={hotPropertiesList?.data?.data?.properties}/> */}
                     <div className='d-flex justify-content-center'>
                      <HorizentalLine classes={`${MediaCT('lg') ? 'sm' : ''}`}/>
                    </div>  
                </section>
                {/* NearbyCities Section */}
                <section className='container px-3 p-0 m-0'>
                  <div className={`p-0 ${MediaCT('md') ? 'col' : 'col-8'}`}>
                    <p className={`mb-4 ${MediaCT('sm') ? 'fCT-bold-sE mt-3 pt-3' : 'fCT-Regular-sE'}`}>Nearby Cities</p>
                    <span className={`d-flex mb-4  ${MediaCT('sm') ? 'fCT-Regular-sB pl-1' : 'fCT-Regular-sD pl-4'} cCT-gray-text`}>Making sure that you got every wish you had with finding the right house, amazing facilities, and live in a great city, is our ultimate goal.</span>
                  </div>
                  <div className='d-flex overflow-auto'>
                    {cities.map((elem,index) => { return elem }) }
                  </div>
                  <div className='d-flex justify-content-center'>
                    <HorizentalLine classes={`${MediaCT('md') ? 'sm' : ''}`}/>
                  </div> 
                </section>
                {/* NearbyCities Section */}
                <section className='container p-0~ m-0'>
                  <ContactUsFeatures  />
                </section>
            </div>
        )
    }
    return homeTemp()
}
// uCT-scale-in-animation uCT-click