import { NavLink } from 'react-router-dom';
import Icon from '../../shared/Icon/Icon'
import React, { useState, useContext  } from 'react'
import $ from 'jquery';
import {login, finishAuthentication} from 'Controllers/services/Auth/deal360Auth/deal360Auth.services'
import { useHistory } from "react-router-dom";
export default function LoginForm() {
    let history = useHistory();
    const [inputstate, setInputstate] = useState({
        mailInput: false,
        passInput: false
    })
    const [deal360AuthVals, setDeal360AuthVals] = useState({
        username: '',
        password: '',
    })
    const [loginErr, setLoginErr] = useState('')
    const onLoginSubmit = (e)=>{
        e.preventDefault()
        if(deal360AuthVals.username && deal360AuthVals.password){
            login(deal360AuthVals.username, deal360AuthVals.password).then(res=>{
                if(!res.token){
                    setLoginErr(res.data.message)
                    return
                }
                finishAuthentication(res.token)
                history.push("/");
            })
        }
    }
    const handelChange = (e) =>{
        setDeal360AuthVals(prevVal =>({...prevVal, [e.target.name]: e.target.value }))
    }
    const loginFormTemp = () => {
        return (
            <div>
                <form onSubmit={onLoginSubmit}>
                    <div className="">
                        <p className="fCT-Light-sD m-0">welcom back</p>
                        <p className="fCT-Regular-sE mb-4">login with</p>
                    </div>
                    {/* UserName Or Email Input */}
                    <div className="mb-4">
                        {/* UserName Or Email Lable */}
                        <label className={`w-100 fCT-Regular-sC m-0 mb-2 ${inputstate.mailInput ? 'cCT-secondary-text' : 'cCT-gray-text'}`} >username or email</label>
                        {/* UserName Or Email Input */}
                        <input onFocus={() => { setInputstate((prevInputstate) => ({ ...prevInputstate, mailInput: true })) }} onBlur={() => { setInputstate((prevInputstate) => ({ ...prevInputstate, mailInput: false })) }
                        } className={`w-100 uCT-border-sA uCT-f-radius-sA fCT-Regular-sB p-2 ${inputstate.mailInput ? 'uCT-form-border-foucs' : 'uCT-form-border-blur'}`} type="text" 
                        onChange={handelChange}
                        placeholder="Username Or Email"  name='username' />
                    </div>
                    {/* Password Lable */}
                    <label className={`w-100 fCT-Regular-sC m-0 mb-2 ${inputstate.passInput ? 'cCT-secondary-text' : 'cCT-gray-text'}`}>password</label>
                    {/* Password Input */}
                    <div className="d-flex mb-2">
                        <input id='loginPassword' onFocus={() => { setInputstate((pervInputstate) => ({ ...pervInputstate, passInput: true })) }} onBlur={() => { setInputstate((prevInputstate) => ({ ...prevInputstate, passInput: false })) }}
                            className={`w-100 test uCT-border-r-none uCT-l-radius-sA fCT-Regular-sB p-2 cCT-gray-text ${inputstate.passInput ? 'uCT-form-border-foucs' : 'uCT-form-border-blur'}`} type="password" placeholder="Password" 
                            onChange={handelChange}
                            name='password'/>
                        {/* Password Icon */}
                        <div onClick={() => { $('#loginPassword').focus() }} className={`d-flex align-items-center cCT-white-bg uCT-r-radius-sA uCT-border-l-none px-4 test ${inputstate.passInput ? 'uCT-form-border-foucs' : 'uCT-form-border-blur'}`}>
                            <i className="uCT-click iCT-eyePasswordLogin iCT-sC uCT-img-cover"></i>
                        </div>
                    </div>
                    {/* Forget Password Link */}
                    <span className="uCT-click w-100 fCT-Regular-sC cCT-gray-text m-0 ">forget password?</span>
                    {/* Login Button */}
                    <div className="my-4">
                        {loginErr ? loginErr : false}
                    <button type="submit" className="w-100 text-capitalize border-0 cCT-Gradian-Linear-Main uCT-f-radius-sA fCT-Regular-sC cCT-smoke-text p-2">login</button>
                </div>
                </form>
                {/* Text */}
                <p className="fCT-Light-sE">or, sign up with</p>
                {/* Sign With  */}
                <div className="d-flex justify-content-start align-items-center mt-4 w-100">
                    {/* Apple Button  */}
                    <a className="text-decoration-none cCT-dark-text fCT-Regular-sD uCT-click uCT-brightness cCT-white-bg uCT-f-radius-sA mr-4 mr-lg-2 mr-xl-4 px-0 px-lg-4" href="https://www.apple.com">
                        <Icon textPosition='right' iconName='iCT-appleLogin' iconSize='iCT-sC' innerText='Apple' textSpaces='d-none d-lg-block p-3 p-lg-2 mt-lg-1' spaces='p-3 p-lg-0' />
                    </a>
                    {/* Google Button  */}
                    <a className="text-decoration-none cCT-dark-text fCT-Regular-sD uCT-click uCT-brightness cCT-white-bg uCT-f-radius-sA mr-4 mr-lg-2 mr-xl-4 px-0 px-lg-4" href="https://www.google.com">
                        <Icon textPosition='right' iconName='iCT-googleLogin' iconSize='iCT-sC' innerText='Google' textSpaces='d-none d-lg-block p-3 p-lg-2 mt-lg-1' spaces='p-3 p-lg-0' />
                    </a>
                    {/* Facebook Button  */}
                    <a className="text-decoration-none cCT-dark-text fCT-Regular-sD uCT-click uCT-brightness cCT-white-bg uCT-f-radius-sA px-0 px-lg-4" href="https://www.facebook.com">
                        <Icon textPosition='right' iconName='iCT-facebookLogin' iconSize='iCT-sC' innerText='Google' textSpaces='d-none d-lg-block p-3 p-lg-2 mt-lg-1' spaces='p-3 p-lg-0' />
                    </a>
                </div>
            </div>
        )
    }
    return loginFormTemp()
}