import Icon from '../../shared/Icon/Icon'
import React, { useState, useEffect } from 'react';

export default function Contacts({APIs = {}}){
  const ContactsTemp = ()=>{
  const [APIsBundles, setAPIsBundles] = useState({
          phone: String,
          website: Number,
          country: String
      });

      useEffect(() => {
          switch(APIs.APIsKey){
              case "AgencyProfile" :
                  setAPIsBundles(prevVal =>({...prevVal, 
                      phone : APIs?.data?.contact_card?.phone,
                      website : APIs?.data?.contact_card?.website,
                      country : APIs?.data?.country?.title,
                  }))
                  break;
              case "AgentProfile" :
                  setAPIsBundles(prevVal =>({...prevVal, 
                      phone : APIs?.data?.contact_card?.phone,
                      website : APIs?.data?.contact_card?.website,
                      country : APIs?.data?.address
                  }))
                  break;
          }
      }, [APIs]);

  return(
    <div className='pl-md-5 pl-2 cCT-main-text fCT-Regular-sB'>
            {/* Icons Holder */}
            <div className='cCT-gray-text d-flex flex-column align-items-start'>
                { APIsBundles.country ?
                  <Icon iconName='iCT-locationgray' iconSize='iCT-sB mr-3 uCT-img-100' spaces='mb-3 'textPosition='right' innerText={APIsBundles.country} /> 
                  : false
                }
                { APIsBundles.website ?
                  <Icon iconName='iCT-earthgray' iconSize='iCT-sB mr-3 uCT-img-100' spaces='mb-3 ' textPosition='right' innerText={APIsBundles.website}  /> 
                  : false
                }
                { APIsBundles.phone ?
                  <Icon iconName='iCT-callgray' iconSize='iCT-sB mr-3 uCT-img-100' spaces='mb-3 ' textPosition='right' innerText={APIsBundles.phone} /> 
                  : false
                }
            </div>
            <div className='d-flex'>
              <div className='d-flex uCT-f-radius px-3 py-2 cCT-Gradian-Linear-supp shadow-sm'>
                <Icon iconName='iCT-facebookblue'iconSize='iCT-sB' spaces='mr-3'/> 
                <Icon iconName='iCT-twitterblue'iconSize='iCT-sB' spaces='mr-3'/> 
                <Icon iconName='iCT-youtubered'iconSize='iCT-sB' spaces='mr-3'/> 
                <Icon iconName='iCT-instagramred'iconSize='iCT-sB'/>
              </div>
            </div>
          </div>
        )
    }
    return ContactsTemp()
}