import './right-widget.sass'
export default function RightWidget({ content, topValue}){
    const rightWidgetTemp = ()=>{
        return (
            <aside style={{top: topValue + 'px'}} className='uCT-right-widget d-flex justify-content-end pl-4'>
                {content} 
            </aside>
        )
    }
    return rightWidgetTemp()
}