import '../home.sass'
import Icon from '../../../shared/Icon/Icon'
import ButtonRightIcon from '../../../shared/buttons/Button-Right-Icon/ButtonRightIcon'
import { MediaCT } from 'view/viewLogic/view-logic'

export default function BecomeAgent(){
    const BecomeAgentTemp = ()=>{
        return(
            <div className={`${MediaCT('lg') ? 'uCT-f-radius-sC mt-3 pt-3' : 'my-5~'} uCT-scale-in-animation uCT-click`}>
                {/* Background Image */}
                <div className={`${!MediaCT('xl') ? 'uCT-f-radius-sC' : false} d-flex flex-column shadow-lg justify-content-center imCT-becomeAgentImg CT-holderBecomeAgentIMG uCT-img-cover`}>
                  <div className={`pl-5 py-5 h-100 d-flex w-50 ${!MediaCT('xl') ? 'uCT-f-radius-sC' : false} flex-column justify-content-center cCT-Gradian-Linear-Opacity-BW cCT-white-text`}>
                    <p className={`mb-2 mt-5 ${MediaCT('md') ? 'fCT-bold-sE' : 'fCT-bold-54'}`}>The Greatest <br/> Real Estate <br/> Platform</p>
                    <span style={{ fontStyle: 'normal'}} className={`${MediaCT('md') ? 'fCT-Regular-sB mb-4' : 'fCT-Regular-sE mb-4'}`}>List Your Property Now !</span>
                    <button className='cCT-Gradian-Linear-Main shadow mb-5 fCT-Regular-sC2 cCT-white-text border-0 CT-becomeAgentBTN-holder uCT-f-radius-sA'>Become An Agent</button>
                  </div>
                </div>
            </div>
        )
    }
    return BecomeAgentTemp()
}
