import './profile-cover.sass'
import React, { useState, useEffect } from 'react';

export default function ProfileCover({APIs = {}}){
      const [APIsBundles, setAPIsBundles] = useState({
          image: String
      });

      useEffect(() => {
          switch(APIs.APIsKey){
              case "AgencyProfile" :
                  setAPIsBundles(prevVal =>({...prevVal, 
                      image : APIs?.data?.image
                  }))
                  break;
              case "AgentProfile" :
                  setAPIsBundles(prevVal =>({...prevVal, 
                      image : APIs?.data?.image
                  }))
                  break;
          }
      }, [APIs]);
    const profileCoverTemp = ()=>{
        return (
            <div className='CT-profile-cover-holder'>
                <div className='CT-profile-cover-content'>
                    {/* cover image */}
                    <div className='d-flex shadow justify-content-center w-100 h-100 uCT-f-radius-sC overflow-hidden'>
                        <i className='iCT-x-coverProfile w-100 h-100 uCT-img-cover uCT-f-radius-sC' style={{backgroundImage: APIsBundles.image ? `url('${APIsBundles.image}')` : false}}></i>
                    </div>
                </div>
            </div>
        )
    }
    return profileCoverTemp()
}