import {API} from 'environment/environment'

export const _GET_AllCountry = async (language)=>{
    language = (typeof language !== 'undefined') ? language : 'en' // language Default
    return await API.get(`api/core_data/country/list?lang=${language}`)
}
export const _GET_AllCategory = async (language)=>{
    language = (typeof language !== 'undefined') ? language : 'en' // language Default
    return await API.get(`api/core_data/category/list?lang=${language}&auth_id=`)
}
export const _GET_AllType= async (language)=>{
    language = (typeof language !== 'undefined') ? language : 'en' // language Default
    return await API.get(`api/core_data/type/list?lang=${language}&auth_id=`)
}
export const _GET_AllCityOfCountry= async (language, countryID)=>{
    language = (typeof language !== 'undefined') ? language : 'en' // language Default
    countryID = (typeof countryID !== 'undefined') ? countryID : 1 // language Default
    return await API.get(`api/core_data/city/list?lang=${language}&country=${countryID}`)
}
export const _GET_AllAmenities = async (language, authID)=>{
    language = (typeof language !== 'undefined') ? language : 'en' // language Default
    authID = (typeof authID !== 'undefined') ? authID : '' // authID Default
    return await API.get(`api/core_data/amenity/list?lang=${language}&auth_id=${authID}`)
}
export const _GET_Price_Area = async () =>{
    return await API.get(`api/core_data/filterData/list`)
} 