import '../home.sass'
import { MediaCT } from 'view/viewLogic/view-logic'

export default function AboutUs(){
    const AboutUsTemp = ()=>{
        return(
          <div className={`m-0 ${MediaCT('sm') ? 'py-0' : 'py-5'}`}>
            <div className={` ${MediaCT('xl') ? 'flex-column align-items-center justify-content-center w-100 py-4 px-3' : 'd-flex pt-5 px-3'}`}>
                {/* Content Holder */}
                <div className={`CT-holderAboutUsText ${!MediaCT('xl') ? 'mb-0' : 'mb-3'}`}>
                  <div className='col-6~ p-0'>
                    <p className={`${!MediaCT('sm') ? 'fCT-bold-54 mb-3 pb-2' : 'fCT-bold-sE mb-3'}`}>About Us</p>
                    <span className={`${!MediaCT('sm') ? 'd-block fCT-Regular-sE mb-3 pl-4' : 'fCT-Regular-sD mb-3 pl-1 d-block'}`}>
                      A New Way For Working For
                      Many Of Professionals.
                    </span>
                  </div>
                  <div className='pl-4~'>
                      <p className={`d-block mb-0  ${!MediaCT('sm') ? 'fCT-Regular-sC pl-4' : 'fCT-Regular-sB pl-1'} cCT-gray-text`}>
                        Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy
                        text ever since the 1500s, when an unknown printer.
                        took a galley of type and scrambled it to make a type specimen book. It has survived not only five centuries, but also the leap into electronic typesetting, remaining essentially unchanged.
                        It was popularised in the 1960s with the release of Letraset sheets containing Lorem Ipsum passages, and more recently with desktop.
                      </p>
                  </div>
                </div>
                {/* Image Holder */}
                <div className='uCT-f-radius-sB d-flex CT-aspect-ratio-holder'>
                  <div className={`d-flex uCT-f-radius-sB shadow p-0 imCT-homeCoverPageImg ${!MediaCT('xl') ? 'CT-holderAboutUsIMG' : 'CT-holderAboutUsIMG-sm col'} CT-holderAboutUsIMG~ uCT-img-cover`}></div>
                </div>
            </div>
          </div>
        )
    }
    return AboutUsTemp()
}