import './GoogleMapView.sass'
import {GOOGLE_MAP_API_KEY} from  'environment/environment'
import PropertySMListCard from 'view/features/property/Property-SM-List-View/PropertySMListCard'
import TagPriceMap from 'view/features/google-map/Tag-Price-Map/TagPriceMap'
import React, { useEffect, useState, useRef } from 'react'
import GoogleMapReact from 'google-map-react';
import ControllersMapViewFeature from 'view/features/shared/ControllersMapView/ControllersMapView.feature'


export default React.memo(function GoogleMapView({APIs, hoverPropertyID, setGeoGraph }) {
    // console.log(List);
    let amenitiesIconHolder ;     
    const prevMarkersRef = useRef([]);
    const [singleProperty, setSingleProperty] = useState()
    const [countOfAmenities, setCountOfAmenities] = useState()
    const [placeRequest, setPlaceRequest] = useState('')
    const [googleMapID, setGoogleMapID] = useState({})
    const [APIsKey, setAPIsKey] = useState('MapView') 
    const styles = {
        hide: [
                {
                    "featureType": "landscape",
                    "elementType": "geometry.fill",
                    "stylers": [
                    {
                        "color": "#d8e0bd"
                    }
                    ]
                },
                {
                    "featureType": "landscape.man_made",
                    "elementType": "geometry.fill",
                    "stylers": [
                    {
                        "color": "#f8f9fa"
                    }
                    ]
                },
                {
                    "featureType": "landscape.natural",
                    "elementType": "geometry",
                    "stylers": [
                    {
                        "color": "#eeefef"
                    }
                    ]
                },
                {
                    "featureType": "landscape.natural",
                    "elementType": "geometry.fill",
                    "stylers": [
                    {
                        "color": "#eeefef"
                    },
                    {
                        "lightness": 20
                    }
                    ]
                },
                {
                    "featureType": "landscape.natural.terrain",
                    "elementType": "geometry.fill",
                    "stylers": [
                    {
                        "color": "#d5d5c9"
                    }
                    ]
                },
                {
                    "featureType": "landscape.natural.terrain",
                    "elementType": "geometry.stroke",
                    "stylers": [
                    {
                        "color": "#ededeb"
                    },
                    {
                        "weight": 8
                    }
                    ]
                },
                {
                    "featureType": "poi.business",
                    "stylers": [
                    {
                        "visibility": "off"
                    }
                    ]
                },
                {
                    "featureType": "road",
                    "elementType": "geometry.fill",
                    "stylers": [
                    {
                        "color": "#ffffff"
                    }
                    ]
                },
                {
                    "featureType": "road",
                    "elementType": "labels.icon",
                    "stylers": [
                    {
                        "visibility": "off"
                    }
                    ]
                },
                {
                    "featureType": "road.highway",
                    "elementType": "geometry.fill",
                    "stylers": [
                    {
                        "color": "#9c9dfe"
                    },
                    {
                        "saturation": 60
                    },
                    {
                        "lightness": 25
                    }
                    ]
                },
                {
                    "featureType": "road.highway",
                    "elementType": "geometry.stroke",
                    "stylers": [
                    {
                        "color": "#9c9dfe"
                    },
                     {
                        "saturation": 15
                    },
                    {
                        "lightness": 100
                    }
                    ]
                },
                {
                    "featureType": "transit",
                    "stylers": [
                    {
                        "visibility": "off"
                    }
                    ]
                },
                {
                    "featureType": "water",
                    "elementType": "geometry.fill",
                    "stylers": [
                    {
                        "color": "#bde2fe"
                    }
                    ]
                },
                {
                    "featureType": "water",
                    "elementType": "geometry.stroke",
                    "stylers": [
                    {
                        "visibility": "off"
                    }
                    ]
                }
                            

        ],
    };
    const [defaultProps, setDefaultProps] = useState({
        center: {
        lat: 25,
        lng: 55
        },
        createMapOptions: function (maps) {
            return {
                panControl: true,
                mapTypeControl: true,
                scrollwheel: true,
                streetViewControl: true,
                fullscreenControl  : true,
                styles: styles['hide'],        
                streetViewControlOptions : {
                    position: maps.ControlPosition.TOP_RIGHT,
                },
                mapTypeControlOptions: {
                    position: maps.ControlPosition.TOP_RIGHT,
                    style: maps.MapTypeControlStyle.DEFAULT ,
                    
                },
                 zoomControlOptions: {
                    position: maps.ControlPosition.TOP_RIGHT,
                    style: maps.ZoomControlStyle.SMALL
                },
                mapTypeControl: false
            }
        },
        zoom: 10,
        
    });
    switch (placeRequest) {
        case 'library':
            amenitiesIconHolder = 'data:image/svg+xml;base64,'
            break;
        case 'shopping_mall':
            amenitiesIconHolder = 'data:image/svg+xml;base64,'
            break;
        case 'restaurant':
            amenitiesIconHolder = 'data:image/svg+xml;base64,'
            break;
        case 'pharmacy':
            amenitiesIconHolder = 'data:image/svg+xml;base64,'
            break;
        case 'hospital':
            amenitiesIconHolder = 'data:image/svg+xml;base64,'
            break;
    }

    console.log(placeRequest);

    // Get Nearby Places From Google APIs
    // pen The Location oF Ameneties Like ['Hospital', 'Bar']

    function addMarker(position, map){
        let google = window.google
        var icons = {
            url: amenitiesIconHolder, // url
            scaledSize: new google.maps.Size(32, 47), // scaled size
            origin: new google.maps.Point(0,0), // origin
            anchor: new google.maps.Point(17, 34), // anchor
        };
        return new google.maps.Marker({
            map,
            position,
            animation: google.maps.Animation.DROP,
            optimized: false,
            icon: icons,
        })
    }

    function _getNearbyPlaces(){
        let google = window.google
        const pyrmont = new google.maps.LatLng(singleProperty?.propertyLocation?.latitude, singleProperty?.propertyLocation?.longitude);
        const service = new google.maps.places.PlacesService(googleMapID.map);
        const request = {
            location: pyrmont,
            radius: '10000',
            type: [placeRequest],
        }; 
        console.log(placeRequest);
        console.log(googleMapID.map);
        
        service.nearbySearch(request, (results, status, pagination) => {
            setCountOfAmenities(results)
            console.log(pagination.getNextPage );
            if (status == google.maps.places.PlacesServiceStatus.OK && placeRequest != 'null' && placeRequest != undefined ) {
                for (var i = 0; i < results.length; i++) {
                    const marker = addMarker(results[i].geometry.location, googleMapID.map)
                    prevMarkersRef.current.push(marker);
                } 
            }else{ console.log(status);}
        });
    }

    function clearMarkers(markers) {
        for (let marker of markers) {
            marker.setMap(null);
        }
    }


    function onClickProperty(property) {
        setSingleProperty(prevVal => prevVal == property ? '' : property) 
        setDefaultProps((prevVal) => ({...prevVal, center: {lat: property?.propertyLocation?.latitude , lng: property?.propertyLocation?.longitude }, zoom: 13 })) 
        setPlaceRequest('null')
    }

    useEffect(() => {
        if (singleProperty) {
            clearMarkers(prevMarkersRef.current)
            _getNearbyPlaces()   
            prevMarkersRef?.current?.splice(0,prevMarkersRef.current.length)
        }else{
            clearMarkers(prevMarkersRef.current)
            prevMarkersRef?.current?.splice(0,prevMarkersRef.current.length)
            setCountOfAmenities('')
        }
        // setDefaultProps((prevVal) => ({...prevVal, zoom: singleProperty ? 14 : 10})) 
    }, [singleProperty, placeRequest]);

    const _onBoundsChange = (center, zoom, boundsMargin) => {
        setGeoGraph(prevVal => ({...prevVal,latitude: boundsMargin[0], longitude: boundsMargin[1], center_latitude:center.lat, center_longitude:center.lng }))
    }
    const GoogleMapTemp = () => {
        return (
            <>
            <GoogleMapReact
                bootstrapURLKeys={{ key: GOOGLE_MAP_API_KEY,libraries:['places']}}
                panControl={true}
                center={defaultProps?.center}
                zoom={defaultProps?.zoom}
                options={defaultProps?.createMapOptions}
                onBoundsChange={_onBoundsChange}
                yesIWantToUseGoogleMapApiInternals
                onGoogleApiLoaded={({map, maps}) => {setGoogleMapID({map, maps})}}
                >

                { singleProperty  ?
                    <PropertySMListCard key={singleProperty?.id} lat={singleProperty?.propertyLocation?.latitude} lng={singleProperty?.propertyLocation?.longitude} key={singleProperty?.id} APIs={{ APIsKey, data: { property: singleProperty } }}/>
                    : false}

                {APIs?.map((property) => {
                    return <TagPriceMap key={property?.id} clickHandler={onClickProperty} onHover={hoverPropertyID} lat={property?.propertyLocation?.latitude} lng={property?.propertyLocation?.longitude} APIs={{ APIsKey, data: { property: property } }}/>        
                })}
            </GoogleMapReact>
            <div className={`CT-Controller-Map-Holder uCT-t-4-all`} style={{right: countOfAmenities == [] ? '-100px' : '10px'}}>
                <ControllersMapViewFeature requestClick={setPlaceRequest} lengthOfRequest={countOfAmenities} typeRequest={placeRequest} />
            </div>
            </>
        )
    }
    return GoogleMapTemp()
})

