import './ProfiileSideNav.sass'
import { useLocation } from 'react-router-dom'
import Icon from '../../shared/Icon/Icon'
import { MediaCT } from 'view/viewLogic/view-logic'
export default function ProfiileSideNav(){
    const location = useLocation();
    const profiileSideNavTemp = ()=>{
        return(
            <div className={`py-lg-4 py-2 ${MediaCT('lg') ? 'uCT-f-radius-sB d-flex CT-gradian-linear-Smoke shadow my-4 justify-content-around align-content-center' : 'd-block'}`}>
                <a className='d-flex mb-lg-4 CT-gradian-linear-Smoke shadow uCT-f-radius-sA' href="#">
                    <Icon iconName='iCT-aboutblue' spaces='cCT-secondary-text fCT-Regular-sB p-lg-2' iconSize='iCT-sB ml-lg-2 mr-lg-3 pb-4 ' textPosition={`${MediaCT('lg') ? 'bottom' : 'right'}`} innerText='ABOUT'/>
                </a>
                <a className='d-flex mb-lg-4' href="#">
                    <Icon iconName='iCT-agentgray' spaces='cCT-gray-text fCT-Regular-sB' iconSize='iCT-sB mr-lg-3 pb-4' textPosition={`${MediaCT('lg') ? 'bottom' : 'right'}`} innerText='AGENTS'/>
                </a>
                {/* if router URL(.../agency) */}
                {location.pathname == '/agency' ? <a className='d-flex mb-lg-4' href="#">
                    <Icon iconName='iCT-propertygray' spaces='cCT-gray-text fCT-Regular-sB' iconSize='iCT-sB mr-lg-3 pb-4' textPosition={`${MediaCT('lg') ? 'bottom' : 'right'}`} innerText='PROPERTIES'/>
                </a> : false}
                <a className='d-flex mb-lg-4' href="#">
                    <Icon iconName='iCT-rategray' spaces='cCT-gray-text fCT-Regular-sB' iconSize='iCT-sB mr-lg-3 pb-4' textPosition={`${MediaCT('lg') ? 'bottom' : 'right'}`} innerText='REVIEWS'/>
                </a>
            </div>
        )
    }
    return profiileSideNavTemp()
}