import NotificationTime from '../../features/notifications/Notification-Time-line/NotificationTime'
import NotificationTimeWireFrame from '../../features/notifications/Notification-Time-line/NotificationTime-WireFrame/NotificationTime.WireFrame'
import NotificationCard from '../../features/notifications/Notification-Card/NotificationCard'
import NotificationCardWireFrame from '../../features/notifications/Notification-Card/NotificationCard-WireFrame/NotificationCard.WireFrame'
import PagesTitle from '../../features/shared/pagesTitle/PagesTitle'

export default function Notification() {

    const notificationTemp = () => {
        return (
            <div className="container p-0~">
                <PagesTitle title='Notifications' />
                <NotificationTime />
                <NotificationTimeWireFrame />
                <NotificationCardWireFrame />
                <NotificationCard />
                <NotificationCard />
                <NotificationCard />
                <NotificationTime />
                <NotificationCard />
                <NotificationCard />
            </div>
        )
    }
    return notificationTemp()
}