// APIs
export const _GET_AllAgencies = async (language, paginate, filter_rating, filter_new, auth_id) => {
    language = (typeof language !== 'undefined') ? language : 'en' // language Default
    paginate = (typeof paginate !== 'undefined') ? paginate : 25 // paginate Default
    filter_rating = (typeof filter_rating !== 'undefined') ? filter_rating : '' // filter_rating Default
    filter_new = (typeof filter_new !== 'undefined') ? filter_new : '' // filter_new Default
    auth_id = (typeof auth_id !== 'undefined') ? auth_id : '' // auth_id Default

    return await API.get(`api/company/index?lang=${language}&paginate=${paginate}&filter_rating=${filter_rating}&filter_new=${filter_new}&auth_id=${auth_id}`)
}

export const _GETID_Agency = async (ID, role_id, language, auth_id) => {
    language = (typeof language !== 'undefined') ? language : 'en' // language Default
    ID = (typeof ID !== 'undefined') ? ID : '' // paginate Default
    role_id = (typeof role_id !== 'undefined') ? role_id : '' // paginate Default
    auth_id = (typeof auth_id !== 'undefined') ? auth_id : '' // auth_id Default
    return await API.get(`api/company/profile?lang=${language}&id=${ID}&role_id=${role_id}&auth_id=${auth_id}`)
}

export const _GET_Agency_Properties = async (ID, role_id, property, page, language, auth_id) => {
    language = (typeof language !== 'undefined') ? language : 'en' // language Default
    ID = (typeof ID !== 'undefined') ? ID : '' // paginate Default
    role_id = (typeof role_id !== 'undefined') ? role_id : '' // paginate Default
    auth_id = (typeof auth_id !== 'undefined') ? auth_id : '' // auth_id Default
    property = (typeof property !== 'undefined') ? property : '' // property Default
    page = (typeof page !== 'undefined') ? page : '' // page Default
    return await API.get(`api/company/profile?lang=${language}&id=${ID}&role_id=${role_id}&auth_id=${auth_id}&property=${property}&page=${page}`)
}
