import PageHead from 'view/layout/Components/PageHead/PageHead'
import PagesTitle from 'view/features/shared/pagesTitle/PagesTitle'
import PropertyGridCard from 'view/features/property/Property-Grid-Card/PropertyGridCard'
import ComparableTable from 'view/features/property/Comparable-Table/Comparable-Table'
import { useState } from 'react/cjs/react.production.min'
export default function CompareListing(){
    let gridCardArr = [
        <PropertyGridCard />
    ]
    const compareListingTemp = () => {
        return (
            <div className="container-fluid p-0">
                {/* page head */}
                <header>
                    <PageHead title="Compare Listing" subTitle="A great real estate agent don't sells, he helps." pImg="imCT-comparableListingImg" />
                </header>
                <div className="container p-0 overflow-auto~ ">
                    <section>
                        <PagesTitle title="Summary" />
                    </section>
                    <section>
                        <div className="">
                            <ComparableTable />
                        </div>
                    </section>
                </div>
            </div>
        )
    }
    return compareListingTemp()
}