import './Tour360.scss'
import Tour360Image from 'view/features/deal360/aboutUs/Tour360/Tour360Image/Tour360Image.feature'
import Tour360Content from 'view/features/deal360/aboutUs/Tour360/Tour360Content/Tour360Content.feature'
export default function Tour360() {
    const tour360Temp = () => {
        return (
            <div className="CT-360-tour-page-holder cCT-Gradian-Linear-mid-Main pt-3 pr-lg-4  pt-lg-0">
                {/* 360Tour Line Shape [I] */}
                <div className="position-absolute CT-360-line-I"></div>
                {/* 360Tour Image Feature */}
                <div className="CT-360-tour-image-feature">
                    <Tour360Image />
                </div>
                {/* 360Tour Content Feature */}
                <div className="CT-360-tour-content-feature px-3 px-lg-0">
                    <Tour360Content />
                </div>
                {/* 360Tour Line Shape [II] */}
                <div className="position-absolute CT-360-line-II"></div>
            </div>
        )
    }
    return tour360Temp()
}