import "./Subscribe.sass"
import { _GET_Subscribe } from "../../../../Controllers/services/subscribe/subscribe.services"
import { useState, useEffect } from "react"
import Banner from "../popups/Banner/Banner.layout"
export default function Subscribes() {
    const [bannerMessage, setBannerMessage] = useState()
    const [bannerType, setBannerType] = useState()
    const [bannerClick, setBannerClick] = useState(false)

    function sendMssage() {
        let bannerstates;
        _GET_Subscribe().then(res => {
            res?.data?.status ? bannerstates = 'success' : bannerstates = 'error';
            setBannerType(bannerstates)
        }).catch(err => {
            bannerstates = 'error'
            setBannerType(bannerstates)
            setBannerMessage(bannerstates)
        })
        setBannerClick(true)
    }
    // HTML Render
    const subSection = () => {
        return (
            // Holder
            <div className="container-fluid p-0 imCT-agencies-list-img CT-subscribe-blackScreen uCT-img-cover">
                <Banner type={bannerType} message={bannerMessage} clickMe={bannerClick} clickStatus={status => setBannerClick(status)} />
                <div className="container">
                    <div className="d-block d-sm-flex py-4 justify-content-between align-items-center">
                        {/*SUBSCRIBE Title*/}
                        <div className="cCT-support2-text text-uppercase mb-3 mb-md-3 pr-4 pr-md-4">
                            <p className="m-0 cCT-Light-20">Subscribe to our</p>
                            <p className="m-0 fCT-Regular-sF">newsletter</p>
                        </div>
                        {/* SUBSCRIBE Search-Holder */}
                        <div className="d-flex uCT-Custom-Width w-100">
                            {/* Search Icone Holder */}
                            <div className="d-flex cCT-white-bg uCT-l-radius-sA border-0 align-items-center pl-3 pr-2 px-md-4">
                                <i className="iCT-send-blue iCT-sB uCT-img-cover"></i>
                            </div>
                            {/* Search Bar */}
                            <input id="message" type='email' className="w-100 border-0 " placeholder='Enter Your E-Mail...' required />

                            {/* Send Button */}
                            <div className="d-flex">
                                <button onClick={sendMssage} type="submit" className="uCT-r-radius-sA p-0 px-3 px-md-4 btn border-0 cCT-Gradian-Linear-Main fCT-Regular-sB cCT-support2-text">Submit</button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
    return subSection()
}
// export default subscribes;