import React, { useEffect, useState, useRef } from 'react'
import './controllersMapView.sass'

export default React.memo(function ControllersMapViewFeature({requestClick, lengthOfRequest = [], typeRequest}){
    const [sendRequest, setSendRequest] = useState(false)
    const [animateController, setAnimateController] = useState({
        shopping_mall : false,
        library : false,
        restaurant : false,
        pharmacy : false,
        hospital : false,
    })

    useEffect(() => {
        switch (sendRequest) {
            case true:
                setSendRequest(!typeRequest ? false : true)
                break;
            case false:
                setSendRequest(true)
                break;
        }
    }, [typeRequest])

    useEffect(() => {
        switch (typeRequest) {
            case 'shopping_mall':
                setAnimateController()
                setAnimateController(prevVal => ({...prevVal, shopping_mall : true}))
                break
            case 'library':
                setAnimateController()
                setAnimateController(prevVal => ({...prevVal, library : true}))
                break
            case 'restaurant':
                setAnimateController()
                setAnimateController(prevVal => ({...prevVal, restaurant : true}))
                break
            case 'pharmacy':
                setAnimateController()
                setAnimateController(prevVal => ({...prevVal, pharmacy : true}))
                break
            case 'hospital':
                setAnimateController()
                setAnimateController(prevVal => ({...prevVal, hospital : true}))
                break
            default:
                setAnimateController(false)
                break;
        }
        console.log(animateController);
    }, [typeRequest]);
    console.log(typeRequest);

    const controllersMapViewTemp = ()=>{
        return(
            <div className='d-flex flex-column align-items-end'>
                {/* Shopping BTN */}
                <div className='d-flex align-items-center justify-content-center position-relative mb-4'>
                    {/* Count And Place Type Section */}
                    <div className={`d-flex flex-nowrap mr-3 cCT-smoke-bg uCT-f-radius-sC CT-Dashed-Border-Shoppingmall uCT-t-6-all overflow-hidden position-absolute`} style={{right: !animateController.shopping_mall ? '-14px' : '40px' }}>
                        <p className='mb-0 rounded-circle iCT-sD d-flex fCT-Regular-sB align-items-center justify-content-center cCT-white-text' style={{background: '#f28a13'}}>
                            { typeRequest == 'shopping_mall' && lengthOfRequest != [] && <span>{lengthOfRequest?.length}</span> }
                        </p>
                        <p className=' mb-0 d-flex align-items-center  uCT-t-6-all uCT-ellipsis-1-line' style={{color: '#f28a13', width: typeRequest == 'shopping_mall' ? '140px' : 0 , padding: typeRequest == 'shopping_mall' ? '0 16px' : 0 }}>ShoppingMalls</p>
                    </div>
                    {/* Icon BTN Click */}
                    <button className="btn border-0  d-flex align-items-center iCT-ShoppingmallGoogleController uCT-img-contain iCT-sE justify-content-center uCT-t-6-all" 
                            onClick={() => {typeRequest !== 'shopping_mall' ? requestClick('shopping_mall') : requestClick()}} 
                            style={{ zIndex: '1'}}>
                    </button>
                </div>
                {/* library BTN */}
                <div className='d-flex position-relative align-items-center justify-content-center mb-4'>
                    {/* Count And Place Type Section */}
                    <div className={`d-flex mr-3 cCT-smoke-bg uCT-f-radius-sC fCT-Regular-sB CT-Dashed-Border-Library uCT-t-6-all overflow-hidden position-absolute`} style={{right: !animateController.library ? '-14px' : '40px' }}>
                        <p className='mb-0 rounded-circle iCT-sD d-flex align-items-center justify-content-center cCT-white-text' style={{background: '#707070'}}>
                            { typeRequest == 'library' && lengthOfRequest != [] && <span>{lengthOfRequest?.length}</span> }
                        </p>
                        <p className=' mb-0 d-flex align-items-center uCT-t-6-all' style={{color: '#707070', width: typeRequest == 'library' ? '85px' : 0 , padding: typeRequest == 'library' ? '0 16px' : 0 }}>Librarys</p>
                    </div>
                    {/* Icon BTN Click */}
                    <button className="btn border-0  d-flex flex-column w-100~ align-items-center iCT-LibraryGoogleController uCT-img-contain iCT-sE justify-content-center iCT-sE~ p-3~ uCT-f-radius-sA uCT-t-6-all" 
                            onClick={() => {typeRequest !== 'library' ? requestClick('library') : requestClick()}} 
                            style={{ zIndex: '1'}}>
                    </button>
                </div>
                {/* Restaurant BTN */}
                <div className='d-flex position-relative align-items-center justify-content-center` mb-4'>
                    {/* Count And Place Type Section */}
                    <div className={`d-flex mr-3 cCT-smoke-bg uCT-f-radius-sC fCT-Regular-sB CT-Dashed-Border-Resturant uCT-t-6-all overflow-hidden position-absolute`} style={{right: !animateController.restaurant ? '-14px' : '40px' }}>
                        <p className='mb-0 rounded-circle iCT-sD d-flex align-items-center justify-content-center cCT-white-text' style={{background: '#1c75bc'}}>
                            { typeRequest == 'restaurant' && lengthOfRequest != [] && <span>{lengthOfRequest?.length}</span> }
                        </p>
                        <p className=' mb-0 d-flex align-items-center  uCT-t-6-all' style={{color: '#1c75bc', width: typeRequest == 'restaurant' ? '108px' : 0 , padding: typeRequest == 'restaurant' ? '0 16px' : 0 }}>Restaurants</p>
                    </div>
                    {/* Icon BTN Click */}
                    <button className="btn border-0  d-flex flex-column w-100~ align-items-center iCT-RestaurantGoogleController uCT-img-contain iCT-sE justify-content-center iCT-sE~ p-3~ uCT-f-radius-sA uCT-t-6-all" 
                            onClick={() => {typeRequest !== 'restaurant' ? requestClick('restaurant') : requestClick()}} 
                            style={{ zIndex: '1'}}>
                    </button>
                </div>
                {/* Pharmacy BTN */}
                <div className='d-flex position-relative align-items-center justify-content-center mb-4 align-items-center justify-content-center'>
                    {/* Count And Place Type Section */}
                    <div className={`d-flex mr-3 cCT-smoke-bg uCT-f-radius-sC fCT-Regular-sB CT-Dashed-Border-Pharmacy uCT-t-6-all overflow-hidden position-absolute`} style={{right: !animateController.pharmacy ? '-14px' : '40px' }}>
                        <p className='mb-0 rounded-circle iCT-sD d-flex align-items-center justify-content-center cCT-white-text' style={{background: '#1adad0'}}>
                            { typeRequest == 'pharmacy' && lengthOfRequest != [] && <span>{lengthOfRequest?.length}</span> }
                        </p>
                        <p className=' mb-0 d-flex align-items-center  uCT-t-6-all' style={{color: '#1adad0', width: typeRequest == 'pharmacy' ? '105px' : 0 , padding: typeRequest == 'pharmacy' ? '0 16px' : 0 }}>Pharmacys</p>
                    </div>
                    {/* Icon BTN Click */}
                    <button className="btn border-0  d-flex flex-column w-100~ align-items-center iCT-ParmacyGoogleController uCT-img-contain iCT-sE justify-content-center iCT-sE~ p-3~ uCT-f-radius-sA uCT-t-6-all" 
                            onClick={() => {typeRequest !== 'pharmacy' ? requestClick('pharmacy') : requestClick()}} 
                            style={{zIndex: '1'}}>
                    </button>
                </div>
                {/* Hospital BTN */}
                <div className='d-flex align-items-center justify-content-center position-relative mb-4'>
                    {/* Count And Place Type Section */}
                    <div className={`d-flex mr-3 cCT-smoke-bg uCT-f-radius-sC fCT-Regular-sB CT-Dashed-Border-Hospital uCT-t-6-all overflow-hidden position-absolute`} style={{right: !animateController.hospital ? '-15px' : '40px' }}>
                        <p className='mb-0 rounded-circle iCT-sD d-flex align-items-center justify-content-center cCT-white-text' style={{background: '#ba0900'}}>
                            { typeRequest == 'hospital' && lengthOfRequest != [] && <span>{lengthOfRequest?.length}</span> }
                        </p>
                        <p className=' mb-0 d-flex align-items-center  uCT-t-6-all' style={{color: '#ba0900', width: typeRequest == 'hospital' ? '95px' : 0 , padding: typeRequest == 'hospital' ? '0 16px' : 0 }}>Hospitals</p>
                    </div>
                    {/* Icon BTN Click */}
                    <button className="btn border-0  d-flex flex-column w-100~ align-items-center iCT-HospitalGoogleController uCT-img-contain iCT-sE justify-content-center iCT-sE~ p-3~ uCT-f-radius-sA uCT-t-6-all" 
                            onClick={() => {typeRequest !== 'hospital' ? requestClick('hospital') : requestClick()}} 
                            style={{ zIndex: '1'}}>
                    </button>
                </div>
            </div>
        )
    }
    return controllersMapViewTemp()
})
