import './PropertySMListCard.sass'
import Icon from '../../shared/Icon/Icon'
import React, { useState, useEffect } from 'react';
import { Link } from "react-router-dom";

export default function PropertySMListCard({APIs = {}}) {

    const [APIsBundles, setAPIsBundles] = useState({
        id: Number,
        image: String,
        title: String,
        country_title: String,
        type_title : String,
        category_title : String,
        high_light_title : Number,
        type_title : String,
        currency_title : Number,
        price : Number
    });

    useEffect(() => {
        switch(APIs.APIsKey){
            case "MapView" :
                setAPIsBundles(prevVal =>({...prevVal, 
                    id : APIs?.data?.property?.id,
                    image : APIs?.data?.property?.user?.image,
                    image_property : APIs?.data?.property?.images,
                    title : APIs?.data?.property?.title,
                    country_title : APIs?.data?.property?.city?.country?.title,
                    type_title  : APIs?.data?.property?.type?.title ,
                    category_title : APIs?.data?.property?.category?.title,
                    high_light_title : APIs?.data?.property?.high_light?.title,
                    currency_title : APIs?.data?.property?.currency?.title,
                    price : APIs?.data?.property?.price
                }))
                break;
            }
    }, []);
    console.log(APIs);
    const PropertySMListCardTemp = () => {
        return (
            <Link to={`/property-details/${APIsBundles.id}`} className='CT-mini-SM-list-card-map uCT-f-radius-sB shadow  d-flex flex-column cCT-support2-bg'>
                {/* Section 2nd */} {/* Images Holders */}
                <div class=" uCT-f-radius-sB overflow-hidden CT-Image-Holder d-flex w-100 h-100 uCT-bd-white-sB" style={{ width: '160px', height: '120px'}}>
                    <i className={`imCT-PropertyListCover uCT-img-contain h-100 w-100`}  style={{ backgroundImage: `url(${APIsBundles?.image_property})`, width: '160px', height: '120px'}}></i>
                </div>
                {/* <div className={`flex-column p-4`}>
                    <div className='w-100 mb-2'>
                        <p className='mb-0 fCT-Regular-sC mb-2 CT-Text-ellipsis-2Line'>{APIsBundles.title}</p>
                        <Icon iconName='iCT-locationblue' spaces='cCT-gray-text fCT-Regular-sB' iconSize='iCT-sB mr-2' textPosition='right' innerText={APIsBundles.country_title} />
                    </div>
                    <div className='d-flex  align-items-center w-100 mb-2'>
                        <Icon iconName='iCT-pricemoove' spaces='cCT-main-text fCT-bold-sE mr-1' iconSize='iCT-sB mr-2' textPosition='right' innerText={APIsBundles.price}/>
                        <p className='mb-0 mt-1 cCT-gray-text fCT-bold-sC'>{APIsBundles.currency_title}</p>
                    </div>
                </div> */}
            </Link>
        )
    }
    return PropertySMListCardTemp()
}