import {API} from 'environment/environment'
// Reducers
export const AgentsReducers = (state={}, action) =>{
    switch(action.type){
        // case 'agent/allAgentsList': state.allAgentsList = action.payload ; break
        default: return state
    }
    return state 
}

// APIs
export const _GET_AllAgents = async (language, paginate, filter_rating, filter_new, auth_id)=>{
    language        = (typeof language !== 'undefined') ? language : 'en' // language Default
    paginate        = (typeof paginate !== 'undefined') ? paginate : 25 // paginate Default
    filter_rating   = (typeof filter_rating !== 'undefined') ? filter_rating : '' // filter_rating Default
    filter_new      = (typeof filter_new !== 'undefined') ? filter_new : '' // filter_new Default
    auth_id         = (typeof auth_id !== 'undefined') ? auth_id : '' // auth_id Default
    return await API.get(`api/agent/index?lang=${language}&paginate=${paginate}&filter_rating=${filter_rating}&filter_new=${filter_new}&auth_id=${auth_id}`)
}

export const _GETID_Agent = async (ID, language, auth_id)=>{
    language        = (typeof language !== 'undefined') ? language : 'en' // language Default
    ID        = (typeof ID !== 'undefined') ? ID : '' // paginate Default
    auth_id         = (typeof auth_id !== 'undefined') ? auth_id : '' // auth_id Default
    return await API.get(`api/agent/profile?lang=${language}&id=${ID}&auth_id=${auth_id}`)
}

export const _GET_Agent_Properties = async (ID, property, page, language, auth_id)=>{
    language        = (typeof language !== 'undefined') ? language : 'en' // language Default
    ID        = (typeof ID !== 'undefined') ? ID : '' // paginate Default
    auth_id         = (typeof auth_id !== 'undefined') ? auth_id : '' // auth_id Default
    property         = (typeof property !== 'undefined') ? property : '' // property Default
    page         = (typeof page !== 'undefined') ? page : '' // page Default
    return await API.get(`api/agent/profile?lang=${language}&id=${ID}&auth_id=${auth_id}&property=${property}&page=${page}`)
}