import './ProfileMiniNav.sass'
import React, { useEffect } from 'react';
import { giveClassOnScroll } from 'view/viewLogic/view-logic'
import { useMediaQuery } from 'react-responsive';
import Icon from 'view/features/shared/Icon/Icon'
export default function ProfiileMiniNav() {
    function onScroll() {
        giveClassOnScroll('moreBTN', 'uCT-f-radius-sA', 'uCT-r-radius-sA', 120)
    }
    let MediaCT1400 = useMediaQuery({ query: `(max-width: 1400px)` })
    let MediaCT768 = useMediaQuery({ query: `(max-width: 768px)` })
    // USE_Effect call after render 
    useEffect(() => {
        if (!MediaCT768) {
            window.addEventListener('scroll', onScroll);
        }
    }, []);
    // Component OUT!
    useEffect(() => () => {
        window.removeEventListener('scroll', onScroll);
    }, [])
    const profiileMiniNavTemp = () => {
        return (
            <div id='propNavBar' className='d-flex align-items-center uCT-f-radius-sA fCT-Regular-sC'>
                <div className=''>
                    <ul className="nav nav-pills nav-fill">
                        <li className="nav-item px-xl-2 px-0">
                            <a className="nav-link px-lg-4 px-3" href="#">Apartment <span className="cCT-main-text">95</span></a>
                        </li>
                        <li className="nav-item px-xl-2 px-0 d-sm-block d-none">
                            <a className="nav-link cCT-gray-text px-lg-4 px-3" href="#">Duplex <span className="cCT-main-text">81</span></a>
                        </li>
                        <li className="nav-item px-xl-2 px-0 d-sm-block d-none ">
                            <a className="nav-link cCT-gray-text px-lg-4 px-3" href="#">Hotel Apartment <span className="cCT-main-text">2</span></a>
                        </li>
                        <li className="nav-item px-xl-2 px-0 d-xl-block d-lg-none d-md-block d-none ">
                            <a className="nav-link cCT-gray-text px-lg-4 px-3" href="#">Penthouse <span className="cCT-main-text">3</span></a>
                        </li>
                    </ul>
                </div>
                <div className='ml-auto '>
                    <button id='moreBTN' className={`CT-Holder-BTN px-4 border-0 uCT-r-radius-sA cCT-secondary-bg cCT-white-text`}>
                        {MediaCT1400 ? <Icon iconName='iCT-moreWhite' iconSize='iCT-sC' /> : <p className='m-0'>MORE</p>}

                    </button>
                </div>
            </div>
        )
    }
    return profiileMiniNavTemp()
}