import './CarouselPage.layout.scss'
import AboutUsPage from 'view/pages/staticPages/AboutUs/AboutUs.page'
import CircleIndicatorsFeature from 'view/features/shared/carouselIndicators/CircleIndicators/CircleIndicators.feature'
import WhoWeAre from 'view/pages/staticPages/WhoWeAre/WhoWeAre'
import Tour360 from 'view/pages/staticPages/360Tour/Tour360'
import OurClients from 'view/features/how-it-works/Our-Clients/OurClients'
import Icon from 'view/features/shared/Icon/Icon'
import OurVision from 'view/pages/staticPages/OurVision/OurVision'
import OurPortfolio from 'view/pages/staticPages/OurPortfolio/OurPortfolio.page'
import OurMissionPage from 'view/pages/staticPages/OurMission/OurMission.page'
import ContentSlider from 'view/layout/Components/sliders/ContentSlider/ContentSlider'
import ButtonRightIcon from 'view/features/shared/buttons/Button-Right-Icon/ButtonRightIcon'
import { useState, useEffect } from 'react'
import { useDispatch } from 'react-redux';
import $ from 'jquery';
export default function CarouselPageLayout() {
    let carouselOurClienttArr = [
        <OurClients />,
        <OurClients />,
        <OurClients />,
        <OurClients />,
    ];
    const dispatch = useDispatch()
    const [screen, setScreen] = useState(5)
    window.addEventListener('touchstart', handleTouchStart, false);
    window.addEventListener('touchmove', handleTouchMove, false);

    var xDown = null;
    var yDown = null;

    function getTouches(evt) {
        return evt.touches
    }

    function handleTouchStart(evt) {
        const firstTouch = getTouches(evt)[0];
        xDown = firstTouch.clientX;
        yDown = firstTouch.clientY;
    };

    function handleTouchMove(evt) {
        if (!xDown || !yDown) {
            return;
        }
        var xUp = evt.touches[0].clientX;
        var yUp = evt.touches[0].clientY;

        var xDiff = xDown - xUp;
        var yDiff = yDown - yUp;

        if (Math.abs(xDiff) > Math.abs(yDiff)) {/*most significant*/
            if (screen <= 6 && xDiff > 0) {
                /* left swipe */
                setScreen(screen + 1)
                window.removeEventListener("touchstart", handleTouchStart, false)
            } else if (screen > 1 && xDiff < 0) {
                /* right swipe */
                setScreen(screen - 1)
                window.removeEventListener("touchstart", handleTouchStart, false)
            }
        }
        /* reset values */
        xDown = null;
        yDown = null;
    };
    // enter component
    useEffect(() => {
        dispatch({ type: 'route/layout', payload: true })
    }, []);

    // leave component
    useEffect(() => () => dispatch({ type: 'route/layout', payload: false },
        window.removeEventListener("touchstart", handleTouchStart, false),
        window.removeEventListener("touchmove", handleTouchMove, false),
    ), []);


    var IS_IPHONE = navigator.userAgent.match(/iPhone/i) != null;
    var IS_ANDROID = navigator.userAgent.match(/Android/i) != null;
    var IS_WINDOWS = navigator.userAgent.match(/Window/i) != null;

    if (IS_IPHONE) {
        $('#aboutMobileScreen').addClass('CT-grid-ios').removeClass('CT-grid-android CT-aboutus-and-Indicators-grid uCT-cards-grid')
        console.log('iphone')
    }
    else if (IS_ANDROID) {
        $('#aboutMobileScreen').addClass('CT-grid-android').removeClass('CT-grid-ios CT-aboutus-and-Indicators-grid')
        console.log('android')
    }
    else if (IS_WINDOWS) {
        $('#aboutMobileScreen').addClass('CT-grid-window').removeClass('CT-grid-ios CT-grid-android')
        console.log('Window')
    }

    const carouselPageTemp = () => {
        return (
            <div className="conatiner-fluid h-100 d-flex align-items-center justify-content-center">
                {/* Holder */}
                <div className="container-fluid p-0 w-100 h-100">
                    {/* Grid Holder */}
                    <div id="aboutMobileScreen" className="CT-aboutus-and-Indicators-grid CT-grid-window CT-height-holder CT-grid-ios">
                        {/* 360 Videos Selector */}
                        <div className="CT-ourportfolio-grid">
                            {screen == 1 ? <OurPortfolio /> : false}
                        </div>
                        {/* About Us Selector */}
                        {/* larg Screen */}
                        <div className="CT-aboutus-text-image-holder d-none d-lg-block">
                            {screen == 2 ? <AboutUsPage /> : false}
                            {screen == 3 ? <WhoWeAre /> : false}
                            {screen == 4 ? <Tour360 /> : false}
                            {screen == 5 ? <OurVision /> : false}
                            {screen == 6 ? <OurMissionPage /> : false}
                            {screen == 7 ?
                                <>
                                    <div className="CT-about-ourClient-holder">
                                        <p className="mb-0 CT-about-ourClient-text fCT-bold-64">Our Clients</p>
                                        <div className="CT-about-ourClient-slider">
                                            <div className="CT-about-ourClient-shape"></div>
                                            <div className="CT-about-ourClient-shapeII"></div>
                                            <ContentSlider content={carouselOurClienttArr} />
                                        </div>
                                        <div className="w-100 CT-about-ourClient-button-grid">
                                            <ButtonRightIcon innerText='Load More Clients' btnICON='iCT-down-arrow' btnCOLOR='cCT-Gradian-Linear-Main' iconBgCOLOR='cCT-inherit-bg' position='center' />
                                        </div>
                                    </div>
                                </>
                                : false}
                        </div>
                        {/* SmallScreen */}
                        <div className="CT-aboutus-text-image-sm-holder d-block d-lg-none h-100 ">
                            {screen == 2 ? <AboutUsPage /> : false}
                            {screen == 3 ? <WhoWeAre /> : false}
                            {screen == 4 ? <Tour360 /> : false}
                            {screen == 5 ? <OurVision /> : false}
                            {screen == 6 ? <OurMissionPage /> : false}
                            {screen == 7 ?
                                <>
                                    <p className="mb-0 CT-about-ourClient-text CT-bold-64">Our Clients</p>
                                    <div className="CT-about-ourClient-holder">
                                        <div className="CT-about-ourClient-slider">
                                            {/* <div className="CT-about-ourClient-shape"></div> */}
                                            {/* <div className="CT-about-ourClient-shapeII"></div> */}
                                            <ContentSlider content={carouselOurClienttArr} />
                                        </div>
                                        <div className="w-100 CT-about-ourClient-button-grid">
                                            <ButtonRightIcon innerText='Load More Clients' btnICON='iCT-down-arrow' btnCOLOR='cCT-Gradian-Linear-Main' iconBgCOLOR='cCT-inherit-bg' position='center' />
                                        </div>
                                    </div>
                                </>
                                : false}
                        </div>
                        {/* Circle Indicators Feature */}
                        <div className="CT-indecators-holder ">
                            <CircleIndicatorsFeature screen={screen} setChangeScreen={screenNum => setScreen(screenNum)} />
                        </div>
                        {/* mouse Icon */}
                        <div className="CT-about-mouse d-none d-lg-block">
                            <Icon iconName='iCT-aboutUs-mouse-blue' iconSize='iCT-sD' spaces='' />
                        </div>
                    </div>
                </div>
            </div>
        )
    }
    return carouselPageTemp()
}