import Icon from '../../shared/Icon/Icon'
import './AgencyListView.sass'
import { useMediaQuery } from 'react-responsive';
export default function AgencyListView() {
    let MediaCT1400 =  useMediaQuery({ query: `(max-width: 1400px)` })
    const agencyListViewTemp = () => {
        return (
            <div className="d-flex flex-column w-100 flex-sm-row CT-agency-holder uCT-f-radius-sC cCT-Gradian-Linear-supp shadow">
                {/* Agency Images */}
                <div className="CT-agency-image d-flex uCT-f-radius-sC shadow ">
                    <i className='iCT-x-agancyLogo w-100 uCT-f-radius-sC uCT-img-cover '></i>
                </div>
                {/* Content */}
                <div className="p-0 w-100 d-flex flex-column justify-content-center align-items-sm-start align-items-center pl-sm-4 pl-3 pr-sm-0 pr-3 uCT-f-radius-sC">
                    {/* Agency Name */}
                    <div className="pb-3 pt-sm-4 pt-3">
                        <p className='mb-0 fCT-Regular-sC'>Agency Name</p>
                    </div>
                    {/* Agency Location */}
                    <div className="pb-3">
                        <p className='fCT-Regular-sB text-center m-0 cCT-gray-text'>Dubai, UAE</p>
                    </div>
                    {/* Tags Section */}
                    <div className='d-flex pb-3'>
                        <Icon iconName='iCT-rategold' spaces='p-2 fCT-bold-sB fCT-Regular-sA mr-2' bgColor='cCT-Gradian-Linear-supp shadow-sm' radiusSize='uCT-f-radius-sA' iconSize='iCT-sA ml-1' innerText='4.9' textPosition='left' />
                        <Icon iconName='iCT-authblue' spaces='p-2 fCT-bold-sB fCT-Regular-sA ' bgColor='cCT-Gradian-Linear-supp shadow-sm' radiusSize='uCT-f-radius-sA' iconSize='iCT-sA ml-1' innerText='Excellent' textPosition='left' />
                    </div>
                    {/* Agency Icon Properties */}
                    <div className="fCT-Regular-sB pb-3">
                        <Icon iconName='iCT-propertymoove' spaces='cCT-gray-text' bgColor='cCT-Gradian-Linear-supp' radiusSize='uCT-f-radius-sA' iconSize='iCT-sB mr-3' textPosition='right' innerText='576 Properties' />
                    </div>
                    {/* Ameneties Section Holder */}
                    <div className="d-flex w-100 justify-content-between flex-column flex-md-row align-items-md-center align-items-sm-start align-items-center">
                        {/* Ameneties Section */}
                        <div className="d-flex uCT-b-radius-sC fCT-Regular-sA pb-3 pr-sm-4 pr-xl-5">
                            <div className="d-flex flex-column justify-content-start">
                                <p className='mb-0'>165</p>
                                <p className='mb-0 cCT-gray-text'>Buy</p>
                            </div>
                            <div className='mb-0 uCT-Horizontal-Line mx-4'></div>
                            <div className="d-flex flex-column">
                                <p className='mb-0'>320</p>
                                <p className='mb-0 cCT-gray-text'>Rent</p>
                            </div>
                            <div className='mb-0 uCT-Horizontal-Line mx-4'></div>
                            <div className="d-flex flex-column">
                                <p className='mb-0'>750</p>
                                <p className='mb-0 cCT-gray-text'>Commercial</p>
                            </div>
                        </div>
                        {/* ContentNav Section */}
                        <div className='d-flex align-items-center justify-content-center pb-3  pr-sm-3'>
                            {/* message */}
                            {MediaCT1400
                            ? 
                                <Icon iconName='iCT-messagewhite' spaces='iCT-sE mr-3' bgColor='cCT-Gradian-Linear-Main' radiusSize='uCT-f-radius-sA' iconSize='iCT-sB' />
                            :  
                                <Icon iconName='iCT-messagewhite' spaces='px-5 py-2 mr-3 fCT-Regular-sC 'bgColor='cCT-Gradian-Linear-Main cCT-white-text' radiusSize='uCT-f-radius-sA' iconSize='iCT-sB mr-2' textPosition='right'  innerText='Message' />
                            }
                            {/* webstie */}
                            <Icon iconName='iCT-earthblue' spaces='iCT-sE mr-3' bgColor='cCT-opacity-main-bg-20' radiusSize='uCT-f-radius-sA' iconSize='iCT-sB' />
                            {/* call */}
                            <Icon iconName='iCT-callgreen' spaces='iCT-sE' bgColor='cCT-opacity-green-bg-20' radiusSize='uCT-f-radius-sA' iconSize='iCT-sB' />
                        </div>
                    </div>
                </div>
            </div>
        )
    }
    return agencyListViewTemp()
}