import Icon from '../../../shared/Icon/Icon'
import React, { useState, useEffect } from 'react';

export default function PropertyDetailsComp({APIs={}}) {
     const [APIsBundles, setAPIsBundles] = useState({
          id: String,
          size: String,
          bedroom: String,
          price: String,
          room: String,
          area: String,
          available_from: String,
          lot_size: String,
          bathroom: String,

      });
      useEffect(() => {
          switch(APIs.APIsKey){
              case "PropertyDetails" :
                  setAPIsBundles(prevVal =>({...prevVal, 
                      id : APIs?.data?.propertyDetails?.id,
                      size : APIs?.data?.propertyDetails?.size,
                      bedroom : APIs?.data?.propertyDetails?.bedroom,
                      price : APIs?.data?.propertyDetails?.price,
                      room : APIs?.data?.propertyDetails?.room,
                      area : APIs?.data?.propertyDetails?.area,
                      available_from : APIs?.data?.propertyDetails?.available_from,
                      lot_size : APIs?.data?.propertyDetails?.lot_size,
                      bathroom : APIs?.data?.propertyDetails?.bathroom
                  }))
                  break;
          }
      }, [APIs]);
    const propertyDetailsTemp = () => {
        return (
            <div className="container px-0">
                <div className="row m-0 d-block d-lg-flex pl-2 pl-lg-4 text-capitalize">
                    <div className="col p-0 fCT-Regular-sB">
                        {/* Property Details Icons Row One*/}
                        <div className="d-flex">
                            <Icon iconName='iCT-propertyIDgray' iconSize='iCT-sB mr-3 p-2' textPosition='right' spaces='pb-4' textSpaces='w-100 cCT-dark-text' innerText='property id:' /> 
                            <Icon textPosition='left' spaces='pb-4' textSpaces='w-100 pl-1 cCT-gray-text' innerText={APIsBundles.id} />
                        </div>
                        <div className="d-flex">
                            <Icon iconName='iCT-propertySizegray' iconSize='iCT-sB mr-3 p-2' textPosition='right' spaces='pb-4' textSpaces='w-100 cCT-dark-text' innerText='property size:' />
                            <Icon textPosition='left' spaces='pb-4' textSpaces='w-100 pl-1 cCT-gray-text' innerText={APIsBundles.size} />
                        </div>
                        <div className="d-flex">
                            <Icon iconName='iCT-Bedroomsgray' iconSize='iCT-sB mr-3 p-2' textPosition='right' spaces='pb-4' textSpaces='w-100 cCT-dark-text' innerText='Bedrooms:' />
                            <Icon textPosition='left' spaces='pb-4' textSpaces='w-100 pl-1 cCT-gray-text' innerText={APIsBundles.bedroom} />
                        </div>
                    </div>
                    {/* Property Details Icons Row Two*/}
                    <div className="col p-0 fCT-Regular-sB">
                        <div className="d-flex">
                            <Icon iconName='iCT-dollargray' iconSize='iCT-sB mr-3 p-2' textPosition='right' spaces='pb-4' textSpaces='w-100 cCT-dark-text' innerText='price:' /> 
                            <Icon textPosition='left' spaces='pb-4' textSpaces='w-100 pl-1 cCT-gray-text' innerText={APIsBundles.price} />
                        </div>
                        <div className="d-flex">
                            <Icon iconName='iCT-garagesgray' iconSize='iCT-sB mr-3 p-2' textPosition='right' spaces='pb-4' textSpaces='w-100 cCT-dark-text' innerText='garages:' />
                            <Icon textPosition='left' spaces='pb-4' textSpaces='w-100 pl-1 cCT-gray-text' innerText={APIsBundles.room}/>
                        </div>
                        <div className="d-flex">
                            <Icon iconName='iCT-dategray' iconSize='iCT-sB mr-3 p-2' textPosition='right' spaces='pb-4' textSpaces='w-100 cCT-dark-text' innerText='area:' />
                            <Icon textPosition='left' spaces='pb-4' textSpaces='w-100 pl-1 cCT-gray-text' innerText={APIsBundles.area} />
                        </div>
                    </div>
                    {/* Property Details Icons Row Three*/}
                    <div className="col p-0 fCT-Regular-sB">
                        <div className="d-flex">
                            <Icon iconName='iCT-availablegray' iconSize='iCT-sB mr-3 p-2' textPosition='right' spaces='pb-4' textSpaces='w-100 cCT-dark-text' innerText='available from:' /> 
                            <Icon textPosition='left' spaces='pb-4' textSpaces='w-100 pl-1 cCT-gray-text' innerText={APIsBundles.available_from} />
                        </div>
                        <div className="d-flex">
                            <Icon iconName='iCT-squarePerfootgray' iconSize='iCT-sB mr-3 p-2' textPosition='right' spaces='pb-4' textSpaces='w-100 cCT-dark-text' innerText='property size:' />
                            <Icon textPosition='left' spaces='pb-4' textSpaces='w-100 pl-1 cCT-gray-text' innerText={APIsBundles.lot_size} />
                        </div>
                        <div className="d-flex">
                            <Icon iconName='iCT-bathroomsgray' iconSize='iCT-sB mr-3 p-2' textPosition='right' spaces='pb-4' textSpaces='w-100 cCT-dark-text' innerText='bathrooms:' />
                            <Icon textPosition='left' spaces='pb-4' textSpaces='w-100 pl-1 cCT-gray-text' innerText={APIsBundles.bathroom} />
                        </div>
                    </div>
                </div>
            </div>
        )
    }
    return propertyDetailsTemp()
}