import './agent-profile-card.sass'
import Icon from '../../../../features/shared/Icon/Icon'
import React, { useState, useEffect } from 'react';
export default function AgentProfileCard({APIs = {}}){
    const [APIsBundles, setAPIsBundles] = useState({
          image: String,
          country: String,
          fullname: String,
          image: String
      });

      useEffect(() => {
          switch(APIs.APIsKey){
              case "AgentProfile" :
                  setAPIsBundles(prevVal =>({...prevVal, 
                      image : APIs?.data?.profile_card?.image,
                      avg_review : APIs?.data?.profile_card?.avg_review,
                      fullname : APIs?.data?.profile_card?.fullname,
                      country : APIs?.data?.profile_card?.country?.title
                  }))
                  break;
          }
      }, [APIs]);
    const AgentProfileCardTemp = ()=>{
        return(
            <div className='cCT-white-bg CT-agency-profile-card-holder uCT-f-radius-sC'>
                <div className='CT-agency-profile-card-content p-4'>
                    {/* image */}
                    <div className='d-flex justify-content-center'>
                        <i className="imCT-agent-img uCT-cardImg-holder cCT-smoke-bg~ uCT-f-radius-smid-A uCT-img-contain position-relative" style={{ backgroundImage: `url(${APIsBundles.image})` }}>
                            {/* Agencies Rate */}
                            <div className="d-flex cCT-dark-bg shadow uCT-StarRate-Holder uCT-f-radius-smid-A uCT-StarRate-position justify-content-center align-items-center uCT-border-sB">
                                <Icon iconName='iCT-goldStar' iconSize='iCT-sB ml-1' textPosition='left' innerText={APIsBundles.avg_review} textSpaces='mt-1 cCT-white-text fCT-bold-sA' />
                            </div>
                        </i>
                    </div>
                    {/* Agencies Text Name */}
                    <div className="text-center text-uppercase pt-4 mt-2">
                        <p className="fCT-bold-sE cCT-dark-text mb-0">{APIsBundles.fullname}</p>
                        <p className="fCT-Regular-sB cCT-gray-text pt-2 mb-0" >{APIsBundles.country}</p>
                    </div>
                </div>
            </div>
        )
    }
    return AgentProfileCardTemp()
}