import Button from '@material-ui/core/Button';
import { SnackbarProvider, useSnackbar } from 'notistack';
import { useEffect, useRef } from 'react';

export default function Banner({ message, type, clickMe, clickStatus }) {
    const elementRef = useRef();
    useEffect(() => {
        // setTimeout(() => {
        // }, 1)
        clickMe && elementRef.current.click()
        clickStatus(false)
    }, [clickMe])

    function MyApp({ message, type }) {
        const { enqueueSnackbar } = useSnackbar();
        const handleClickVariant = (variant) => () => {
            // variant could be success, error, warning, info, or default
            enqueueSnackbar(message, { variant });
        };
        return (
            <Button ref={elementRef} className="d-none" id='x' onClick={handleClickVariant(type)}></Button>
        );
    }
    return (
        <SnackbarProvider autoHideDuration={2000} maxSnack={5}>
            <MyApp message={message} type={type} />
        </SnackbarProvider>
    );
}
