import './How-It-Works.scss'
import PagesTitle from 'view/features/shared/pagesTitle/PagesTitle'
import ButtonRightIcon from 'view/features/shared/buttons/Button-Right-Icon/ButtonRightIcon'
import ConnectWithProperty from 'view/features/how-it-works/Connect-With-Property/ConnectWithProperty'
import OurNumbers from 'view/features/how-it-works/Our-Numbers/OurNumbers'
import HowItWorksComp from 'view/features/how-it-works/How-It-Works/HowItWorks'
import SophisticatedControl from 'view/features/how-it-works/Sophisticated-Control/SophisticatedControl'
import OurClients from 'view/features/how-it-works/Our-Clients/OurClients'
import DropInformation from 'view/features/how-it-works/Drop-Information/DropInformation'
import ContactUsFeatures from 'view/features/deal360/home/Contact-Us/ContactUs.features'
import HorizentalLine from 'view/features/shared/HorizentalLine/HorizentalLine'
import ContentSlider from 'view/layout/Components/sliders/ContentSlider/ContentSlider'
export default function HowItWorks() {
    let OurClintArr = [
        <OurClients />,
        <OurClients />,
        <OurClients />,
        <OurClients />,
        <OurClients />,
        <OurClients />,
        <OurClients />,
        <OurClients />
    ]
    const howItWorksTemp = () => {
        return (
            <div className="container-fluid px-0">
                <div className="container mt-5 px-4~">
                    <div className="CT-how-it-works-container">
                        {/* Connect With Property */}
                        <div className="CT-connect-with-property">
                            <ConnectWithProperty />
                        </div>
                        {/* Our-numbers */}
                        <div className="CT-our-numbers">
                            <HorizentalLine classes='md' />
                            <OurNumbers />
                            <HorizentalLine classes='md' />
                        </div>
                        {/* How It Works Comp */}
                        <div className="CT-how-works">
                            <HowItWorksComp />
                            <HorizentalLine classes='md' />
                        </div>
                        {/* Sophisticated Control */}
                        <div className="CT-sophisticated-control">
                            <SophisticatedControl />
                            <HorizentalLine classes='md' />
                        </div>
                        {/* Our Clients */}
                            <div className="CT-our-clients text-left">
                            <div className="fCT-bold-54 cCT-dark-text ">
                                <p className='mb-0'>Our Clients</p>
                            </div>
                            <div className='w-100'>
                                <ContentSlider content={OurClintArr} title='' />
                            </div>
                            </div>
                            {/* Button Icon */}
                            <div className="pt-5 w-100">
                                <ButtonRightIcon innerText='Load More Clients' btnICON='iCT-down-arrow' btnCOLOR='cCT-Gradian-Linear-Main' iconBgCOLOR='cCT-inherit-bg' position='center' />
                            </div>
                            <HorizentalLine classes='md' />
                        {/* Contact Us Features */}
                        <div className="contact-us">
                            <ContactUsFeatures />
                            <HorizentalLine classes='md' />
                        </div>
                    </div>
                </div>
                {/* Drop Information */}
                <div className="CT-drop-information">
                    <DropInformation />
                </div>
            </div>

        )
    }
    return howItWorksTemp()
}