
import './PropertyListCard.sass'
import Icon from '../../shared/Icon/Icon'
import ImagesSlider from 'view/layout/Components/sliders/ImagesSlider/images-slider.layout'
import React, { useState, useEffect, useRef } from 'react';
import { useMediaQuery } from 'react-responsive';
import { Link } from "react-router-dom";



const PropertyListCard = React.memo(({APIs = new Object()}) => {
    const [APIsBundles, setAPIsBundles] = useState({
        id: Number,
        image: String,
        property_image: String,
        fullname: String,
        title: String,
        country_title: String,
        type_title : String,
        category_title : String,
        high_light_title : Number,
        bathroom : Number,
        bedroom : Number,
        area : Number,
        bathroom : Number,
        currency_title : Number,
        price : Number
    });
    const [rightHandSectionSize, setRightHandSectionSize] = useState({
            width: undefined,
            height: undefined,
        });
    
    useEffect(() => {
        switch(APIs.APIsKey){
            case "MapView" :
                setAPIsBundles(prevVal =>({...prevVal, 
                    id : APIs?.data?.property?.id,
                    image : APIs?.data?.property?.user?.image,
                    property_image : APIs?.data?.property?.images,
                    fullname : APIs?.data?.property?.user?.fullname,
                    title : APIs?.data?.property?.title,
                    country_title : APIs?.data?.property?.city?.title,
                    type_title  : APIs?.data?.property?.type?.title ,
                    category_title : APIs?.data?.property?.category?.title,
                    high_light_title : APIs?.data?.property?.high_light?.title,
                    bathroom : APIs?.data?.property?.bathroom,
                    bedroom : APIs?.data?.property?.bedroom,
                    area : APIs?.data?.property?.area,
                    currency_title : APIs?.data?.property?.currency?.title,
                    price : APIs?.data?.property?.price
                }))
                break;
            case "PropertiesList" :
                setAPIsBundles(prevVal =>({...prevVal, 
                    id : APIs?.data?.property?.id,
                    image : APIs?.data?.property?.user?.image,
                    property_image : APIs?.data?.property?.images,
                    fullname : APIs?.data?.property?.user?.fullname,
                    title : APIs?.data?.property?.title,
                    country_title : APIs?.data?.property?.city?.title,
                    type_title  : APIs?.data?.property?.type?.title ,
                    category_title : APIs?.data?.property?.category?.title,
                    high_light_title : APIs?.data?.property?.high_light?.title,
                    bathroom : APIs?.data?.property?.bathroom,
                    bedroom : APIs?.data?.property?.bedroom,
                    area : APIs?.data?.property?.area,
                    currency_title : APIs?.data?.property?.currency?.title,
                    price : APIs?.data?.property?.price
                }))
                break;
            }
    }, []);

    useEffect(() => {
        // Handler to call on window resize
        function handleResize() {
            // Set Content Section width/height to state
            setRightHandSectionSize({...rightHandSectionSize,
                width: document.getElementById('rightSection').clientWidth,
                height: document.getElementById('rightSection').clientHeight,
            });
        }
        // Add event listener
        window.addEventListener("resize", handleResize, false);
        // Call handler right away so state gets updated with initial window size
        handleResize();
        // Remove event listener on cleanup
        return () => window.removeEventListener("resize", handleResize);
    }, [APIsBundles]);
    
    useEffect(() => {
        // Handler to call on window resize
        function handleResize() {
            // Set Content Section width/height to state
            setRightHandSectionSize({...rightHandSectionSize,
                width: document.getElementById('rightSection').clientWidth,
                height: document.getElementById('rightSection').clientHeight,
            });
        }
        // Add event listener
        window.addEventListener("resize", handleResize, false);
        // Call handler right away so state gets updated with initial window size
        handleResize();
        // Remove event listener on cleanup
        return () => window.removeEventListener("resize", handleResize);
    }, []);

    const PropertyCardTemp = () => {
        return (
            <Link key={APIsBundles?.id} to={`/property-details/${APIsBundles?.id}`}  className='d-flex flex-column h-100~ w-100'>
                <div className="cCT-smoke-bg -flex~ uCT-f-radius-sB CT-List-Card-Holder bg-danger~ align-items-center shadow">
                    {/* Slider Holder */}
                    <div  className={`h-100`}>
                        <ImagesSlider _ID={APIsBundles.id} propertyImages={APIs?.data?.property?.images} rightHandSectionSize={rightHandSectionSize}/>
                    </div>
                    {/* Details Section 2nd */}
                    <div id={`rightSection`}  className={`d-flex justify-content-between align-items-center~  uCT-click`}>
                        <div className={`${rightHandSectionSize.width < 461.5 ? 'p-3' : 'py-4 pl-4 pr-2'} d-flex flex-column justify-content-center`}>
                            {/* Title Section */}
                            <div className={`d-flex flex-column~ align-items-center justify-content-between w-100  position-relative ${rightHandSectionSize.width < 461.5 ? 'mb-2~ fCT-Regular-sC' : 'mb-3~ fCT-Regular-sC'} `}>
                                <div className={`mr-3 mb-3~ w-100 `} style={{height: rightHandSectionSize.width > 461.5 ? '46px' : '40px'}}>
                                    <span className='mb-0 w-100 h-100~ fCT-Regular-sC~ uCT-ellipsis-2-line'>{APIsBundles.title}</span>
                                </div>
                            </div>
                            {/* Ameneties Section */}
                            <div className='d-flex flex-column align-items-start justify-content-between cCT-secondary-text-tx'>
                                <div className={`d-flex`}>
                                    {/* Amenties */}
                                    <div className={`d-flex ${rightHandSectionSize.width > 461.5 ? 'flex-column mr-4' : 'align-items-center justify-content-center'} ${rightHandSectionSize.width > (379) ? 'my-3' : 'my-2'} fCT-Light-sA`}>
                                        <div className='d-flex align-items-center'>
                                            <i className={`uCT-img-cover iCT-bathroom ${rightHandSectionSize.width > 461.5 ? 'mr-2 px-2 iCT-sB' : 'mr-2 iCT-sA'}`}></i>
                                            <p className='mb-0'>{APIsBundles.bathroom} {rightHandSectionSize.width > 461.5 ? 'Bathrooms' : ''}</p>
                                        </div>
                                        <div className={`d-flex align-items-center ${rightHandSectionSize.width < 461.5 ? 'mx-3' : 'my-3'}`}>
                                            <i className={`uCT-img-cover iCT-bedroom ${rightHandSectionSize.width > 461.5 ? 'mr-2 px-2 iCT-sB' : 'mr-2 iCT-sA'}`}></i>
                                            <p className='mb-0'>{APIsBundles.bedroom} {rightHandSectionSize.width > 461.5 ? 'Bedrooms' : ''}</p>
                                        </div>
                                        <div className='d-flex align-items-center'>
                                            <i className={`uCT-img-cover iCT-size ${rightHandSectionSize.width > 461.5 ? 'mr-2 px-2 iCT-sB' : 'mr-2 iCT-sA'}`}></i>
                                            <p className='mb-0'>{APIsBundles.area} {rightHandSectionSize.width > 461.5 ? 'Size' : ''}</p>
                                        </div>
                                    </div>
                                    {/* Sec Section Of Ameneties */}
                                    {/* {rightHandSectionSize.width > 461.5 ? 
                                    <div className={`d-flex ${rightHandSectionSize.width > 461.5 ? 'flex-column' : 'align-items-center justify-content-center'} ${rightHandSectionSize.width > (379) ? 'my-3' : 'my-2'} fCT-Light-sA`}>
                                        <div className='d-flex align-items-center'>
                                            <i className={`uCT-img-cover iCT-bathroom ${rightHandSectionSize.width > 461.5 ? 'mr-2 px-2 iCT-sB' : 'mr-2 iCT-sA'}`}></i>
                                            <p className='mb-0'>{APIsBundles.bathroom} {rightHandSectionSize.width > 461.5 ? 'Bathrooms' : ''}</p>
                                        </div>
                                        <div className={`d-flex align-items-center ${rightHandSectionSize.width < 461.5 ? 'mx-3' : 'my-3'}`}>
                                            <i className={`uCT-img-cover iCT-bedroom ${rightHandSectionSize.width > 461.5 ? 'mr-2 px-2 iCT-sB' : 'mr-2 iCT-sA'}`}></i>
                                            <p className='mb-0'>{APIsBundles.bedroom} {rightHandSectionSize.width > 461.5 ? 'Bedrooms' : ''}</p>
                                        </div>
                                        <div className='d-flex align-items-center'>
                                            <i className={`uCT-img-cover iCT-size ${rightHandSectionSize.width > 461.5 ? 'mr-2 px-2 iCT-sB' : 'mr-2 iCT-sA'}`}></i>
                                            <p className='mb-0'>{APIsBundles.area} {rightHandSectionSize.width > 461.5 ? 'Size' : ''}</p>
                                        </div>
                                    </div>
                                    : false} */}

                                </div>
                                {/* Tages  */}
                                <div className={`d-flex justify-content-start align-items-start h-100  fCT-Light-sA cCT-main-text ${rightHandSectionSize.width > (379) ? 'mb-3' : 'mb-2'}`} >
                                    <span className={`${rightHandSectionSize.width > 461.5 ? 'px-3 py-1 mr-2' : 'px-2 py-0 mr-2'} shadow-sm uCT-f-radius-sA cCT-main-text cCT-white-bg`} style={{fontSize: rightHandSectionSize.width > 461.5 ? '12px' : '10px'}}>{APIsBundles.type_title}</span>
                                    <span className={`${rightHandSectionSize.width > 461.5 ? 'px-3 py-1 mr-2' : 'px-2 py-0 mr-2'} shadow-sm uCT-f-radius-sA fCT-bold-sA~ cCT-white-text`} style={{backgroundColor: APIsBundles.high_light_title == 'new offer'? '#9c9dfe' : 'red', textTransform: 'capitalize' , fontSize: rightHandSectionSize.width > 461.5 ? '12px' : '10px'}}>{APIsBundles.high_light_title}</span>
                                    <span className={`${rightHandSectionSize.width > 461.5 ? 'px-3 py-1 mr-2' : 'px-2 py-0 mr-2'} shadow-sm uCT-f-radius-sA cCT-main-text cCT-white-bg`} style={{fontSize: rightHandSectionSize.width > 461.5 ? '12px' : '10px'}}>{APIsBundles.category_title}</span>
                                </div>
                                {/* Rates */}
                                {rightHandSectionSize.width < 380 ? 
                                    <div className={`d-flex w-100~ align-items-end my-2`}>
                                        <div className={`d-flex justify-content-center mb-3~ `} >
                                            <Icon iconName='iCT-rategold' iconSize='iCT-sA uCT-img-100' spaces='mr-1 fCT-Regular-sC'/>
                                            <Icon iconName='iCT-rategold' iconSize='iCT-sA uCT-img-100' spaces='mr-1 fCT-Regular-sC'/>
                                            <Icon iconName='iCT-rategold' iconSize='iCT-sA uCT-img-100' spaces='mr-1 fCT-Regular-sC'/>
                                            <Icon iconName='iCT-rategray' iconSize='iCT-sA uCT-img-100' spaces='mr-1 fCT-Regular-sC'/>
                                            <Icon iconName='iCT-rategray' iconSize='iCT-sA uCT-img-100' spaces='mr-1 fCT-Regular-sC'/>
                                        </div>  
                                    </div>
                                : false}
                                {/* Property Name And Icon Holder 2nd */}
                                <div className={`mt-3~ d-flex align-items-center justify-content-start  w-100`}>
                                    {/* Price Section */}
                                    {rightHandSectionSize.width < 380 ? 
                                    <div className={`d-flex align-items-center mr-4  ${rightHandSectionSize.width < 461.5 ? 'mb-2~ fCT-bold-sB' : 'fCT-bold-sF mb-3~'}`}>
                                        <Icon iconName='iCT-pricemoove' spaces='cCT-main-text fCT-bold-sE~ mr-1' iconSize='iCT-sB mr-2' textPosition='right' innerText={APIsBundles.price} />
                                        <p className={` cCT-gray-text ${rightHandSectionSize.width < 461.5 ? 'fCT-bold-sA mt-auto m-0' : 'fCT-bold-sC '}`}>{APIsBundles.currency_title}</p>
                                    </div>: false}
                                    {/* Information Section */}
                                    <div className={`d-flex ml-auto~`}>
                                        {/* Logo Holder 1st Section */}
                                        <div className={`cCT-white-bg d-flex align-items-center  justify-content-center shadow-sm uCT-f-radius-sC ${rightHandSectionSize.width < 461.5 ? 'mr-2~' : 'mr-3 py-1 px-2'}`} style={{width: rightHandSectionSize.width > 461.5 ? '150px' : '24px'}}>
                                            <i style={{ backgroundImage: `url(${APIsBundles.image ? APIsBundles.image : ''})` }} className={`uCT-img-cover d-block rounded-circle ${rightHandSectionSize.width < 461.5 ? 'iCT-sA~ p-2 mr-2~' : 'iCT-sD~ p-3 mr-1'}`}></i>
                                            { rightHandSectionSize.width > 461.5 ?
                                                <p className="d-flex mb-0 fCT-bold-sA uCT-ellipsis-1-line mr-2~  justify-content-start">{APIsBundles.fullname ? APIsBundles.fullname : 'PlaceHolder'}</p>
                                                : false
                                            }
                                        </div>
                                        {/* Actions Icons Holder 2nd Section */}
                                        <div className=" d-flex align-items-center uCT-f-radius-sC">
                                            <Icon iconName='iCT-callgreen' spaces={`${rightHandSectionSize.width < 461.5 ? 'iCT-sC mx-2' : 'iCT-sD mr-3'}`} radiusSize='rounded-circle' bgColor='cCT-opacity-green-bg-20 ' iconSize={`${rightHandSectionSize.width < 461.5 ? 'iCT-sA' : 'iCT-sB'}`} />
                                            <Icon iconName='iCT-comparemoove' spaces={`${rightHandSectionSize.width < 461.5 ? 'iCT-sC' : 'iCT-sD'}`} radiusSize='rounded-circle' bgColor='cCT-opacity-main-bg-20 ' iconSize={`${rightHandSectionSize.width < 461.5 ? 'iCT-sA' : 'iCT-sB'}`} />
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>

                        {/* Third Section */}
                        {rightHandSectionSize.width > (379) ? 
                            <div className={`ml-auto~ d-flex flex-column align-items-center justify-content-center ${rightHandSectionSize.width > 461.5 ? 'CT-Border-Style-Holder-LG p-4' : 'CT-Border-Style-Holder-SM'}  p-2`} >
                                {/* Price Section */}
                                <div className={`d-flex align-items-center  ${rightHandSectionSize.width < 461.5 ? 'mb-2~ fCT-bold-sB' : 'fCT-bold-sF mb-3~'}`}>
                                    <Icon iconName='iCT-pricemoove' spaces='cCT-main-text fCT-bold-sE~ mr-1' iconSize='iCT-sB mr-2' textPosition='right' innerText={APIsBundles.price} />
                                    <p className={`mb-0 cCT-gray-text ${rightHandSectionSize.width < 461.5 ? 'fCT-bold-sA ' : 'fCT-bold-sC  mt-auto pb-1'}`}>{APIsBundles.currency_title}</p>
                                </div>
                                {/* Rate Stars */}
                                <div className={`d-flex w-100~ align-items-end my-3`}>
                                    <div className={`d-flex justify-content-center mb-3~ `} >
                                        <Icon iconName='iCT-rategold' iconSize='iCT-sA uCT-img-100' spaces='mr-1 fCT-Regular-sC'/>
                                        <Icon iconName='iCT-rategold' iconSize='iCT-sA uCT-img-100' spaces='mr-1 fCT-Regular-sC'/>
                                        <Icon iconName='iCT-rategold' iconSize='iCT-sA uCT-img-100' spaces='mr-1 fCT-Regular-sC'/>
                                        <Icon iconName='iCT-rategray' iconSize='iCT-sA uCT-img-100' spaces='mr-1 fCT-Regular-sC'/>
                                        <Icon iconName='iCT-rategray' iconSize='iCT-sA uCT-img-100' spaces='mr-1 fCT-Regular-sC'/>
                                    </div>  
                                </div>
                                {/* View Details BTN */}
                                <div className=''>
                                    <button className={`btn~ border-0 uCT-f-radius-sC cCT-orange-bg cCT-white-text px-3 py-2 `} style={{ fontSize : 
                                    rightHandSectionSize.width > 461.5 ? '12px' : '10px'}}>View Details</button>
                                </div>
                            </div>
                        : false}
                    </div>
                </div>
                
            </Link>
        )
    }
    return PropertyCardTemp()
})

export default PropertyListCard

