import './light-box.popups.sass'
// import { MediaCT } from 'view/viewLogic/view-logic'
import { useState, useEffect } from 'react'

export default function LightBoxPopup({image, multiImages, btnToggle, btnEventCheck, closeButton}){

  const [imageToShow, setImageToShow] = useState();
  const [lightboxDisplay, setLightBoxDisplay] = useState(false);
  
  //function to show a specific image in the lightbox, amd make lightbox visible
  const showImage = (image) => {
    setImageToShow(image);
    setLightBoxDisplay(true);
  }

  useEffect(() => {
    switch (btnToggle) {
      case 'open':
      setImageToShow(multiImages[0]);
      setLightBoxDisplay(true);
      case 'close':
        hideLightBox()
        break;
    }
  }, [btnToggle]);

  //hide lightbox
  const hideLightBox = () => {
    setLightBoxDisplay(false);
      setImageToShow(multiImages[0]);

  };
 
  //show next image in lightbox 
  const showNext = (e) => {
    e.stopPropagation();
    let currentIndex = multiImages.indexOf(imageToShow);
    if (currentIndex >= multiImages.length - 1) {
      setLightBoxDisplay(false);
    } else {
      let nextImage = multiImages[currentIndex + 1];
      setImageToShow(nextImage);
    }
  };

  //show previous image in lightbox
  const showPrev = (e) => {
    e.stopPropagation();
    let currentIndex = multiImages.indexOf(imageToShow);
    if (currentIndex <= 0) {
      setLightBoxDisplay(false);
    } else {
      let nextImage = multiImages[currentIndex - 1];
      setImageToShow(nextImage);
    }
  }; 

  const LightBoxPopupTemp = ()=>{
      return (
          <>
            {/* Show Image In Dom */}
            {<div className="uCT-img-cover w-100 h-100 " onClick={() => { showImage(image)}} style={{ backgroundImage: `url('${image}')`}}> </div>}
            {/* Light Box Show */}
            {
              lightboxDisplay || btnToggle == 'open' ? 
              <div id="lightbox" className={`d-flex align-items-center justify-content-center w-100 h-100`} onClick={hideLightBox}> 
                {/* Start Section ( Left Arrow ) */}
                <div className={`w-25 h-100 d-flex align-items-center justify-content-center`} onClick={showPrev}>
                    <i className={`iCT-sE uCT-img-cover iCT-slidersArrowLeft`}></i>
                </div>
                {/* Center Section ( Image ) */}
                  <div id="lightbox-img" className={`uCT-img-contain uCT-t-2-all~`} onClick={() => btnToggle == 'open' ?  btnEventCheck() : false} style={{ backgroundImage: `url('${imageToShow}')`}}></div>
                {/* End Section ( Right Arrow ) */}
                <div className={`w-25 h-100 d-flex align-items-center justify-content-center`} onClick={showNext}>
                    <i className={`iCT-sE uCT-img-cover iCT-slidersArrowRight`}></i>
                </div>
                {/* Close Button */}
                { btnToggle == 'open' ?
                <div className={`d-flex p-5 position-fixed`} style={{right: '0px' , top: '0'}}>{closeButton}</div> 
                : 
                <div onClick={() => {hideLightBox()}} className={`p-5 border-0 d-flex position-fixed `} style={{right: '0px' , top: '0'}}>
                  <i className={`iCT-sE uCT-img-cover iCT-closewhite`}></i>
                </div>
                }
              </div>
            : ""
            } 
          </>
      );
    }
  return LightBoxPopupTemp()
}