import './PropertyTitle.sass'
import Icon from 'view/features/shared/Icon/Icon'
import Tags from 'view/features/shared/Tags/Tags'
import React, { useState, useEffect } from 'react';
export default function PropertyTitle({APIs={}}) {
     const [APIsBundles, setAPIsBundles] = useState({
          title: String,
          price: String,
          currency_title: String,
          city_title: String,
          country_title: String,
      });

      useEffect(() => {
          switch(APIs.APIsKey){
              case "PropertyDetails" :
                  setAPIsBundles(prevVal =>({...prevVal, 
                      title : APIs?.data?.main?.title,
                      price : APIs?.data?.main?.price,
                      currency_title : APIs?.data?.main?.currency?.title,
                      city_title : APIs?.data?.main?.city?.title,
                      country_title : APIs?.data?.main?.country?.title,
                      category_title : APIs?.data?.main?.category?.title,
                      high_light_title : APIs?.data?.main?.high_light?.title,
                      type_title : APIs?.data?.main?.type?.title,
                  }))
                  break;
          }
      }, [APIs]);
    const propertyTitleTemp = () => {
        return (
            <div className="container mx-auto px-0 w-100 cCT-Gradian-Linear-supp2 uCT-t-radius-sC">
                {/* Prop Title */}
                <div className="pl-3 px-lg-4 pt-4 CT-bold-32">
                    <p className="mb-0" >{APIsBundles.title}</p>
                </div>
                {/* Prop Price */}
                <div className="d-flex align-items-center justify-content-start pl-3 ml-lg-4 px-lg-4 mt-4">
                    <Icon textPosition='right' iconName='iCT-dollarmoove' iconSize='iCT-sC mr-4' innerText={APIsBundles.price} textSpaces='CT-bold-32 cCT-main-text' />
                    <Icon textPosition='right' innerText={APIsBundles.currency_title} textSpaces='ml-1 mt-1 mb-0 cCT-gray-text fCT-bold-sC ' />
                </div>
                {/* Prop Tags */}
                <div className="py-4 pl-3 mx-lg-4 pl-lg-4 ">
                    <Tags innerText={APIsBundles.city_title} spaces='mr-2 pb-3'  />
                    <Tags innerText={APIsBundles.country_title} spaces='mr-2 pb-3' />
                    <Tags innerText={APIsBundles.category_title} spaces='mr-2 pb-3' />
                    <Tags innerText={APIsBundles.high_light_title} spaces='mr-2 pb-3' />
                    <Tags innerText={APIsBundles.type_title} spaces='mr-2 pb-3' />
                </div>
            </div>
        )
    }
    return propertyTitleTemp()
}