import React, { useState, useEffect } from 'react';

export default function ProfiileAbout({APIs = {}}) {
    const [APIsBundles, setAPIsBundles] = useState({
        about_me: String
    });

    useEffect(() => {
        switch(APIs.APIsKey){
            case "AgencyProfile" :
                setAPIsBundles(prevVal =>({...prevVal, 
                    about_me : APIs?.data?.about_me
                }))
                break;
            case "AgentProfile" :
                setAPIsBundles(prevVal =>({...prevVal, 
                    about_me : APIs?.data?.about_me
                }))
                break;
        }
    }, [APIs]);
    const profiileAboutTemp = () => {
        return (
            <div className='pl-md-4 pl-2'>
                <p className='m-0 pl-md-4 cCT-gray-text text-break fCT-Light-sB'>
                    {APIsBundles.about_me == '' ? 'No Data Here' : APIsBundles.about_me}
                </p>
            </div>
        )
    }
    return profiileAboutTemp()
}