
import ArticleThumbnail from '../../features/blog/blog-article/Article-Thumbnail/Article-Thumbnail'
import PageHead from    '../..//layout/Components/PageHead/PageHead'
import RightWidget from '../../layout/Components/widgets/Right-Widget/Right-Widget'
import ArticleContent from '../../features/blog/blog-article/Article-Content/Article-Content'
import ShareArticle from '../../features/blog/blog-article/Share-Article/Share-Article'
import ArticleCreateComment from '../../features/blog/blog-article/Article-Create-Comment/Article-Create-Comment'
import PagesTitle from '../../features/shared/pagesTitle/PagesTitle'
import ReviewCard from '../../features/profiles/Review-Card/ReviewCard'
import BlogGridCard from '../../features/blog/Blog-Grid-Card/Blog-Grid-Card'
import ButtonRightIcon from '../../features/shared/buttons/Button-Right-Icon/ButtonRightIcon'
import ContentSlider from 'view/layout/Components/sliders/ContentSlider/ContentSlider'
import AdvSmCard from 'view/features/advertisement/Adv-SM-Card/Adv-SM-Card'
import PopularPosts from 'view/features/blog/Popular-Posts/Popular-Posts'
import { MediaCT } from 'view/viewLogic/view-logic'
export default function BlogArticle(){
    let ContentSliderArr = [<BlogGridCard />, <BlogGridCard />, <BlogGridCard />]
    const rightWidgetContent = () => {
        return (
            <section className='w-100'>
                <AdvSmCard />
                <PagesTitle title='Popular Posts' />
                <PopularPosts />
                <PopularPosts />
                <PopularPosts />
            </section>
        )
    }
    const blogArticleTemp = ()=>{
        return (
            <div>
                <div className='container-fluid p-0'>
                    {/* page head */}
                <header>
                    <PageHead title="FIND THE LAST NEWS & TRENDS" subTitle="Home is One Click Away ." pImg="BlogArticle-Img"/>
                </header>
                 {/* page body */}
                 <div className='container px-md-4 px-3'>
                    <div className='row m-0 flex-nowrap'>
                        {/* page content */}
                        <div className='flex-grow-1 pt-4'>
                            {/* Thumbnail */}
                            <section className='d-flex flex-wrap w-100'>
                                {/* Article Thumbnail */}
                                <ArticleThumbnail/> 
                                <PagesTitle title='Best Things To Do In Abu Dhabi' smallUnderTitle='This article was published on April 26th, 2021' />
                            </section>
                            {/* content */}
                            <section className='pl-md-4 pl-1 pb-4'>
                                <ArticleContent />
                            </section>
                            {/* Share Article */}
                            <section className='pb-4'>
                                <ShareArticle/>
                            </section>
                            {/* leave a comment */}
                            <section className='pb-4'>
                                <PagesTitle title='Leave A Comment' smallUnderTitle='Comment as a guest' />
                                <div className='pl-md-4 pl-1 pb-4'>
                                    <ArticleCreateComment/>
                                </div>
                                {/* reviews */}
                                <div className='pl-md-4 pl-1'>
                                    <PagesTitle title='Comments :' />
                                        <div className='mt-4 uCT-cards-grid justify-content-center'>
                                        <ReviewCard />
                                        <ReviewCard />
                                        <ReviewCard />
                                        <ReviewCard />
                                        <ReviewCard />
                                        <ReviewCard />
                                        <div class=' ml-4 w-100'>
                                            <ButtonRightIcon innerText='Load More Reviews' btnICON='iCT-down-arrow' btnCOLOR='cCT-Gradian-Linear-Main' iconBgCOLOR='cCT-inherit-bg' position='center' />
                                        </div>
                                    </div>
                                </div>
                            </section>
                            <section className=' pb-4'>
                                <ContentSlider content={ContentSliderArr} title='Related Article' />
                            </section>
                            
                        </div>
                        {/* page widget */}
                        {MediaCT('lg') ? false :
                            <div id='rightWidget' className=''>
                                    <RightWidget content={rightWidgetContent()} />
                            </div>
                        }
                    </div>
                </div>
                </div>
               
            </div>
        )
    }
    return blogArticleTemp()
}