import Icon from '../../Icon/Icon'
export default function ButtonRightIcon({innerText, btnCOLOR, iconBgCOLOR, btnICON, position}){
    switch(position){
        case 'left':
            position = 'justify-content-start'
            break;
        case 'center':
            position = 'justify-content-center'
            break;
        case 'right':
            position = 'justify-content-end'
            break;
    }
    const buttonTemp = ()=>{
        return (
            <div className={`${position} w-100 d-flex`}>
                <div className={`${btnCOLOR} uCT-scale-in-animation text-center uCT-f-radius-sA shadow d-inline-flex`}>
                    <button className='btn  cCT-white-text px-0 py-0 uCT-f-radius-sA d-flex align-items-center w-100'>
                        <p className='m-0 px-5'>{innerText}</p>
                        <div className={`${iconBgCOLOR} h-100 py-2 ml-auto uCT-f-radius-sA shadow-sm`}>
                            <Icon iconName={btnICON} iconSize='iCT-sB uCT-img-contain' spaces='py-2 px-3'/> 
                        </div>
                    </button>
                </div>
            </div>
        )
    }
    return buttonTemp()
}