export default function AgentWireFrame() {
    const agentWireFrameTemp = () => {
        return (
            <div className='uCT-mini-card uCT-wire-frame-opacity-5 '>
                <div className="uCT-f-radius-sC d-flex flex-column cCT-layout-I ">
                    {/* Profile Image Section 1st */}
                    <div className="d-flex align-items-center justify-content-center pb-4">
                        <i style={{ height: '195px' }} className="cCT-layout-II uCT-cardImg-holder uCT-f-radius-smid-A uCT-img-contain mt-4 position-relative">
                            {/* Agent Rate */}
                            <div className="aCT-wire-frame-loading-agencies cCT-layout-I uCT-StarRate-Holder d-flex shadow-sm  uCT-f-radius-smid-A uCT-StarRate-position justify-content-center align-items-center">
                            </div>
                        </i>
                    </div>
                    {/* UserName Section 2nd */}
                    <div className="text-center d-flex flex-column align-items-center mb-4">
                        {/* Username */}
                        <p style={{ width: '180px' }} className="my-3 aCT-wire-frame-loading-agencies uCT-f-radius fCT-wireFrame-hG"></p>
                        {/* Role NAme , and ID , Company Name */}
                        <p style={{ width: '90px' }} className="mb-2 aCT-wire-frame-loading-agencies uCT-f-radius fCT-wireFrame-hB"></p>
                    </div>

                    {/* BTN Section 3rd */}
                    <div className="d-flex justify-content-center align-items-center">
                        {/* Message Icon */}
                        <button style={{ width: '144px' }} className='btn aCT-wire-frame-loading-agencies fCT-wireFrame-hG uCT-f-radius cCT-layout-I mr-3 px-4'>
                        </button>
                        {/* Call Icon */}
                        <button className='btn uCT-icon-holder aCT-wire-frame-loading-agencies uCT-f-radius cCT-layout-I'>
                        </button>
                    </div>
                    {/* Agent Ameneties Section */}
                    <div className="d-flex justify-content-center align-items-center mt-4 uCT-b-radius-sC w-100 cCT-layout-II">
                        <div style={{ height: '44px' }} className={`mt-4`}></div>
                    </div>
                </div>
            </div>
        )
    }
    return agentWireFrameTemp()
}