import '../home.sass'
import AgencieCard from 'view/features/agency/Agencies-Card/AgenciesCard'
import ContentSlider from 'view/layout/Components/sliders/ContentSlider/ContentSlider'
import { MediaCT } from 'view/viewLogic/view-logic'

export default function exceptionalDevelopers({ APIs = new Object() }){

    const exceptionalDevelopersTemp = ()=>{
        return(
          <div className={`${MediaCT('md') ? 'py-4 px-3~' : 'pb-4'} px-3`}>
            <div className={`p-0 ${MediaCT('md') ? 'col' : 'col-6 '}`}>
              <p className={`${MediaCT('md') ? 'fCT-bold-sE mb-3' : 'mb-4 pb-2 fCT-bold-54'}`}>Our exceptional {MediaCT('md') ? '' : <br />} Developers</p>
              <p className={`${MediaCT('md') ? 'fCT-Regular-sB ml-1' : 'mb-4 ml-4 fCT-Regular-sE '}`}>Check out our amazing list of developers.</p>
            </div>
            <div className='d-flex~ w-100'>
                <ContentSlider content={
                  APIs?.data?.agencies?.data?.data?.company?.map((agency) => {
                    return <AgencieCard APIs={{ from: 'AgenciesList', data: { agency } }} key={agency.id} />
                  })
                } />
            </div>
          </div>
        )
    }
    return exceptionalDevelopersTemp()
}