import PageHead from 'view/layout/Components/PageHead/PageHead'
import AgentCard from 'view/features/agent/AgentCard/AgentCard'
import PagesTitle from 'view/features/shared/pagesTitle/PagesTitle'
import { _GET_AllAgents } from 'Controllers/services/agent/agent.services'
import AgentWireFrame from 'view/features/agent/AgentCard/Agent-WireFrame/Agent.WireFrame'
import React, { useEffect, useState } from 'react';
export default function AgentList() {
    const [allAgentsList, setAllAgentsList] = useState([])
    const [APIsKey, setAPIsKey] = useState('AgentList')
    useEffect(()=>{
        _GET_AllAgents()
        .then(res =>{
            setAllAgentsList(res)
        }).catch()
    },[])

    const agentListTemp = () => {
        return (
            <div className='container-fluid p-0'>
                {/* page head */}
                <header>
                    <PageHead title="Our Great Agents" subTitle="A great real estate agent don't sells, he helps." pImg="imCT-agentListImg" />
                </header>
                {/* page body */}
                <div class='container p-md-4 p-3'>
                    <PagesTitle title='Agents' />
                    <section className='uCT-card-grid-holder w-100 mb-4'>
                        {allAgentsList?.data?.data?.agent.map((agent) => {
                            return <AgentCard APIs={{APIsKey, data: {agent}}} key={agent.id}/>
                        })}
                        <AgentWireFrame/>
                        {/* dummy Cards */}
                        <div className='uCT-mini-dummy'></div>
                        <div className='uCT-mini-dummy'></div>    
                        <div className='uCT-mini-dummy'></div>    
                        <div className='uCT-mini-dummy'></div>    
                    </section>
                </div>
            </div>
        )
    }
    return agentListTemp()
}