import './DropInformation.scss'
import LayerIV from '../../../../assets/images/LayerIV.png'
import LayerV from '../../../../assets/images/LayerV.png'
import LayerVI from '../../../../assets/images/LayerVI.png'
import LayerVII from '../../../../assets/images/LayerVII.png'
export default function DropInformation() {
    const dropInformationTemp = () => {
        return(
            <div className="text-center imCT-drop-info-bg position-relative ">
                    <img src={LayerIV}  className='position-absolute CT-LayerIV' alt="" />
                    <img src={LayerV}   className='position-absolute CT-LayerV' alt="" />
                    <img src={LayerVI}  className='position-absolute CT-LayerVI' alt="" />
                    <img src={LayerVII} className='position-absolute CT-LayerVII' alt="" />
                {/* Text Drom Info */}
                <div className="position-relative">
                    <p className='mb-0 CT-font-bold imCT-line-group CT-Information-line cCT-dark-text'>Drop Your Information, <br /> And Let Us Contact You Very Soon</p>
                    {/* line-group */}
                </div>
                {/* Property-Button */}
                <div className="d-flex align-items-center justify-content-center pt-4 pb-5">
                    <a href="#" className="CT-zIndex px-4 uCT-f-radius-sC border-0 shadow btn px-xl-4 py-xl-3 CT-BTN-Holder cCT-Gradian-Linear-Main cCT-white-text d-flex align-items-center justify-content-center">
                        <i className="iCT-add-white uCT-img-cover iCT-sB "></i>
                        <p className='mb-0 ml-2 text-nowrap'> Register Now </p>
                    </a>
                </div>
            </div>
        )
    }
    return dropInformationTemp()
}