export default function NotificationCardWireFrame(){
    const notificationCardWireFrameTemp = () => {
        return(
            <div className='uCT-wire-frame-opacity-5 cCT-layout-I  container p-0 uCT-f-radius-sC d-flex shadow-lg mb-4'>
                {/* Image Card */}
                <div className='d-flex align-items-start align-items-md-center aCT-wire-frame-loading cCT-layout-I uCT-l-radius-sC  pt-2 py-md-4 px-2 px-md-5'>
                    <i className="cCT-layout-II uCT-f-radius-sC iCT-sG" ></i>
                </div>
                {/* Image Text */}
                <div className="pt-3 pl-3 pt-md-4 w-100 cCT-dark-text ">
                    {/* Surname */}
                    <p style={{ width: '300px' }} className='aCT-wire-frame-loading uCT-f-radius fCT-wireFrame-hG'></p>
                    {/* Day And Date */}
                    <p style={{ width: '150px' }} className=' mt-3 aCT-wire-frame-loading uCT-f-radius fCT-wireFrame-hB ml-2 mb-0 mt-2'></p>
                    <p className='py-3 uCT-f-radius fCT-wireFrame-hB'></p>
                    {/* Notification Text */}
                </div>
                {/* Delete Card Button */}
                <div className="pr-4 pt-3 p-md-4 ">
                    <button className="btn p-0 d-flex border-0 p-0~ uCT-f-radius-sC align-items-start aCT-wire-frame-loading justify-content-center cCT-layout-II">
                        <i className="aCT-wire-frame-loading iCT-sB"></i>
                    </button>
                </div>
            </div>
        )
    }
    return notificationCardWireFrameTemp()
}