export default function Tags({ spaces = '', textSpaces = '', innerText = ''}) {
    const tagsTemp = () => {
        return (
            <div className="d-inline-block">
                {/* Tags */}
                  <div className={`uCT-f-radius-sA fCT-Light-sA cCT-main-text text-uppercase text-nowrap ${spaces} uCT-click uCT-brightness`}>
                    <p className={`px-3 py-1 m-0 uCT-f-radius-sA cCT-white-bg ${textSpaces}`}>{innerText}</p>
                  </div>
            </div>
        )
    }
    return tagsTemp()
}