import './AgentCard.sass'
import Icon from '../../shared/Icon/Icon'
import { Link } from "react-router-dom";
import React, { useEffect, useState } from 'react';


export default function AgentCard({APIs = {}}){

    const [APIsBundles, setAPIsBundles] = useState({
    id: String,
    image: String,
    company_image: String,
    avg_rating: Number,
    fullname: String,
    country: String,
    buy_count: String,
    rent_count: String,
    commercial_count: String
    });
    useEffect(() => {
        switch(APIs.APIsKey){
            case "AgentList" :
                setAPIsBundles(prevVal =>({...prevVal, 
                    id : APIs?.data?.agent?.id,
                    image : APIs?.data?.agent?.image,
                    company_image : APIs?.data?.agent?.country?.image,
                    avg_rating : APIs?.data?.agent?.avg_rating,
                    fullname : APIs?.data?.agent?.fullname,
                    country : APIs?.data?.agent?.country?.title,
                    buy_count : APIs?.data?.agent?.buy_count,
                    rent_count : APIs?.data?.agent?.rent_count,
                    commercial_count : APIs?.data?.agent?.commercial_count
                }))
                break;
            case "PropertyDetails" :
                setAPIsBundles(prevVal =>({...prevVal, 
                    id : APIs?.data?.user?.id,
                    image : APIs?.data?.user?.image,
                    company_image : APIs?.data?.user?.country?.image,
                    avg_rating : APIs?.data?.user?.avg_rating,
                    fullname : APIs?.data?.user?.fullname,
                    country : APIs?.data?.user?.country?.title,
                    buy_count : APIs?.data?.user?.buy_count,
                    rent_count : APIs?.data?.user?.rent_count,
                    commercial_count : APIs?.data?.user?.commercial_count
                }))
                break;
        }
    }, [APIs]);

    const AgentCardTemp = ()=>{
        return(
            <Link to={`/agent/${APIsBundles.id}`} className='uCT-mini-card uCT-scale-in-animation uCT-click'>    
                <div className="cCT-white-bg uCT-f-radius-sC d-flex flex-column  ">
                    {/* Profile Image Section 1st */}
                    <div className="d-flex align-items-center justify-content-center pb-4">
                        {/* Agent Image */}
                        <i style={{ backgroundImage: `url(${APIsBundles.image})` }} className=' mb-2 mt-4 uCT-cardImg-holder uCT-img-contain uCT-f-radius-sB position-relative'>
                            {/* Agency Image */}
                            {APIsBundles.company_image ?
                                <i style={{ backgroundImage: `url(${APIsBundles.company_image})` }} className='uCT-bd-support1-sA CT-sub-image-holder d-inline-block uCT-img-contain uCT-f-radius-sB border-0 shadow'></i>
                            :false }
                            <div className="d-flex cCT-dark-bg shadow uCT-StarRate-Holder uCT-f-radius-smid-A uCT-StarRate-position justify-content-center align-items-center uCT-border-sB">
                                <Icon  iconName  = 'iCT-starRate' iconSize  = 'iCT-sB ml-1' textPosition = 'left' innerText = {APIsBundles.avg_rating} textSpaces= 'mt-1 cCT-white-text fCT-bold-sA' />
                            </div>
                        </i>
                    </div>
                    {/* UserName Section 2nd */}
                    <div className="text-center mb-4">
                        {/* Username */}
                        <p className='mb-0 h5 fCT-bold-sE text-capitalize'>{APIsBundles.fullname}</p>
                        {/* Role NAme , and ID , Company Name */}
                        <p className='my-2 cCT-gray-text fCT-Light-sC text-capitalize'>broker</p>
                        {/* Location Section */}
                        <p className='mb-0 cCT-gray-text fCT-Regular-sB'>{APIsBundles.country}</p>
                    </div>
                    {/* BTN Section 3rd */}
                    <div className="d-flex  justify-content-center">
                        {/* Message Icon */}
                        <button className='btn cCT-Gradian-Linear-Main cCT-main-tx d-flex uCT-f-radius-sA align-items-center mr-3 px-4'>
                            <Icon  iconName='iCT-messagewhite' iconSize='iCT-sB mr-3' textPosition='right' innerText='Message' textSpaces='fCT-Regular-sC cCT-support2-text' />
                        </button>
                        {/* Call Icon */}
                        <button className='btn d-flex uCT-f-radius-sA align-items-center uCT-icon-holder cCT-opacity-green-bg-20'>
                            <Icon  iconName='iCT-callgreen' iconSize='iCT-sB' textPosition='right' />
                        </button>
                    </div>
                    {/* Ameneties Section 4th */}
                    <div className="d-flex justify-content-center align-items-center h-50 mt-4 py-3 uCT-b-radius-sC w-100 cCT-support2-bg fCT-Regular-sA">
                        <div className="d-flex flex-column justify-content-start">
                            <p className='mb-0'>{APIsBundles.buy_count}</p>
                            <p className='mb-0 cCT-gray-text'>Buy</p>
                        </div>
                            <div className='mb-0 uCT-Horizontal-Line mx-4'></div>
                        <div className="d-flex flex-column">
                            <p className='mb-0'>{APIsBundles.rent_count}</p>
                            <p className='mb-0 cCT-gray-text'>Rent</p>
                        </div>
                            <div className='mb-0 uCT-Horizontal-Line mx-4'></div>
                        <div className="d-flex flex-column">
                            <p className='mb-0'>{APIsBundles.commercial_count}</p>
                            <p className='mb-0 cCT-gray-text'>Commercial</p>
                        </div>
                    </div>
                </div>
            </Link>
        )
    }
    return AgentCardTemp()
}