import './agency-profile-card.sass'
import Icon from 'view/features/shared/Icon/Icon'
import React, { useState, useEffect } from 'react';
export default function AgencyProfileCard({contactSection = '' , statusSection = '', btnSection = '' , holderSpace='', APIs= {}}){
    const [APIsBundles, setAPIsBundles] = useState({
        image: String,
        avg_review: Number,
        fullname: String,
        country: String
    });
    useEffect(() => {
        switch(APIs.from){
            case "AgencyProfile" :
                setAPIsBundles(prevVal =>({...prevVal, 
                    image : APIs?.data?.profileCard?.image,
                    fullname : APIs?.data?.profileCard?.fullname,
                    country : APIs?.data?.profileCard?.country?.title,
                    avg_review : APIs?.data?.profileCard?.avg_review,
                }))
                break;
        }
    }, [APIs]);
    const AgencyProfileCardTemp = ()=>{
        return(
            <div className={`d-flex justify-content-center CT-agency-profile-card-holder ${holderSpace} uCT-f-radius-sC`}>
                {/* {console.log(APIs)} */}
                <div className='CT-agency-profile-card-content cCT-white-bg CT-agency-card-shadow uCT-f-radius-sC shadow'>
                    {/* image */}
                    <div className='d-flex justify-content-center align-items-center position-relative pt-4 mb-5'>
                        <i className='iCT-x-agancyLogo uCT-f-radius-sB CT-profile-img cCT-smoke-bg uCT-img-cover' style={{ backgroundImage: `url(${APIsBundles.image})`}}></i>
                        <div className='CT-agency-rate-holder cCT-white-text fCT-Regular-sB' >
                            <Icon iconName='iCT-rategold' spaces='px-4 py-2 fCT-bold-sB' bgColor='cCT-dark-bg' radiusSize='uCT-f-radius-sA' iconSize='iCT-sB ml-1' textPosition='left' innerText={APIsBundles.vg_review}/>
                        </div>
                    </div>
                    {/* name */}
                    <div className='pb-4'>
                        <p className='fCT-bold-sE text-center m-0'>{APIsBundles.fullname}</p>
                        <p className='fCT-Regular-sC text-center pt-2 m-0 cCT-gray-text'>{APIsBundles.country}</p>
                    </div>
                    {/* contacts */}
                    { contactSection == 'contact' ?
                    <div className='d-flex justify-content-around px-4 pb-4'>
                        <div className='d-flex flex-column align-items-center'>
                            <Icon iconName='iCT-callgreen' spaces='iCT-sE mb-2' bgColor='cCT-support1-bg' radiusSize='uCT-f-radius-sA' iconSize='iCT-sB' />
                            <p className='mb-0 fCT-Regular-sA'>1 Agent</p>
                        </div>
                        <div className='d-flex flex-column align-items-center'>
                            <Icon iconName='iCT-callgreen' spaces='iCT-sE mb-2' bgColor='cCT-support1-bg' radiusSize='uCT-f-radius-sA' iconSize='iCT-sB' />
                            <p className='mb-0 fCT-Regular-sA'>20 Properties</p>
                        </div>
                        <div className='d-flex flex-column align-items-center'>
                            <Icon iconName='iCT-callgreen' spaces='iCT-sE mb-2' bgColor='cCT-opacity-green-bg-20' radiusSize='uCT-f-radius-sA' iconSize='iCT-sB' />
                            <p className='mb-0 fCT-Regular-sA cCT-green-text'>OPEN</p>
                        </div>
                    </div> : false
                    }
                    {/* BTN Section */}
                    { btnSection == 'btn' ?
                    <div className='d-flex justify-content-around px-4 pb-4'>
                        <Icon iconName='iCT-callgreen' spaces='px-5 py-2  mr-3' bgColor='cCT-support1-bg' radiusSize='uCT-f-radius-sA' iconSize='iCT-sB' titleRight='Message'/>
                        <Icon iconName='iCT-callgreen' spaces='iCT-sE mr-3' bgColor='cCT-opacity-main-bg-20' radiusSize='uCT-f-radius-sA' iconSize='iCT-sB' />
                    </div> : false
                    }
                    {/* status */}
                    { statusSection == 'status' ?
                    <div className="d-flex align-items-center justify-content-center px-4 py-2 cCT-support2-bg w-100 uCT-b-radius-sC">
                        <div className="d-flex flex-column justify-content-start">
                            <p className='mb-0 h6 fCT-Regular-sB'>165</p>
                            <p className='mb-0 cCT-secondary-tx fCT-Regular-sB'>Buy</p>
                        </div>
                        <div className='mb-0 CT-Horizontal-Line cCT-white-bg mx-4'></div>
                        <div className="d-flex flex-column">
                            <p className='mb-0 fCT-Regular-sB'>320</p>
                            <p className='mb-0 cCT-secondary-tx fCT-Regular-sB'>Rent</p>
                        </div>
                        <div className='mb-0 CT-Horizontal-Line cCT-white-bg mx-4'></div>
                        <div className="d-flex flex-column">
                            <p className='mb-0 fCT-Regular-sB'>750</p>
                            <p className='mb-0 cCT-secondary-tx fCT-Regular-sB'>Commercial</p>
                        </div>
                    </div> : false
                    }
                </div>
            </div>
        )
    }
    return AgencyProfileCardTemp()
}