export default function PropertyDescriptionWireFrame(){
    const propertyDescriptionWireFrameTemp =()=>{
        return(
            <div className="uCT-wire-frame-opacity-5">
                <div className="m-0 w-100 pl-2 pl-md-5">
                    <p style={{ width: '300px' }} className='fCT-wireFrame-hB uCT-f-radius aCT-wire-frame-loading'></p>
                </div>
            </div>
        )
    }
    return propertyDescriptionWireFrameTemp()
}