// import { MediaCT } from 'view/viewLogic/view-logic'
import { useMediaQuery } from 'react-responsive';
export default function PropertyGalleryWireFrame(){
    const mediaLG = useMediaQuery({ query: `(max-width: 992px)` })
    const propertyGalleryWireFrameTemp = () => {
        return(
            <div className="uCT-f-radius-sC uCT-wire-frame-opacity-5">
                {/* Video 360 Section small screen */}
                <div className={`d-flex m-0 CT-galleryHolder d-lg-none`}>
                    <div className={`m-0 shadow-sm ${mediaLG ? 'w-100 aCT-wire-frame-loading-cover cCT-layout-I uCT-f-radius-smid-A mt-4' : 'd-none'}`}></div>
                </div>
                {/* Property Gallery Holder */}
                <div className={`${mediaLG ? 'm-0 mt-3 CT-list-gallery-holder uCT-f-radius-smid-A' : 'CT-gallery-holder'}`}>
                    {/* Main Video 360 Section */}
                    <div className={`shadow-sm ${mediaLG ? 'w-0 d-none' : 'aCT-wire-frame-loading-cover cCT-layout-I CT-virtual-360 uCT-l-radius-sC'}`} ></div>
                    {/* Main Images Section */}
                    <div className={`w-100~ uCT-f-radius-smid-A ${mediaLG ? 'CT-gallery-grid-sm-holder overflow-auto d-flex uCT-f-radius-smid-A' : 'CT-prop-gallery-grid  position-relative'}`}>
                        {/* Simi Main Images */}
                        <div className={`uCT-img-cover shadow-sm ${mediaLG ? 'mr-3 aCT-wire-frame-loading cCT-layout-I CT-gallery-grid-sm-holder uCT-f-radius-smid-A' : ' aCT-wire-frame-loading-cover cCT-layout-I prop-mini-card'}`}></div>
                        <div className={`uCT-img-cover shadow-sm ${mediaLG ? 'mr-3 aCT-wire-frame-loading cCT-layout-I CT-gallery-grid-sm-holder uCT-f-radius-smid-A' : '  aCT-wire-frame-loading-cover cCT-layout-I prop-mini-card-2 uCT-top-r-radius-sC'}`}></div>
                        {/* Small Main Images */}
                        <div className={`uCT-img-cover shadow-sm mr-3 mr-lg-0 CT-gallery-grid-sm-holder ${mediaLG ? 'aCT-wire-frame-loading cCT-layout-I uCT-f-radius-smid-A' : 'aCT-wire-frame-loading-cover cCT-layout-I '}`}></div>
                        <div className={`uCT-img-cover shadow-sm mr-3 mr-lg-0 CT-gallery-grid-sm-holder ${mediaLG ? 'aCT-wire-frame-loading cCT-layout-I uCT-f-radius-smid-A' : 'aCT-wire-frame-loading-cover cCT-layout-I  '}`}></div>
                        <div className={`uCT-img-cover shadow-sm mr-3 mr-lg-0 CT-gallery-grid-sm-holder ${mediaLG ? 'aCT-wire-frame-loading cCT-layout-I uCT-f-radius-smid-A' : 'aCT-wire-frame-loading-cover cCT-layout-I '}`}></div>
                        <div className={`uCT-img-cover shadow-sm mr-3 mr-lg-0 CT-gallery-grid-sm-holder ${mediaLG ? 'aCT-wire-frame-loading cCT-layout-I uCT-f-radius-smid-A' : 'aCT-wire-frame-loading-cover cCT-layout-I uCT-bottom-r-radius-sC '}`}></div>
                            
                        {/* Show More Button */}
                        <button className={`py-2 px-4 text-uppercase fCT-Regular-sA cCT-white-text uCT-f-radius-smid-A  ${mediaLG ? ' btn CT-list-gallery-grid-button aCT-wire-frame-loading cCT-layout-I' : 'btn CT-btnPosition aCT-wire-frame-loading cCT-layout-I'} `} >
                                <p style={{width: '107px', height: '18px'}} className="m-0 text-center "></p>
                            </button> 
                      
                    </div>
                </div>
            </div>
        )
    }
    return propertyGalleryWireFrameTemp()
}