import styles from './head-section.module.scss'
export default function HeadSectionFeature (){
    const HeadSectionTemp = ()=>{
        return(
            <div className={`${styles['CT-grid-container']} p-0 m-0`}>
                {/* image */}
                <section className={`${styles['CT-image']}`}>
                    <div style={{ width: '1138.11px', height: '1030.74px' }} className='imCT-services-head-section-BG1 uCT-img-cover'></div>
                    <div></div>
                    <div></div>
                    <div></div>
                </section>
                {/* Text */}
                <section className={`${styles['CT-text']}`}>
                    text
                </section>
            </div>
        )
    }
    return HeadSectionTemp()
}