import './advanced-search-holder.sass'
import FiltersFeature from 'view/features/AdvancedSearch/Filters/Filters.feature'
import FilterResultsFeature from 'view/features/AdvancedSearch/FilterResults/FilterResults.feature'
import AdvancedSearchStyle1Feature from 'view/features/AdvancedSearch/AdvancedSearchStyle1/AdvancedSearchStyle1.feature'
import { useEffect, useState } from 'react'
import { _GET_AllCountry, _GET_AllCategory, _GET_AllType, _GET_AllCityOfCountry, _GET_AllAmenities, _GET_Price_Area } from 'Controllers/services/cores/coreData.services'
export default function AdvancedSearchHolderFeature({advancedSearch}) {
    const [runTimeResults, setRunTimeResults] = useState(null) 
    //{city, type_name, amenities[]}
    useEffect(()=>{
        console.log(runTimeResults)
    }, [runTimeResults])
    const [APIsKey, setAPIsKey] = useState('mainAppAdvancedSearch')
    // Core Data State
    const [coreData, setCoreData] = useState({
        cities: [],
        category: [],
        type: [],
        amenity: [],
        price: [],
        size: []
    })
    // Get CORES APIs ---
    useEffect(() => {
        // City
        _GET_AllCityOfCountry('en', 1).then(res => {
            setCoreData(prevState => ({ ...prevState, cities: res?.data?.data }))
        }).catch()
        // Category
        _GET_AllCategory('en').then(res => {
            setCoreData(prevState => ({ ...prevState, category: res?.data?.data }))
        }).catch()
        // Type
        _GET_AllType('en').then(res => {
            setCoreData(prevState => ({ ...prevState, type: res?.data?.data }))
        }).catch()
        // Amenities
        _GET_AllAmenities('en').then(res => {
            setCoreData(prevState => ({ ...prevState, amenity: res?.data?.data }))
        }).catch()
        // Price
        _GET_Price_Area().then(res=>{
            setCoreData(prevState => ({ ...prevState, price: res?.data?.data?.price }))
            setCoreData(prevState => ({ ...prevState, size: res?.data?.data?.size }))
        }).catch()
    }, [])

    const advancedSearchTemp = () => {
        return (
            <div>
                {/* filter section  */}
                {/* <FiltersFeature APIs={{ APIsKey, data: { coreData } }} toggleOpenClose={advancedSearch} filterValues={runTimeResults} runTimeResults={setRunTimeResults}/> */}
                <AdvancedSearchStyle1Feature />
                {/*  Secound  Bar Section  {Search Results} ( 3 ) */}
                {runTimeResults ?
                    <FilterResultsFeature runTimeResults={runTimeResults} />
                : 
                    <div className='w-100 p-5 my-4 text-center cCT-white-text h5 cCT-Gradian-Linear-mid-Main shadow uCT-f-radius-sB'> Search Results Will Appear here... </div>}
                    <div style={{height: '104px'}}></div>
            </div>

        )
    }
    return advancedSearchTemp()
}