import React from 'react'

export default React.memo(function Icon({iconName  = '', spaces  = '', bgColor = '', iconSize  = '', radiusSize  = '', textSpaces = '', textPosition='', innerText, iconAPI='' }){
  const iconTemp = ()=>{
        return (
              <div>
                {/* titleRight */}
                { textPosition == 'right' ?
                  <div className={`${radiusSize} ${bgColor} d-flex align-items-center ${spaces} uCT-click uCT-brightness`}>
                    <i className={`uCT-img-100 ${!iconAPI ? iconName : false} ${iconSize}`} style={{ backgroundImage: iconAPI ? `url('${iconAPI}')` : false}} ></i>
                    <p className={`mb-0 w-100~ ${textSpaces}`} style={{textTransform: 'capitalize'}}>{innerText}</p>
                  </div>
                  : false
                }
                {/* titleBottom */}
                { textPosition == 'bottom' ?
                  <div className={`d-flex flex-column align-items-center justify-content-center ${radiusSize} ${bgColor} ${spaces} uCT-click uCT-brightness`}>
                      <i className={`uCT-img-100 ${iconName} ${iconSize}`} style={{ backgroundImage: iconAPI !== '' ? `url('${iconAPI}')` : false}}></i>
                      <p className={`mb-0 ${textSpaces}`} style={{textTransform: 'capitalize'}}>{innerText}</p>
                  </div>
                  : false
                }
                {/* titleLeft */}
                { textPosition == 'left' ?
                  <div className={`d-flex ${radiusSize} ${bgColor} ${spaces} uCT-click uCT-brightness align-items-center justify-content-center`}>
                      <p className={`mb-0 ${textSpaces}`}>{innerText}</p>
                <i className={`uCT-img-100 ${iconName} ${iconSize}`} style={{ textTransform: 'capitalize', backgroundImage: iconAPI !== '' ? `url('${iconAPI}')` : false}}></i>
                  </div>
                  : false
                }
                {/* withOut Title */}
                { textPosition == '' ?
              <div className={`d-flex ${radiusSize} ${bgColor} ${spaces} align-items-center justify-content-center uCT-click uCT-brightness~`}>
                <i className={`uCT-img-100 ${iconName} ${iconSize}`} style={{ textTransform: 'capitalize',  backgroundImage: iconAPI !== '' ? `url('${iconAPI}')` : false}}></i>
                  </div>
                  : false
                }
              </div>
        )
    }
    return iconTemp()
})
