import './article-thumbnail.sass'
export default function ArticleThumbnail(){
    const articleThumbnailTemp = ()=>{
        return (
            <div className='w-100 CT-blog-art-thumbnail-holder'>
                {/* Blog thumbnail */}
                <div className='d-flex justify-content-center w-100 h-100 uCT-f-radius-sC overflow-hidden shadow'>
                    <i className='iCT-x-blogThumbnail w-100 h-100 uCT-img-cover'></i>
                </div>
            </div>
        )
    }
    return articleThumbnailTemp()
}