import React, { useState, useEffect, useRef } from 'react'
import Pagination from '@mui/material/Pagination';
import Stack from '@mui/material/Stack';

export default React.memo(function PaginationFeature({APIs = new Object(), currentPage}){
    const [APIsBundles, setAPIsBundles] = useState({
        current_page: Number,
        total_pages: 1,
    });
    console.log(APIs);

     useEffect(() => {
        switch(APIs.APIsKey){
            case "MapView" :
                setAPIsBundles(prevVal =>({...prevVal, 
                    current_page : APIs?.data?.pagination?.current_page,
                    total_pages : APIs?.data?.pagination?.total_pages,
                }))
                break;
            }
    }, [APIs]);

    const getPageNumber = (e, value) => {
        currentPage(value)
        window.scrollTo({
                top: 0,
                behavior: 'smooth'
            })
    }

    const PaginationTemp = _ => {
        return(
            <Stack>
                <Pagination count={APIsBundles?.total_pages} onChange={getPageNumber} size="large"/>
            </Stack>
        )
    }
    return PaginationTemp()
})
