import AgencieCard from 'view/features/agency/Agencies-Card/AgenciesCard'
import AgenciesWireFrame from 'view/features/agency/Agencies-Card/Agencies-WireFrame/Agencies-WireFrame'
import PagesTitle from 'view/features/shared/pagesTitle/PagesTitle'
import PageTitleWireframe from 'view/features/shared/pagesTitle/PageTitle-wireFrame/PageTitle.wireframe'
import PageHead from 'view/layout/Components/PageHead/PageHead'
import PageHeadWireFrame from 'view/layout/Components/PageHead/PageHead-wireFrame/PageHead.wireFrame'
import React, { useEffect, useState } from 'react';
import { _GET_AllAgencies } from "Controllers/services/agency/agency.services";
export default function AgenciesList() {
    const [allAgenciesList, setAllAgenciesList] = useState([])
    useEffect(() => {
        _GET_AllAgencies()
            .then(res => {
                setAllAgenciesList(res)
            }).catch()
    }, [])
    const agenciesListTemp = () => {

        return (

            <div className='container-fluid p-0' >
                {/* page head */}
                <header>
                    {allAgenciesList?.data?.data?.company ?
                        <PageHead title="Our Great Agencies" subTitle="A great real estate agent don't sells, he helps." pImg="imCT-agenciesListImg" />
                        :
                        <PageHeadWireFrame />
                    }
                </header>
                {/* page Body */}
                <div className="container p-md-4 p-3 ">
                    {allAgenciesList?.data?.data?.company ?
                        <PagesTitle title="Agencies-List" smallUnderTitle={`${allAgenciesList?.data?.data?.company.length} Agency`} /> :
                        <PageTitleWireframe />
                    }
                    <div className="uCT-card-grid-holder w-100 mb-4">
                        {/* <AgenciesWireFrame />
                        <AgenciesWireFrame />
                        <AgenciesWireFrame />
                        <AgenciesWireFrame /> */}
                    </div>
                    <section className='uCT-card-grid-holder w-100 mb-4'>
                        {/* agencies cards */}
                        {allAgenciesList?.data?.data?.company ?
                            allAgenciesList?.data?.data?.company.map((agency) => {
                                return <AgencieCard APIs={{ from: 'AgenciesList', data: { agency } }} key={agency.id} />
                            }) :
                            Array(25).fill().map((elem, index) => {
                                return <AgenciesWireFrame key={index} />

                            })
                        }
                        {/* dummy Cards */}
                        <div className='uCT-mini-dummy'></div>
                        <div className='uCT-mini-dummy'></div>
                        <div className='uCT-mini-dummy'></div>
                        <div className='uCT-mini-dummy'></div>
                    </section>
                </div>
            </div>
        )
    }
    return agenciesListTemp()
}