import PageTitleWireframe from 'view/features/shared/pagesTitle/PageTitle-wireFrame/PageTitle.wireframe'
import PagesTitle from 'view/features/shared/pagesTitle/PagesTitle'
import PropertyCardWireFrame from 'view/features/property/Property-Grid-Card/PropertyCard-wireframe/PropertyCard.wireframe'
import { useEffect, useState } from 'react'
import {_GET_AllProperties} from 'Controllers/services/property/property.services'
import PropertyGridCard from 'view/features/property/Property-Grid-Card/PropertyGridCard'
import ContentSlider from 'view/layout/Components/sliders/ContentSlider/ContentSlider'
import ButtonRightIcon from 'view/features/shared/buttons/Button-Right-Icon/ButtonRightIcon'
export default function FilterResultsFeature({runTimeResults}){
    const [filterResults, setFilterResults] =  useState({
        properties : []
    })
    const [APIsKey, setAPIsKey] = useState('PropertiesList')
    // Get APIS
    useEffect(()=>{
        if (runTimeResults){
            // reset properties array Value
            setFilterResults(prevVal => ({ ...prevVal, properties: [] }))
            // Filters Value
            let filterVal = {
                city_id: runTimeResults?.city?.city_id,
                type_id: runTimeResults?.type_id,
                category:  runTimeResults?.category,
                amenity_IDs: runTimeResults?.amenities,
                bedroom: runTimeResults?.bedrooms,
                bathroom: runTimeResults?.bathrooms,
                min_price: runTimeResults?.min_price,
                max_price: runTimeResults?.max_price,
                property_name: runTimeResults?.property_name
            }
            
            // Get Properties APIs
            _GET_AllProperties('en', 8, filterVal)
            .then(res =>{
                setFilterResults(prevVal => ({ ...prevVal, properties: res}))
            }).catch()
        }
    }, [runTimeResults])

    const ResultTitleContent = ()=>{
        return(
            <>
                {runTimeResults?.city?.city_title ? <div>Search Result Of <span className='cCT-main-text'>{runTimeResults?.city?.city_title}</span></div> : 'Search Results...'}
            </>
        )
    }
    const filterResultsTemp = ()=>{
        return(
           
            // title
            <section>
                <div className='px-md-0 px-3 w-100'>
                    {filterResults?.properties?.data?.data ? <PagesTitle title={<ResultTitleContent/>} smallUnderTitle={`${filterResults?.properties?.data?.data?.paginate?.total} Properties`} /> : <PageTitleWireframe />}
                </div>
                {/* Properties Cards */}
                <div>
                    {filterResults?.properties?.data?.data?.properties ?
                        filterResults?.properties?.data?.data?.properties !='' ?
                            <>
                                <ContentSlider content={
                                        filterResults?.properties?.data?.data?.properties?.map((property) => {
                                            return <PropertyGridCard key={property?.id} APIs={{ APIsKey, data: { property } }} />
                                        })
                                } />
                                <ButtonRightIcon innerText='See More Properties' btnICON='iCT-ArrowRight-White' btnCOLOR='cCT-Gradian-Linear-Main' iconBgCOLOR='cCT-inherit-bg' position='right' />
                            </>  
                        :false
                    :
                        <ContentSlider content={
                            Array(8).fill().map((w, i) => {
                                return <PropertyCardWireFrame key={i} />
                            })
                        } />
                    }
                </div>
            </section>
        )
    }
    return filterResultsTemp()
}