import './blog-list.sass'
import PageHead from 'view/layout/Components/PageHead/PageHead'
import PageHeadWireFrame from 'view/layout/Components/PageHead/PageHead-wireFrame/PageHead.wireFrame'
import BlogNav from 'view/features/blog/Blog-Nav/Blog-Nav'
import RightWidget from 'view/layout/Components/widgets/Right-Widget/Right-Widget'
import BlogGridCard from 'view/features/blog/Blog-Grid-Card/Blog-Grid-Card'
import BlogGridCardWireFrame from 'view/features/blog/Blog-Grid-Card/BlogGridCard-WireFrame/BlogGridCard.WireFrame'
import { giveClassOnScroll, SideNavScreenHeight, MediaCT } from 'view/viewLogic/view-logic'
import React, { useState, useEffect } from 'react'
import AdvSmCard from 'view/features/advertisement/Adv-SM-Card/Adv-SM-Card'
import PagesTitle from 'view/features/shared/pagesTitle/PagesTitle'
import PopularPosts from 'view/features/blog/Popular-Posts/Popular-Posts'
export default function BlogList() {
    function onScroll() {
        giveClassOnScroll('blogNavHolder', 'cCT-Gradian-Linear-supp2 px-4 shadow', '', 80)
    }
    function onResize() {
        SideNavScreenHeight('rightWidget', 'giveMeSpace')
    }
    // USE_Effect call after render 
    useEffect(() => {
        window.addEventListener('scroll', onScroll);

        window.addEventListener('resize', onResize);

        SideNavScreenHeight('rightWidget', 'giveMeSpace')
    }, []);
    // Component OUT!
    useEffect(() => () => {
        window.removeEventListener('scroll', onScroll);
        window.removeEventListener('resize', onResize);
    }, [])
    // useEffect(() => () => console.log("unmount"), []);
    const rightWidgetContent = () => {
        return (
            <section className='w-100~'>
                <div className='CT-widget-parent-space'></div>
                <div className='mb-4'></div>
                <AdvSmCard />
                <PagesTitle title='Popular Posts' />
                <PopularPosts />
                <PopularPosts />
                <PopularPosts />
            </section>
        )
    }
    const blogListTemp = () => {
        return (
            <div className='container-fluid p-0'>
                {/* page head */}
                <header>
                    <PageHeadWireFrame />
                    <PageHead title="FIND THE LAST NEWS & TRENDS" subTitle="Home is One Click Away ." pImg="BlogArticle-Img" />
                </header>
                {/* page body */}
                <main className='container px-md-4 px-3'>
                    <div className='row m-0 flex-nowrap~ flex-md-row flex-column'>
                        {/* page content */}
                        <div className='uCT-content-with-widget'>
                            {/* blog nav */}
                            <nav id='blogNavHolder' className=' py-4 uCT-b-radius-sB cCT-smoke-bg CT-blog-nav'>
                                <BlogNav />
                            </nav>
                            {/* blog articles */}
                            <section className='d-flex~ flex-wrap~ w-100 uCT-card-grid-holder  justify-content-center~ mb-4'>
                                <BlogGridCardWireFrame />
                                <BlogGridCard />
                                <BlogGridCard />
                                <BlogGridCard />
                                <BlogGridCard />
                                <BlogGridCard />
                                <BlogGridCard />
                                <BlogGridCard />
                                {/* dummy Cards */}
                                <div className='uCT-mini-dummy'></div>
                                <div className='uCT-mini-dummy'></div>
                            </section>
                        </div>
                        {/* right widget */}
                        {MediaCT('lg') ? false :
                            <div id='rightWidget' className=''>
                                <RightWidget content={rightWidgetContent()} />
                            </div>
                        }
                    </div>
                </main>
            </div>
        )
    }
    return blogListTemp()
}