import Icon from '../../../shared/Icon/Icon'
import React, { useState, useEffect } from 'react';
import './PropertyLocation.sass'
import { MediaCT } from 'view/viewLogic/view-logic'
export default function PropertyLocation({APIs=[]}) {
    const [APIsBundles, setAPIsBundles] = useState({
          id: String,
          size: String,
          bedroom: String,
          price: String,
          room: String,
          area: String,
          available_from: String,
          lot_size: String,
          bathroom: String,

      });
      useEffect(() => {
          switch(APIs.APIsKey){
              case "PropertyDetails" :
                  setAPIsBundles(prevVal =>({...prevVal, 
                      id : APIs?.data?.propertyLocation?.id,
                      size : APIs?.data?.propertyLocation?.size,
                      bedroom : APIs?.data?.propertyLocation?.bedroom,
                      price : APIs?.data?.propertyLocation?.price,
                      room : APIs?.data?.propertyLocation?.room,
                      area : APIs?.data?.propertyLocation?.area,
                      available_from : APIs?.data?.propertyLocation?.available_from,
                      lot_size : APIs?.data?.propertyLocation?.lot_size,
                      bathroom : APIs?.data?.propertyLocation?.bathroom
                  }))
                  break;
          }
      }, [APIs]);
    const propertyLocationTemp = () => {
        return (
            <div className="">
                <div className="position-relative">
                    {/* Search Bar Holder */}
                    {/* <div className="d-flex align-items-center uCT-f-radius-sC shadow-sm CT-SearchBar-Holder justify-content-center CT-Search-Holder w-100"> */}
                    {/* Search Icon Holder */}
                    {/* <div className="d-flex  cCT-support1-bg uCT-l-radius-sB align-items-center h-100 pr-2 pl-4">
                            <i className="iCT-Search-Blue iCT-sB uCT-img-cover"></i>
                        </div> */}
                    {/* Search Bar */}
                    {/* <input className="cCT-Gradian-Linear-supp border-0 h-100 uCT-r-radius-sB w-100" placeholder='Search With a Keyword...' type="text" /> */}
                    {/* </div> */}
                    {/* Background Image */}
                    <iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d13822.243033156674!2d31.27408075!3d29.9920509!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x1458390d7a401b37%3A0xd9bddbc1cd334294!2z2YXYr9mK2YbYqSDYp9mE2LLZh9mI2LEg2YTYuNio2KfYtyDYp9mE2YLZiNin2Kog2KfZhNmF2LPZhNit2Kk!5e0!3m2!1sen!2seg!4v1622467351688!5m2!1sen!2seg" allowFullScreen="" loading="lazy" className={`d-flex border-0 uCT-f-radius-sC ${MediaCT('lg') ? 'CT-propertyMap-sm-holder' : 'CT-propertyMap-lg-holder'}`}>
                    </iframe>
                    {/* Side Controller Holder */}
                    {/*  <div className='d-flex CT-controllerHolder'>
                            <div className='d-flex flex-column'>
                            <Icon iconName="iCT-searchPlusWhite"bgColor="cCT-main-bg" iconSize="iCT-sC" spaces="mb-3 iCT-sE" radiusSize='uCT-f-radius-sA'/>
                            <Icon iconName="iCT-searchMinusWhite"bgColor="cCT-main-bg" iconSize="iCT-sC" spaces="mb-3 iCT-sE" radiusSize='uCT-f-radius-sA'/>
                            <Icon iconName="iCT-sendWhite"bgColor="cCT-main-bg" iconSize="iCT-sC" spaces="mb-3 iCT-sE" radiusSize='uCT-f-radius-sA'/>
                            <Icon iconName="iCT-carWhite"bgColor="cCT-main-bg" iconSize="iCT-sC" spaces="mb-3 iCT-sE" radiusSize='uCT-f-radius-sA'/>
                            <Icon iconName="iCT-cartWhite"bgColor="cCT-main-bg" iconSize="iCT-sC" spaces="mb-3 iCT-sE" radiusSize='uCT-f-radius-sA'/>
                            <Icon iconName="iCT-bookWhite"bgColor="cCT-main-bg" iconSize="iCT-sC" spaces="mb-3 iCT-sE" radiusSize='uCT-f-radius-sA'/>
                            <Icon iconName="iCT-restaurantWhite"bgColor="cCT-main-bg" iconSize="iCT-sC" spaces="mb-3 iCT-sE" radiusSize='uCT-f-radius-sA'/>
                            <Icon iconName="iCT-pillWhite"bgColor="cCT-main-bg" iconSize="iCT-sC" spaces="mb-3 iCT-sE" radiusSize='uCT-f-radius-sA'/>
                            <Icon iconName="iCT-hospitalWhite"bgColor="cCT-main-bg" iconSize="iCT-sC" spaces="mb-3 iCT-sE" radiusSize='uCT-f-radius-sA'/>
                            </div>
                        </div> */}
                </div>
                {/* Property Location Icons Row*/}
                <div className="col pr-0 pl-2 pl-lg-4 pt-4 cCT-gray-text text-nowrap fCT-Regular-sB text-capitalize">
                    {/* Location Icons Row One*/}
                    <div className="d-flex pl-lg-4">
                        <Icon iconName='iCT-citygray' iconSize='iCT-sB mr-3 p-2' textPosition='right' spaces='pb-4' textSpaces='w-100' innerText='city :' />
                        <Icon textPosition='left' spaces='pb-4' textSpaces='w-100 pl-1' innerText={APIs[0]?.city?.title} />
                    </div>
                    {/* Location Icons Row Two*/}
                    <div className="d-flex pl-lg-4">
                        <Icon iconName='iCT-country-gray' iconSize='iCT-sB mr-3 p-2' textPosition='right' spaces='pb-4' textSpaces='w-100' innerText='country :' />
                        <Icon textPosition='left' spaces='pb-4' textSpaces='w-100 pl-1' innerText={APIs[0]?.country?.title} />
                    </div>
                    {/* Location Icons Row Three*/}
                    <div className="d-flex pl-lg-4">
                        <Icon iconName='iCT-address-gray' iconSize='iCT-sB mr-3 p-2' textPosition='right' spaces='pb-lg-4' textSpaces='w-100' innerText='address :' />
                        <Icon textPosition='left' spaces='pb-lg-4' textSpaces='w-100 pl-1' innerText={APIs[0]?.country?.address} />
                    </div>
                </div>
            </div>
        )
    }
    return propertyLocationTemp()
}