import { MediaCT } from 'view/viewLogic/view-logic'

export default function Browse360VR(){
    const Browse360VRTemp = ()=>{
        return(
          <div className={`${MediaCT('md') ? 'pt-3 mt-3' : 'm-0 pt-5~'} px-3 CT-holderBrowse360VR~`}>
            <div className='col-6 p-0'>
              <p className={`${MediaCT('md') ? 'mb-3 fCT-bold-sE' : 'pb-3 mb-3 fCT-bold-54'}`}>Browse 360VR Units</p>
              <p className={`${MediaCT('md') ? 'mb-3 fCT-bold-sB pl-1' : 'mb-4 pl-4 fCT-Regular-sE'}`}>Find your dream house in one of our listed categories.</p>
            </div>
            <div className={`${!MediaCT('md') ? 'd-flex CT-aspect-ratio-holder' : 'row flex-nowrap overflow-auto m-0'}`}>
              {/* Section One */}
              <div className={`${!MediaCT('md') ? 'd-flex flex-column w-100' : 'row m-0 flex-nowrap '}`}>
                {/* First Album */}
                <div className={`${!MediaCT('md') ? ' w-100 h-100' : 'CT-browse360VR-Card-sm'} mb-4 mr-4 shadow-lg uCT-f-radius-sA d-flex`}>
                  <i className='d-flex align-items-end w-100 uCT-f-radius-sA imCT-homeCoverPageImg uCT-img-cover'>
                    <div style={{ fontStyle: 'normal'}} className='d-flex justify-content-between uCT-b-radius-sA px-4 align-items-center cCT-white-text cCT-main-bg uCT-opacity-95 CT-browse360VR-holderText w-100'>
                      <span>Apartment</span>
                      <span>68 Listing</span>
                    </div>
                  </i>
                </div>
                {/* Second Album */}
                <div className={`${!MediaCT('md') ? ' w-100 h-100' : 'CT-browse360VR-Card-sm'} shadow-lg uCT-f-radius-sA `}>
                  <i className='d-flex align-items-end uCT-f-radius-sA imCT-homeCoverPageImg w-100 h-100 uCT-img-cover'>
                    <div style={{ fontStyle: 'normal' }} className='d-flex justify-content-between uCT-b-radius-sA px-4 align-items-center cCT-white-text cCT-secondary-bg uCT-opacity-95 CT-browse360VR-holderText w-100'>
                      <span>Offices</span>
                      <span>68 Listing</span>
                    </div>
                  </i>
                </div>
              </div>
              {/* Section Two */}
              <div className={`${!MediaCT('md') ? 'd-flex flex-column w-100 ml-4' : 'CT-browse360VR-Card-sm ml-4'}  shadow-lg uCT-f-radius-sA`}>
                  <i className={`d-flex align-items-end imCT-homeCoverPageImg uCT-f-radius-sA ${!MediaCT('md') ? 'CT-browse360VR-fullCard' : 'CT-browse360VR-Card-sm'} uCT-img-cover`}>
                    <div style={{ fontStyle: 'normal'}} className='d-flex justify-content-between uCT-b-radius-sA px-4 align-items-center cCT-white-text cCT-secondary-bg uCT-opacity-95 CT-browse360VR-holderText w-100'>
                      <span>Villas</span>
                      <span>68 Listing</span>
                    </div>
                  </i>
              </div>
            </div>
          </div>
        )
    }
    return Browse360VRTemp()
}