import Icon from '../../shared/Icon/Icon'
import React, { useState, useEffect } from 'react';

export default function Overview({APIs = {}}){

    const [APIsBundles, setAPIsBundles] = useState({
        count_agents: Number,
        count_properties: Number,
        buy_count: Number,
        rent_count: Number,
        commercial_count: Number
    });
    useEffect(() => {
        switch(APIs.APIsKey){
            case "AgencyProfile" :
                setAPIsBundles(prevVal =>({...prevVal, 
                    count_agents : APIs?.data?.count_agents,
                    count_properties : APIs?.data?.count_properties,
                    buy_count : APIs?.data?.buy_count,
                    rent_count : APIs?.data?.rent_count,
                    commercial_count : APIs?.data?.commercial_count
                }))
                break;
            case "AgentProfile" :
                setAPIsBundles(prevVal =>({...prevVal, 
                    count_agents : APIs?.data?.amentiesCounts?.count_agents,
                    count_properties : APIs?.data?.amentiesCounts?.count_properties,
                    buy_count : APIs?.data?.amentiesCounts?.buy_count,
                    rent_count : APIs?.data?.amentiesCounts?.rent_count,
                    commercial_count : APIs?.data?.amentiesCounts?.commercial_count
                }))
                break;
        }
    }, [APIs]);

  const OverviewTemp = ()=>{
  return(
          <div className='pl-md-4 pl-2 d-flex flex-column align-items-start'>
            {/* Icons Holder */}
            <div className='cCT-gray-text d-flex flex-column align-items-start pl-md-4'>
                <Icon iconName='iCT-clockgreen'iconSize='iCT-sB uCT-img-100 mr-3'spaces='mb-3 fCT-Regular-sB'innerText='Open From 9:00AM To 7:00PM' textPosition='right'/> 
                <Icon iconName='iCT-agentblue'iconSize='iCT-sB uCT-img-100 mr-3' spaces='mb-3 fCT-Regular-sB' innerText={APIsBundles.count_agents  + ' ( Agents )'} textPosition='right'/> 
                <Icon iconName='iCT-propertymoove'iconSize='iCT-sB uCT-img-100 mr-3' spaces='mb-3 fCT-Regular-sB' innerText={APIsBundles.count_properties?.length + ' ( Properties )'} textPosition='right'/> 
            </div>
            {/* Details Section */}
            { APIsBundles.buy_count || APIsBundles.rent_count || APIsBundles.commercial_count &&
                <div className="d-flex align-items-center justify-content-start px-md-5 ml-2 py-2 w-100 uCT-b-radius-sC fCT-Regular-sA">
                    {APIsBundles.buy_count && 
                        <>
                            <div className="d-flex flex-column justify-content-start">
                                <p className='mb-0 '>{APIsBundles.buy_count}</p>
                                <p className='mb-0 cCT-gray-text'>Buy</p>
                            </div>
                            <div className='mb-0 CT-Horizontal-Line cCT-white-bg mx-4'></div>
                        </>
                    }
                    {APIsBundles.rent_count && 
                        <>
                            <div className="d-flex flex-column">
                                <p className='mb-0'>{APIsBundles.rent_count}</p>
                                <p className='mb-0 cCT-gray-text'>Rent</p>
                            </div>
                            <div className='mb-0 CT-Horizontal-Line cCT-white-bg mx-4'></div>
                        </>
                    }
                    {APIsBundles.rent_count && 
                        <>
                            <div className="d-flex flex-column">
                                <p className='mb-0'>{APIsBundles.commercial_count}</p>
                                <p className='mb-0 cCT-gray-text'>Commercial</p>
                            </div>
                        </>
                    }
                </div>
            }
          </div>
        )
    }
    return OverviewTemp()
}