import $ from 'jquery'; 
import { useMediaQuery } from 'react-responsive';
// effect element height with window height (selectorID) : selector id that u want to effect with height (spaceID) : element give more space 
export function SideNavScreenHeight(selectorID, spaceID){
    if(window.innerHeight < 900 ){
        $(`#${selectorID}`).css('max-height', (window.innerHeight) + 'px')
        $(`#${spaceID}`).removeClass('d-none')
    }else{
        $(`#${spaceID}`).addClass('d-none')
    }
    // window.innerHeight < 900 ? $(`#${selectorID}`).css('max-height', window.innerHeight + 'px') : false
}
export function giveClassOnScroll(selectorID, addClasess, removeClasses, scrollLimt){
    let elem = document.getElementById(selectorID)
    if(elem){
        var rect = elem.getBoundingClientRect();
        if (rect.top <= scrollLimt){
            $(`#${selectorID}`).addClass(addClasess)
            $(`#${selectorID}`).removeClass(removeClasses)
        }else{
            $(`#${selectorID}`).removeClass(addClasess)
            $(`#${selectorID}`).addClass(removeClasses)
        }
    }
}
export function ToggleNumbers(start, end, state, setState){
    switch (state) {
        case start:
            setState(end)
            break;
        case end:
            setState(start)
            break;
        default:
            setState(start)
            break;
    }
}
export function MediaCT(mideaSize){
    let sizeValue = Number
    switch(mideaSize){
        case'xl':
            sizeValue = 1200
            break;
        case'lg':
            sizeValue = 992
            break;
        case'md':
            sizeValue = 768
            break;
        case'sm':
            sizeValue = 575
            break;
    }
    return useMediaQuery({ query: `(max-width: ${sizeValue}px)` })
}