import HorizentalLine from 'view/features/shared/HorizentalLine/HorizentalLine'
import ButtonRightIcon from 'view/features/shared/buttons/Button-Right-Icon/ButtonRightIcon'
import PagesTitle from 'view/features/shared/pagesTitle/PagesTitle'
import PageTitleWireframe from 'view/features/shared/pagesTitle/PageTitle-wireFrame/PageTitle.wireframe'
import PropertyGallery from 'view/features/property/property-details/Property-Gallery/PropertyGallery'
import PropertyGalleryWireFrame from 'view/features/property/property-details/Property-Gallery/PropertyGallery-WireFrame/PropertyGallery.WireFrame'
import PropertyTitle from 'view/features/property/property-details/Property-Title/PropertyTitle'
import PropertyTitleWireFrame from 'view/features/property/property-details/Property-Title/PropertyTitle-WireFrame/PropertyTitle.WireFrame'
import PropertyNavigator from 'view/features/property/property-details/Property-Navigator/PropertyNavigator'
import PropertyNavigatorWireFrame from 'view/features/property/property-details/Property-Navigator/PropertyNavigator-WireFrame/PropertyNavigator.WireFrame'
import RightWidget from 'view/layout/Components/widgets/Right-Widget/Right-Widget'
import PropertyOverview from 'view/features/property/property-details/Property-Overview/PropertyOverview'
import PropertyOverviewWireFrame from 'view/features/property/property-details/Property-Overview/PropertyOverview-WireFrame/PropertyOverview.WireFrame'
import PropertyDescription from 'view/features/property/property-details/Property-Description/PropertyDescription'
import PropertyDescriptionWireFrame from 'view/features/property/property-details/Property-Description/PropertyDescription-WireFrame/PropertyDescription.WireFrame'
import PropertyDetailsComp from 'view/features/property/property-details/Property-Details-Comp/PropertyDetailsComp'
import PropertyDetailsWireFrame from 'view/features/property/property-details/Property-Details-Comp/PropertyDetails-WireFrame/PropertyDetails.WireFrame'
import PropertyLocation from 'view/features/property/property-details/Property-Location/PropertyLocation'
import PropertyLocationWireFrame from 'view/features/property/property-details/Property-Location/PropertyLocation-WireFrame/PropertyLocation.WireFrame'
import PropertyNearby from 'view/features/property/property-details/Property-Nearby/PropertyNearby'
import PropertyNearbyWireFrame from 'view/features/property/property-details/Property-Nearby/PropertyNearby-WireFrame/PropertyNearby.WireFrame'
import PropertyFloorPlan from 'view/features/property/property-details/Property-Floor-Plan/PropertyFloorPlan'
import PropertyAmenities from 'view/features/property/property-details/Property-Amenities/PropertyAmenities'
import PropertyAmenitiesWireFrame from 'view/features/property/property-details/Property-Amenities/PropertyAmenities-WireFrame/PropertyAmenities.WireFrame'
import ContentSlider from 'view/layout/Components/sliders/ContentSlider/ContentSlider'
import PropertyGridCard from 'view/features/property/Property-Grid-Card/PropertyGridCard'
import RatingOverview from 'view/features/profiles/Rating-Overview/RatingOverview'
import RatingOverviewWireFrame from 'view/features/profiles/Rating-Overview/RatingOverview-WireFrame/RatingOverview.WireFrame'
import ReviewCard from 'view/features/profiles/Review-Card/ReviewCard'
import AgentCard from 'view/features/agent/AgentCard/AgentCard'
import AgentWireFrame from 'view/features/agent/AgentCard/Agent-WireFrame/Agent.WireFrame'
import AdvSmCard from 'view/features/advertisement/Adv-SM-Card/Adv-SM-Card'
import { giveClassOnScroll, SideNavScreenHeight } from 'view/viewLogic/view-logic'
import React, { useState, useEffect } from 'react'
import { _GETID_Property } from 'Controllers/services/property/property.services'
import { useParams } from "react-router-dom";
import { useMediaQuery } from 'react-responsive';
import PropertyCardWireFrame from 'view/features/property/Property-Grid-Card/PropertyCard-wireframe/PropertyCard.wireframe'
import AgencyListWireFrame from 'view/features/agency/Agency-List-View/AgencyList-WireFrame/AgencyList.WireFrame'
export default function PropertyDetails() {
    const mediaXl = useMediaQuery({ query: `(max-width: 1345px)` })
    let { id } = useParams();
    const [APIsKey, setAPIsKey] = useState('PropertyDetails')
    const [propertyByID, setPropertyByID] = useState();
    function onScroll() {
        giveClassOnScroll('propNavHolder', 'p-4 shadow', 'shadow-sm', 80)
        giveClassOnScroll('propNavWhiteHolder', '', 'cCT-white-bg', 104)
    }
    // USE_Effect call after render 
    useEffect(() => {
        setPropertyByID('')
        _GETID_Property(id).then(res => {
            setPropertyByID(res)
            console.log(res);
        })
    }, [id])
    useEffect(() => {
        window.addEventListener('scroll', onScroll);
        return () => {
            window.removeEventListener('scroll', onScroll);
        }
    }, []);
    let floorPlansArr = [0]

    const rightWidgetContent = () => {
        return (
            <section className="w-100">
                {propertyByID ?
                    <>
                        <div className="mb-4">
                            <AgentCard APIs={{ APIsKey, data: { user: propertyByID?.data?.data?.user } }} />
                        </div>
                        <AdvSmCard />
                    </>
                    :
                    <>
                        <AgentWireFrame />
                    </>
                }
            </section>
        )
    }
    const propertyDetailsTemp = () => {
        return (
            <div className="container-fluid px-3 px-md-4 py-0">
                {propertyByID ? <PropertyGallery APIs={{ APIsKey, data: { images: propertyByID?.data?.data?.image, virtual_tour: propertyByID?.data?.data?.virtual_tour } }} /> : <PropertyGalleryWireFrame />}
                {/* <LightBoxPopup images={images}/> */}
                <div className="container p-0">
                    <div className={`row w-100 m-0 p-0 ${mediaXl ? 'flex-wrap' : 'flex-lg-nowrap'}  `}>
                        {/* page content */}
                        <div className='flex-grow-1'>
                            {/* Property Title */}
                            {propertyByID ? <PropertyTitle APIs={{ APIsKey, data: { main: propertyByID?.data?.data?.main } }} /> : <PropertyTitleWireFrame />}
                            {/* Property Navigator */}
                            <nav id='propNavHolder' className={`shadow-sm uCT-b-radius-sB CT-blog-nav ${propertyByID ? 'cCT-Gradian-Linear-supp2' : 'cCT-layout-I uCT-wire-frame-opacity-5'} `}>
                                <div id='propNavWhiteHolder' className={`uCT-f-radius-sB ${propertyByID ? 'cCT-white-bg' : 'cCT-layout-I'}`}>
                                    {propertyByID ? <PropertyNavigator /> : false}
                                </div>
                            </nav>
                            {/* Property Overview */}
                            <div className={`uCT-f-radius-sB  mt-4 pl-2 ${propertyByID ? 'cCT-white-bg' : 'cCT-layout-I uCT-wire-frame-opacity-5'} `}>
                                {propertyByID ?
                                    <>
                                        <PagesTitle title="Overview" />
                                        <PropertyOverview APIs={{ APIsKey, data: { overview: propertyByID?.data?.data?.overview } }} />
                                    </>
                                    :
                                    <>
                                        <PageTitleWireframe />
                                        <PropertyOverviewWireFrame />
                                    </>
                                }
                            </div>
                            {/* Property Description */}
                            <div className="pl-2 pl-lg-4">
                                {propertyByID ?
                                    <>
                                        <PagesTitle title="Description" />
                                        <PropertyDescription APIs={{ APIsKey, data: { description: propertyByID?.data?.data?.description } }} />
                                    </>
                                    :
                                    <>
                                        <PageTitleWireframe />
                                        <PropertyDescriptionWireFrame />
                                    </>}
                            </div>
                            {/* Property Details */}
                            <div className="pl-2 pl-lg-4">
                                {propertyByID ?
                                    <>
                                        <PagesTitle title="Property Details" />
                                        <PropertyDetailsComp APIs={{ APIsKey, data: { propertyDetails: propertyByID?.data?.data?.propertyDetails } }} />
                                    </>
                                    :
                                    <>
                                        <PageTitleWireframe />
                                        <PropertyDetailsWireFrame />
                                    </>}
                            </div>
                            <div className='d-flex justify-content-center m-0 p-0'>
                                <HorizentalLine classes='sm' />
                            </div>
                            {/* Property Location */}
                            <div className=''>
                                {propertyByID ?
                                    <>
                                        <PagesTitle title="Property Location" />
                                        <PropertyLocation APIs={{ APIsKey, data: { propertyLocation: propertyByID?.data?.data?.propertyLocation } }} />
                                    </>
                                    :
                                    <>
                                        <PageTitleWireframe />
                                        <PropertyLocationWireFrame />
                                    </>}
                                <div className='d-flex justify-content-center m-0 p-0'>
                                    <HorizentalLine classes='sm' />
                                </div>
                            </div>
                            {/* Property Nearby */}
                            <div className='pl-md-4'>
                                {propertyByID ?
                                    <>
                                        <PagesTitle title="Nearby" />
                                        <PropertyNearby />
                                    </>
                                    :
                                    <>
                                    <PageTitleWireframe />
                                    <PropertyNearbyWireFrame />
                                    </>
                                }
                                <div className='d-flex justify-content-center m-0 p-0'>
                                    <HorizentalLine classes='sm' />
                                </div>
                            </div>
                            {/* Property Floor Plane */}
                            <div className='pl-~2 pl-lg-4'>
                                <PagesTitle title="Floor Plane" />
                                {floorPlansArr.map((floorPlan, i) => {
                                    return <PropertyFloorPlan key={i} holderState={i == 0 ? 'open' : false} />
                                })}
                                <div className='d-flex justify-content-center m-0 p-0'>
                                    <HorizentalLine classes='sm' />
                                </div>
                            </div>
                            {/* Property Amenities */}
                            <div className='pl-2 pl-md-4'>
                                {propertyByID ?
                                    <>
                                        <PagesTitle title="Amenities" />
                                        <PropertyAmenities APIs={{ APIsKey, data: { amenities: propertyByID?.data?.data?.amenities } }} />
                                    </>
                                    :
                                    <>
                                        <PageTitleWireframe />
                                        <PropertyAmenitiesWireFrame />
                                    </>}

                                <ButtonRightIcon innerText='Load More Amenities' btnICON='iCT-ArrowRight-White' btnCOLOR='cCT-Gradian-Linear-Main' iconBgCOLOR='cCT-inherit-bg' position='center' />
                                <div className='d-flex justify-content-center m-0 p-0'>
                                    <HorizentalLine classes='sm' />
                                </div>
                            </div>
                            {/* Property Rate */}
                            <div className='pl-2 pl-lg-4'>
                                {propertyByID ?
                                    <>
                                        <PagesTitle title="Property Rate" />
                                        <RatingOverview APIs={{ APIsKey, data: { reviews: propertyByID?.data?.data?.reviews } }} />
                                    </>
                                    :
                                    <>
                                        <PageTitleWireframe />
                                        <RatingOverviewWireFrame />
                                    </>}
                            </div>
                            {/* Property Reviews */}
                            <div className='pl-2 pl-lg-4'>
                                <div className="row w-100 m-0">
                                    <div className="col-8 px-0">
                                        <PagesTitle title="Property Reviews" />
                                    </div>
                                    <div className="col-4 px-0 d-flex align-items-center">
                                        <ButtonRightIcon innerText='Write a Review' btnICON='iCT-writeRate' btnCOLOR='cCT-Gradian-Linear-Main' position='right' />
                                    </div>
                                </div>
                                <div className="uCT-cards-grid pt-4 justify-content-center ml-4">
                                    {propertyByID?.data?.data?.reviews?.all_review?.map((reviewCard) => {
                                        return <ReviewCard key={reviewCard?.id} APIs={{ APIsKey, data: { all_reviews: reviewCard } }} />
                                    })
                                    }
                                    {/* dummy Cards */}
                                    <div className='uCT-review-dummy'></div>
                                    <div className='uCT-review-dummy'></div>
                                </div>
                                <ButtonRightIcon innerText='Load More Reviews' btnICON='iCT-ArrowRight-White' btnCOLOR='cCT-Gradian-Linear-Main' iconBgCOLOR='cCT-inherit-bg' position='center' />
                                <div className='d-flex justify-content-center m-0 p-0'>
                                    <HorizentalLine classes='sm' />
                                </div>
                            </div>
                        </div>
                        {/* right widget */}
                        {mediaXl ?
                            <div className="w-100 pt-5 pb-4 d-flex justify-content-center">
                                <AgencyListWireFrame />
                            </div>
                            :
                            <div className="">
                                <RightWidget content={rightWidgetContent()} topValue='105' />
                            </div>
                        }
                    </div>
                    {/* similar listings */}
                    <div>
                         
                        <ContentSlider content={
                            propertyByID?.data?.data?.same_properties ?
                                propertyByID?.data?.data?.same_properties?.map((property) => {
                                    return <PropertyGridCard key={property?.id} APIs={{ APIsKey, data: { property } }} />
                                })
                                : Array(8).fill().map((w, i) => {
                                    return <PropertyCardWireFrame key={i} />
                                })
                        } title='Similar Listings' />
                    </div>
                </div>

            </div>
        )
    }
    return propertyDetailsTemp()
}