import "./Footer.sass"
import Icon from "../../../features/shared/Icon/Icon"
import { _GET_AllFooter } from "../../../../Controllers/services/footer/footer.services"
import { _GET_TopCategory } from "../../../../Controllers/services/footer/footer.services"
import { useEffect, useState } from "react"
export default function Footer() {
    const [allFooter, setAllFooter] = useState([])
    const [topCategory, setTopCategory] = useState([])
    // html Render
    useEffect(() => {
        _GET_AllFooter().then(res => {
            setAllFooter(res?.data?.data[0])
        }).catch(err => {
            console.log(err);
        })

    }, [])
    useEffect(() => {
        _GET_TopCategory().then(res => {
            setTopCategory(res.data.data)
        }).catch(error => {
            console.log(error);
        })
    }, [])

    function openLink(link) {
        switch (link) {
            case 'google': return window.open(allFooter?.play_store, "_blank");
            case 'apple': return window.open(allFooter?.app_store, "_blank");
            case 'facebook': return window.open(allFooter?.facebook, "_blank");
            case 'twitter': return window.open(allFooter?.twitter, "_blank");
            case 'youtube': return window.open(allFooter?.youtube, "_blank")
            case 'instagram': return window.open(allFooter?.instagram, "_blank");
            default:
                return window.open("https://deal360.ae/", "_blank");
        }
    }
    const temp = () => {
        return (

            <footer className="cCT-smoke-bg text-capitalize">
                <div className="container cCT-gray-text ">
                    {/* Logo ROW */}
                    <div className="row py-4">
                        <i className="imCT-darkLogo uCT-Dark-logo uCT-img-cover"></i>
                    </div>
                    {/* Second Footer Row */}
                    <div className="row flex-column flex-md-row p-0 m-0 cCT-gray-text">
                        {/* About */}
                        <div className="col col-md-4 p-0">
                            <div className="cCT-dark-text">
                                <p className="fCT-Regular-sE mb-4">About</p>
                            </div>
                            <Icon iconName='iCT-tower-office-gray' iconSize='iCT-sB mr-3' textPosition='right' textSpaces='w-100' innerText={allFooter?.address} spaces='mb-4' />
                            <Icon iconName='iCT-call-gray' iconSize='iCT-sB mr-3' textPosition='right' innerText={allFooter?.phone} spaces='mb-4' />
                            <Icon iconName='iCT-office-phone-gray' iconSize='iCT-sB mr-3' textPosition='right' innerText={allFooter?.phone} spaces='mb-4' />
                            <Icon iconName='iCT-mail-gray' iconSize='iCT-sB mr-3' textPosition='right' innerText={allFooter?.email} spaces='mb-4' />
                        </div>
                        {/* List Of Category */}
                        <div className="col col-md-4 p-0">
                            <div className="cCT-dark-text">
                                <p className="fCT-Regular-sE mb-4">List Of Category</p>
                                {topCategory?.map((category) => {
                                    return <div key={category?.id} className="w-100 d-flex align-items-center mb-3">
                                        <div className="pl-3">
                                            <p className="cCT-gray-text mb-0 ">{category?.title}</p>
                                            <span className=""> <strong>{category?.total}</strong></span>
                                        </div>
                                    </div>
                                })
                                }
                            </div>
                        </div>
                        {/* Social Media */}
                        <div className="col col-md-4 p-0 d-flex d-sm-block flex-column align-items-center justify-content-center">
                            <p className="fCT-Regular-sE cCT-dark-text w-100 mb-3 mb-md-4">Social Media</p>
                            <p className="fCT-Regular-sC w-100 mb-3 mb-md-5">follow us in social media to find out the latest updates in our progress.</p>
                            <div className="d-flex w-100 justify-content-center justify-content-sm-start my-4 pl-3">
                                <div onClick={() => openLink('facebook')}>
                                    <Icon iconName='iCT-facebookwhite' bgColor='cCT-gray-bg' iconSize='iCT-sB' radiusSize='rounded-circle' spaces='mr-3 p-2' />
                                </div>
                                <div onClick={() => openLink('twitter')}>
                                    <Icon iconName='iCT-twitter' bgColor='cCT-gray-bg' iconSize='iCT-sB' radiusSize='rounded-circle' spaces='mr-3 p-2' />
                                </div>
                                <div onClick={() => openLink('youtube')}>
                                    <Icon iconName='iCT-youtube' bgColor='cCT-gray-bg' iconSize='iCT-sB' radiusSize='rounded-circle' spaces='mr-3 p-2' />
                                </div>
                                <div onClick={() => openLink('instagram')}>
                                    <Icon iconName='iCT-instagram' bgColor='cCT-gray-bg' iconSize='iCT-sB' radiusSize='rounded-circle' spaces='mr-3 p-2' />
                                </div>
                            </div>
                            {/* Google Play Button */}
                            <div onClick={() => openLink('google')} className="CT-group-app mb-4 mr-sm-5 py-3 px-4 d-flex align-items-center justify-content-center uCT-click uCT-f-radius-sA uCT-googlePlay cCT-dark-text">
                                <Icon iconName='iCT-googlePlay' textPosition='' iconSize='iCT-sD' spaces='mr-3' />
                                <div className="">
                                    <p className="m-0 fCT-Light-sA cCT-dark-text text-center">Available On</p>
                                    {/* <a href={allFooter?.play_store}> */}
                                    <p className="m-0 fCT-Regular-sD text-center">google play</p>
                                    {/* </a> */}
                                </div>
                            </div>
                            {/* APP Store Button */}
                            <div onClick={() => openLink('apple')} className="CT-group-app mb-4 mr-sm-5 py-3 px-4 px-sm-4 d-flex align-items-center justify-content-center uCT-click uCT-f-radius-sA uCT-googlePlay cCT-dark-text">
                                <Icon iconName='iCT-appStore-dark' textPosition='' iconSize='iCT-sD' spaces='mr-3' />
                                <div className="">
                                    <p className="m-0 fCT-Light-sA text-center">Download On The</p>
                                    <p className="m-0 fCT-Regular-sD text-center" >App Store</p>
                                </div>
                            </div>
                        </div>
                    </div>
                    <hr className="cCT-smoke-bg m-0" />
                    {/* CopyRight */}
                    <div className="row d-block d-sm-flex m-0 py-4 cCT-dark-text fCT-Light-sC">
                        <div className="col col-md-6 p-0 pr-4">
                            <p className="mb-4 mb-md-0 fCT-Light-sB">DEAL360 Copyright&copy;2006-2021 All Right Reserved. <span className='cCT-main-text'>Created By Creative Motion Developers Team </span></p>
                        </div>
                        <div className="col p-0">
                            <p className="mb-4 mb-md-0">Privacy Policy</p>
                        </div>
                        <div className="col p-0">
                            <p className="mb-4 mb-md-0">Termes Of User</p>
                        </div>
                        <div className="col p-0 d-flex justify-content-start">
                            <Icon iconName='iCT-unitedArabEmirates' iconSize='iCT-sC' textPosition='left' innerText="English" textSpaces='mr-3' />
                        </div>
                    </div>
                </div>
            </footer>
        )
    }
    return temp()
}
// export default Footer;