import './AboutUsImage.feature.scss'
import $ from 'jquery';
import AboutImage from '../../../../../../assets/images/Intersection_SVG.svg'
import { useEffect } from 'react';
export default function AboutUsImageFeature() {
    const aboutusSvg = () => {
        let aboutSVG = document.getElementById('aboutSVG')
        aboutSVG.addEventListener("load", function () {
            let _SVGImageRight = aboutSVG.contentDocument.children[0].children[0].children[0]
            _SVGImageRight.animate([
                { transform: 'translateY(-700px)', },
                { transform: 'translateY(100px)' },
                { transform: 'translateY(0px)', }
            ], {
                // timing options
                duration: 1200,
            });
            let _SVGImageCenter = aboutSVG.contentDocument.children[0].children[1].children[0]
            _SVGImageCenter.animate([
                { transform: 'translateY(-750px)' },
                { transform: 'translateY(10px)' },
                { transform: 'translateY(0px)' }
            ], {
                // timing options
                duration: 800,
            });
            let _SVGImageLeft = aboutSVG.contentDocument.children[0].children[2].children[0]
            _SVGImageLeft.animate([
                { transform: 'translateY(-500px)', },
                { transform: 'translateY(100px)' },
                { transform: 'translateY(0px)' }
            ], {
                // timing options
                duration: 1200,
            });
            $('#aboutSVG').addClass('visible')
            $('#aboutSVG').removeClass('invisible')
        })
    }
    useEffect(() => { aboutusSvg() }, [])
    const aboutUsImageTemp = () => {
        return (
            <div className="d-flex align-items-center justify-content-center px-3 pt-2">
                {/* About Us Image */}
                <object id='aboutSVG' data={AboutImage} className='in~visible CT-aboutus-image uCT-img-contain' ></object>
            </div>

        )
    }
    return aboutUsImageTemp()
}