export default function PageHeadWireFrame() {
    const pageHeadTemp = () => {
        return (
            <div className={`uCT-img-parallax CT-pageHead-holder cCT-layout-II uCT-img-cover d-flex flex-column justify-content-center`}>
                <div >
                    <div className="container cCT-support1-text">
                        <p style={{ width: '200px' }} className="aCT-wire-frame-loading mb-3 fCT-wireFrame-hG uCT-f-radius "></p>
                        <p style={{ width: '350px' }} className="aCT-wire-frame-loading fCT-wireFrame-hG uCT-f-radius "></p>
                    </div>
                </div>
            </div>
        )
    }
    return pageHeadTemp()
}