
import { MediaCT } from 'view/viewLogic/view-logic'
import './blog-nav.sass'
export default function BlogNav(){
    const blogNavTemp = ()=>{
        return (
            <div className=''>
                {/* Secound Section */}
                <div className="d-flex w-100 align-items-center flex-column flex-xl-row">
                    {/* Holder Navigation Links */}
                    <nav className={`${MediaCT('xl') ? 'w-100 overflow-auto' : false} order-xl-0 order-1 py-xl-0 py-2 navbar  navbar-expand p-0`}>
                        <div className="collapse navbar-collapse" id="navbarSupportedContent" >
                            <ul className="navbar-nav flex-nowrap text-nowrap justify-content-center w-100">
                                {/* Home BTN */}
                                <li className="nav-item active  mr-4">
                                    <a className="nav-link p-0 cCT-secondary-text" href="#">Home</a>
                                </li>
                                <li className="nav-item mr-4">
                                    <a className="nav-link p-0 cCT-secondary-text" href="#">News</a>
                                </li>
                                <li className="nav-item mr-4">
                                    <a className="nav-link p-0 cCT-secondary-text" href="#">Trends</a>
                                </li>
                                <li className="nav-item mr-4">
                                    <a className="nav-link p-0 cCT-secondary-text" href="#">Research</a>
                                </li>
                                <li className="nav-item mr-4">
                                    <a className="nav-link p-0 cCT-secondary-text " href="#">Life At Home</a>
                                </li>
                            </ul>
                        </div>
                    </nav>
                    {/* Search Input */}
                    <div className="order-xl-1 w-100 order-0 mb-xl-0 mb-3 shadow d-flex cCT-Gradian-Linear-supp align-items-center uCT-f-radius-sB justify-content-center CT-blogSearchHolder flex-grow-1 ">
                        {/* Search Icone Holder */}
                        <div className="d-flex cCT-inherit-bg uCT-l-radius-sC align-items-center h-100 pr-2 pl-4">
                            <i className="iCT-searchblue iCT-sB uCT-img-cover"></i>
                        </div>
                        {/* Search Bar */}
                        <input className="cCT-inherit-bg pr-2 border-0 h-100 w-100 uCT-r-radius-sB" placeholder='Search...' type="text" />
                    </div>
                </div>
            </div>
        )
    }
    return blogNavTemp()
}