export default function ShareArticle(){
    const shareArticleTemp = ()=>{
        return (
            <div className=''>
                <p className='text-center fCT-Regular-sC cCT-gray-text'> <b> <span className='cCT-gray-text'>-----</span> Share this article <span className='cCT-gray-text'>-----</span> </b></p>
                <div className='d-flex justify-content-center'>
                    <div className="uCT-click rounded-circle uCT-bg mr-4 p-2">
                        <a href="https://www.facebook.com">
                            <i className="d-flex iCT-sB uCT-img-cover iCT-blue-fb"></i>
                        </a>
                    </div>
                    <div className="uCT-click rounded-circle uCT-bg mr-4 p-2">
                        <a href="https://www.twitter.com">
                            <i className="d-flex iCT-sB uCT-img-cover iCT-blue-tw"></i>
                        </a>
                    </div>
                    <div className="uCT-click rounded-circle uCT-bg p-2">
                        <a href="https://www.instagram.com">
                            <i className="d-flex iCT-sB uCT-img-cover iCT-blue-insta"></i>
                        </a>
                    </div>
                </div>
            </div>
        )
    }
    return shareArticleTemp()
}