// from https://github.com/chenkie/react-user-authentication
import { EventEmitter } from 'events'
import { isTokenExpired } from './jwtHelper'
import { browserHistory } from 'react-router'
import jwtDecode from 'jwt-decode'
import { API } from 'environment/environment'

export const _doAuthentication = async (endpoint, values) => {
  return await API.post(endpoint, JSON.stringify(values), { 'Content-Type': 'application/json' })
}

export const login = (user, password) => {
  return _doAuthentication('api/auth/login', { user, password })
}
export const finishAuthentication = (token) => {
  localStorage.setItem('token', token)
}