import { API } from "../../../environment/environment";


export const footerReducer = (state = {}, action) => {
    switch (action.type) {

        default:
            return state;
    }
    return state
}
export const _GET_AllFooter = async () => {
    return await API.get(`api/setting/main/list`)
}

export const _GET_TopCategory = async () => {
    return await API.get(`api/core_data/category/top`)
}