import './mini-agent-card.sass'
import Icon from '../../shared/Icon/Icon'
import React, { useState, useEffect } from 'react';
import { Link } from "react-router-dom";


export default function MiniAgentCard({APIs = {}}){
    console.log(APIs);

    const [APIsBundles, setAPIsBundles] = useState({
    id: String,
    image: String,
    avg_rating: Number,
    fullname: String,
    country: String,
    count_agents: String,
    count_properties: String,
    status_work: String,
    buy_count: String,
    rent_count: String,
    commercial_count: String
    });
    useEffect(() => {
        switch(APIs.from){
            case "agents" :
                setAPIsBundles(prevVal =>({...prevVal, 
                    id : APIs?.data?.agents?.id,
                    image : APIs?.data?.agents?.image,
                    avg_rating : APIs?.data?.agents?.avg_rating,
                    fullname : APIs?.data?.agents?.fullname,
                    country : APIs?.data?.agents?.country?.title,
                    count_agents : APIs?.data?.agents?.count_agents,
                    count_properties : APIs?.data?.agents?.count_properties,
                    status_work : APIs?.data?.agents?.status_work,
                    buy_count : APIs?.data?.agents?.buy_count,
                    rent_count : APIs?.data?.agents?.rent_count,
                    commercial_count : APIs?.data?.agents?.commercial_count
                }))
                break;
            case "AgencyProfile" :
                setAPIsBundles(prevVal =>({...prevVal, 
                    id : APIs?.data?.agentsFromAgency?.id,
                    image : APIs?.data?.agentsFromAgency?.image,
                    avg_rating : APIs?.data?.agentsFromAgency?.avg_rating,
                    fullname : APIs?.data?.agentsFromAgency?.fullname,
                    country : APIs?.data?.agentsFromAgency?.country?.title,
                    count_agents : APIs?.data?.agentsFromAgency?.count_agents,
                    count_properties : APIs?.data?.agentsFromAgency?.count_properties,
                    status_work : APIs?.data?.agentsFromAgency?.status_work,
                    buy_count : APIs?.data?.agentsFromAgency?.buy_count,
                    rent_count : APIs?.data?.agentsFromAgency?.rent_count,
                    commercial_count : APIs?.data?.agentsFromAgency?.commercial_count
                }))
                break;
        }
    }, [APIs]);

    const miniAgentCardTemp = ()=>{
        return (
            <Link to={`/agent/${APIsBundles.id}`}className='CT-mini-card uCT-scale-in-animation uCT-click'>
                <div className="position-relative uCT-f-radius-sB d-flex h-100 flex-column align-items-center justify-content-end CT-transform-scale">
                    {/* agent image */}
                    <div className='d-flex justify-content-center position-absolute CT-Image-Holder uCT-bd-main-sB rounded-circle'>
                        <i className='iCT-x-agentProfile uCT-bd-white-sA rounded-circle CT-profile-img-holder uCT-img-contain' style={{ backgroundImage: `url(${APIsBundles.image})` }} ></i>
                    </div>
                    <div className='cCT-Gradian-Linear-supp shadow w-100 uCT-f-radius-sA CT-Holder-Content d-flex flex-column align-items-center justify-content-end'>
                        {/* Tags Section */}
                        <div className='d-flex'>
                            <Icon iconName='iCT-rategold' spaces='p-2 fCT-bold-sB fCT-Regular-sA mr-2' bgColor='cCT-Gradian-Linear-supp shadow-sm' radiusSize='uCT-f-radius-sA' iconSize='iCT-sA ml-1' innerText={APIsBundles.avg_rating} textPosition='left'/>
                            <Icon iconName='iCT-authblue' spaces='p-2 fCT-bold-sB fCT-Regular-sA ' bgColor='cCT-Gradian-Linear-supp shadow-sm' radiusSize='uCT-f-radius-sA' iconSize='iCT-sA ml-1' innerText='Excellent' textPosition='left'/>
                        </div>
                        {/* agent Name */}
                        <p className='fCT-Regular-sC text-center pt-2 mb-0'>
                            {APIsBundles.fullname}
                        </p>
                        {/* agent Role */}
                        <p className='fCT-Regular-sB text-center m-0 cCT-gray-text'>
                            Senior Client Manager
                        </p>
                        {/* BTN Section 3rd */}
                        <div className="d-flex my-4 px-4~">
                            {/* Message Icon */}
                            <Icon iconName='iCT-messagewhite' spaces='px-sm-5 px-4 py-2 mr-3 fCT-Regular-sC' bgColor='cCT-Gradian-Linear-Main cCT-white-text' radiusSize='uCT-f-radius-sA' iconSize='iCT-sB mr-2' textPosition='right' innerText='Message' />
                            {/* Call Icon */}
                            <Icon iconName='iCT-callgreen' spaces='iCT-sE' bgColor='cCT-opacity-green-bg-20' radiusSize='uCT-f-radius-sA' iconSize='iCT-sB' />
                        </div>
                    </div>
                </div>
            </Link>
        )
    }
    return miniAgentCardTemp()
}