import PagesTitle from '../../../shared/pagesTitle/PagesTitle'
import './article-create-comment.sass'
export default function ArticleCreateComment(){
    const articleCreateCommentTemp = ()=>{
        return (
            <section>
                {/* text area */}
                <div className='d-flex shadow-sm uCT-f-radius-sA '>
                    <div className='uCT-l-radius-sA border-0 px-4 py-3 d-flex cCT-white-bg'>
                        <i className=' uCT-bd-main-sA iCT-x-agentProfile rounded-circle iCT-sE cCT-white-bg uCT-img-cover'></i>
                    </div>
                    <textarea name="" id="" placeholder='write a comment' class='py-4 uCT-r-radius-sA CT-article-text-area w-100 uCT-border-sA border-0 cCT-white-bg m-0'>
                    </textarea>
                </div>
                {/* email */}
                <div className="d-flex CT-mail-Holder w-100 mt-4 uCT-f-radius-sA shadow-sm">
                    {/* Search Icone Holder */}
                    <div className="d-flex cCT-white-bg uCT-l-radius-sA border-0 align-items-center h-100 px-4">
                        <i className="iCT-e-mailblue iCT-sC uCT-img-cover"></i>
                    </div>
                    {/* Search Bar */}
                    <input className="cCT-white-bg border-0 border-right-0 w-100 CT-Outline-None pr-4" placeholder='Enter Your E-Mail...' type="text" />
                    {/* Filtration Icone Holder */}
                    <div className="cCT-white-bg uCT-r-radius-sA">
                        <button type="submit" className="px-4 h-100 uCT-r-radius-sA btn cCT-secondary-bg cCT-white-text fCT-Regular-sB cCT-white-tx text-nowrap">Comment</button>
                    </div>
                </div>
            </section>
        )
    }
    return articleCreateCommentTemp()
}