import PageHead from 'view/layout/Components/PageHead/PageHead'
import PropertyGridCard from 'view/features/property/Property-Grid-Card/PropertyGridCard'
import PropertyCardWireFrame from 'view/features/property/Property-Grid-Card/PropertyCard-wireframe/PropertyCard.wireframe'
import PropertyListCard from 'view/features/property/Property-List-Card/PropertyListCard'
import PropertyViewsTool from 'view/features/property/Property-Views-Tool/PropertyViewsTool'
import RightWidget from 'view/layout/Components/widgets/Right-Widget/Right-Widget'
import PagesTitle from 'view/features/shared/pagesTitle/PagesTitle'
import PageTitleWireframe from 'view/features/shared/pagesTitle/PageTitle-wireFrame/PageTitle.wireframe'
import PageHeadWireFrame from 'view/layout/Components/PageHead/PageHead-wireFrame/PageHead.wireFrame'
import './PropertiesList.sass'

import Icon from 'view/features/shared/Icon/Icon'
import { useMediaQuery } from 'react-responsive';
import AdvSmCard from 'view/features/advertisement/Adv-SM-Card/Adv-SM-Card'

import { _GET_AllProperties} from 'Controllers/services/property/property.services'
import { MediaCT } from 'view/viewLogic/view-logic'
import { useState, useEffect } from 'react'
import {  useSelector } from 'react-redux'
import { useLocation } from 'react-router-dom'
import { Link } from "react-router-dom";
// import { FixedSizeList as List } from 'react-window';



export default function PropertyList() {
    const location = useLocation();

    const advancedFilter = useSelector(state => state?.AdvancedSearchReducer?.advancedFilter)
    const [APIsKey, setAPIsKey] = useState('PropertiesList')
    const [allPropertiesList, setAllPropertiesList] = useState([])

    let [toggleValue, setToggleValue] = useState('grid')
    let MediaCT1112 = useMediaQuery({ query: `(min-width: ${'1112'}px)` })
    let MediaCT992 = useMediaQuery({ query: `(min-width: ${'992'}px)` })

    useEffect(()=>{
        // _GET_AllProperties().then(res=>{
        //     setAllPropertiesList(!advancedFilter || advancedFilter == undefined ? res : advancedFilter)
        // }).catch()
        console.log(advancedFilter)
    }, [advancedFilter])

    const rightWidgetContent = () => {
        return (
            <section>
                <AdvSmCard />
                <div className='mb-4'></div>
                <AdvSmCard />
            </section>
        )
    }
    
    const PropertyListTemp = () => {
    return (
        <div className='container-fluid p-0'>
                {/* page head */}
                <header>
                    { allPropertiesList?.data?.data?.paginate?.total ?
                        <PageHead title="Find Your Home In Perfect Way" subTitle="A great real estate agent don't sells, he helps." pImg="imCT-PropertyListCover" />
                        :
                        <PageHeadWireFrame />
                    }
                </header>
                <div className='container h-100 d-flex px-4 py-4'>
                    {/* page body */}
                    <div className='w-100'>
                        {/* Controller Section */}
                        <section className={`d-flex flex-column w-100 justify-content-center`}>
                            {/* Search Input 2nd Section */}
                            <div className={`d-flex CT-table-search-holder mb-4 ${MediaCT('md') ? 'w-100' : 'w-100'} p-0 mr-4`}>
                                {/* Search Icone Holder */}
                                { !MediaCT('md') ? 
                                <div className='shadow-sm d-flex uCT-f-radius-sA w-100 mr-4 cCT-Gradian-Linear-mid-Main'>
                                    <Icon iconName='iCT-searchblue' spaces='px-4 h-100' radiusSize='uCT-l-radius-sC' iconSize='iCT-sB' />
                                    {/* Search Bar */}
                                    <input className="cCT-inherit-bg border-0 w-100 uCT-r-radius-sC" placeholder='Search...' type="text" />
                                </div> : false
                                }
                                {/* Map View BTN */}
                                { location.pathname != '/map'  ?
                                    <Link to='/map' className={`btn d-flex CT-Holder-BTN-View-sm uCT-click uCT-brightness uCT-f-radius-sA justify-content-center align-items-center cCT-opacity-main-bg-10 mb-4 h-100`} style={{border: '1.5px #9c9dfe solid', padding: '12px 0px'}}>
                                        <i className='iCT-sC uCT-img-contain iCT-locationMain mr-2'></i>
                                        <p className='mb-0 d-flex align-items-center cCT-main-text fCT-Regular-sB'>Map View</p>
                                    </Link>: false
                                }
                            </div> 
                            {/* porperty views buttons */}
                            <div className='d-flex justify-content-start'>
                                <PropertyViewsTool holderName={toggleValue} toggleToGridCard={()=> setToggleValue(toggleValue ='grid')} toggleToListCard={()=> setToggleValue(toggleValue ='list')}/>
                            </div>
                        </section>
                        {/* Page Title */}
                        <section>
                            <div className='w-100'>
                                {allPropertiesList?.data?.data?.paginate?.total ?  <PagesTitle title='Properties for Buy In UAE' smallUnderTitle={`${allPropertiesList?.data?.data?.paginate?.total} Properties`} /> : <PageTitleWireframe />}
                            </div>
                        </section>
                        {/* Properties And Ads Sections */}
                        <section className='d-flex'>
                            {/* Property Grid Cards */}
                            {toggleValue == "grid"? 
                                <>
                                    <div className='uCT-card-grid-holder w-100'>
                                        { allPropertiesList?.data?.data?.properties ?
                                        allPropertiesList?.data?.data?.properties.map((property)=>{
                                                return <PropertyGridCard APIs={{APIsKey, data: {property : property}}} key={property.id}/>
                                            }) 
                                        : 
                                        Array(25).fill().map((w, i)=>{
                                            {/* wire frame */}
                                            return <PropertyCardWireFrame key={i}/>
                                        })
                                        }
                                    </div>
                                </> 
                            : false}
                            {/* Property List Cards */}
                            {toggleValue == "list" ? 
                            <div className='d-flex flex-column'>
                                {allPropertiesList?.data?.data?.properties.map((property) => {
                                    return(
                                        <div className='d-flex mb-4'>
                                            <PropertyListCard APIs={{APIsKey, data: {property : property}}} key={property.id} />
                                        </div>
                                    )
                                })}
                            </div> 
                            : false}
                        </section>
                    </div>
                    {/* Ads Section */}
                    { MediaCT992 ?
                    <div className="bg-light~">
                        <RightWidget content={rightWidgetContent()} topValue='104'/>
                    </div> : false
                    }
                </div>
            </div>
        )
    }
    return PropertyListTemp()
}