import AboutUsTextContentFeature from 'view/features/deal360/aboutUs/AboutUs/AboutUsTextContent/AboutUsTextContent.feature'
import AboutUsImageFeature from 'view/features/deal360/aboutUs/AboutUs/AboutUsImage/AboutUsImage.feature'
import './AboutUs.page.scss'
export default function AboutUsPage() {
    const aboutUsPageTemp = () => {
        
        return(
            <div className="CT-aboutus-page-holder justify-content-center align-items-center">
                <div className="CT-text-content-feature">
                    <AboutUsTextContentFeature />
                </div>
                <div className="CT-image-feature CT-aboutus-image-animation">
                    <AboutUsImageFeature/>
                </div>
              
            </div>
        )
    }
    return aboutUsPageTemp()
}