import "../NavBar/Navbar.sass";
import Icon from 'view/features/shared/Icon/Icon'
import { Link } from "react-router-dom";
export default function Navbar({ advancedSearch }) {
  const navBarTemplate = () => {
    return (
      <div onClick={() => advancedSearch(true)}  className="container-fluid d-flex CT-nav-bg-color CT-z-Index align-items-center justify-content-center CT-Holder-h-Navbar px-4">
        {/* First Section */}
        <div className="d-flex justify-content-center px-0">
          {/* Logo Icon */}
          <Link className="CT-Nav-logo uCT-img-100 imCT-darkLogo d-md-block d-none" to='/'></Link>
          <Link className="CT-Nav-logo-icon uCT-img-100 iCT-D360L d-md-none d-block" to='/'></Link>
        </div>
        {/* Secound Section */}
        <div className="d-flex w-100 align-items-center">
          {/* Horizental Line Divider */}
          <div className="d-xl-flex d-none align-items-center">
            <p className="cCT-gray-text mb-0 ml-lg-5 ml-4 h4">|</p>
          </div>
          {/* Holder Navigation Links */}
          <nav className='d-md-block d-xl-none d-none ml-4'>
            <Icon iconName='iCT-burger-Menu' iconSize='iCT-sB' spaces='iCT-sE mx-4~' radiusSize='rounded-circle' />
          </nav>
          <nav className="text-capitalize navbar navbar-expand-xl p-0 ml-xl-5">
            <div className="collapse navbar-collapse" id="navbarSupportedContent" >
              <ul className="navbar-nav">
                {/* Home BTN */}
                <li className="nav-item active mr-4">
                  <Link className="nav-li1nk cCT-inherit-bg p-0 cCT-secondary-text " to='/'>Home</Link >
                </li>
                {/* Properties DropMenu BTN */}
                <li className="nav-item dropdown mr-4">
                  <a className="nav-link d-flex align-items-center p-0 cCT-gray-text" href="#" data-toggle="dropdown" >Explore
                    <i className="iCT-arrowDownblue uCT-img-contain iCT-sA ml-2"></i>
                  </a>
                  <div className="dropdown-menu" aria-labelledby="navbarDropdown">
                    <Link className="dropdown-item" to='/properties-list'>Properties</Link >
                    <Link className="dropdown-item" to='/agencies-list'>Agencies</Link >
                    <Link className="dropdown-item" to='/agents-list'>Agents</Link >
                  </div>
                </li>
                {/* Expoler DropMenu BTN */}
                <li className="nav-item dropdown mr-4~">
                  <a className="nav-link d-flex align-items-center p-0 cCT-gray-text" href="#" data-toggle="dropdown">Community
                    <i className="iCT-arrowDownblue uCT-img-contain iCT-sA ml-2"></i>
                  </a>
                  <div className="dropdown-menu" aria-labelledby="navbarDropdown">
                    <Link className="dropdown-item" to='/blog'>Blog</Link >
                    <Link className="dropdown-item" to='/about-us'>About Us</Link >
                    <Link className="dropdown-item" to='/services'>Our Services</Link >
                  </div>
                </li>
              </ul>
            </div>
          </nav>
          {/* Search Input */}
          <div onClick={(e) => { e.stopPropagation(); advancedSearch()}} className=" d-flex align-items-center shadow uCT-f-radius-sC justify-content-center CT-searchHolder cCT-Gradian-Linear-mid-Main mx-xl-5 ml-sm-4 ml-0 mx-sm-4 w-100">
            {/* Search Icone Holder */}
            <div className="uCT-click d-flex cCT-inherit-bg uCT-l-radius-sC align-items-center h-100 pr-2 pl-4">
              <i className="iCT-searchblue iCT-sB uCT-img-cover"></i>
            </div>
            {/* Search Bar */}
            <input className="uCT-click cCT-inherit-bg border-0 h-100 w-100" disabled placeholder='Search...' type="text" />
            {/* Filtration Icone Holder */}

          </div>
        </div>
        {/* Third Section */}
        <div className="d-flex~ align-items-center d-none d-md-flex">
          {/* List Your Property BTN */}
          <Link className="uCT-f-radius-sC border-0 shadow btn px-xl-4 CT-BTN-Holder cCT-Gradian-Linear-Main cCT-white-text d-flex align-items-center justify-content-center" to='/how-it-works'>
            <i className="iCT-add-white uCT-img-cover iCT-sB"></i>
            <p className='mb-0 ml-2 text-nowrap CT-list-prop-btn'> List Your Property </p>
          </Link >
          {/* Holder Icons */}
          <div className="d-flex align-items-center">
            {/* Notification BTN */}
            <Link to='/notification'>
              <Icon iconName='iCT-notificationblue' bgColor='cCT-opacity-secondary-bg-20' iconSize='iCT-sB' spaces='iCT-sE mx-4' radiusSize='rounded-circle' />
            </Link>
            {/* Personal Information DropMenu */}
            <ul className="navbar-nav">
              <li className="nav-item dropdown">
                {/* Image Profile */}
                <div className="nav-link uCT-click d-flex align-items-center p-0 h-100" href="#" data-toggle="dropdown">
                  <i className="imCT-Profile uCT-img-100 CT-Border-3 rounded-circle iCT-sF mr-2"></i>
                  <i className="iCT-arrowDownblue uCT-img-cover iCT-sA mr-3"></i>
                </div>
                {/* DropMenu */}
                <div className="dropdown-menu CT-profile-menu" aria-labelledby="navbarDropdown">
                  <Link className="dropdown-item" to='login'>Login</Link>
                </div>
              </li>
            </ul>
          </div>
        </div>
      </div>
    );
  };
  return navBarTemplate();
}
