import Icon from 'view/features/shared/Icon/Icon'
import { Link } from "react-router-dom";
export default function AgenciesWireFrame() {
    const agenciesWireFrameTemp = () => {
        return (
            <div className='uCT-card'>
                <div className="w-100 h-100 flex-column uCT-f-radius-sC d-flex align-items-center shadow-sm cCT-layout-I ">
                    {/* Agencies Logo */}
                    <div className="d-flex mb-4 ">
                        <i style={{ height: '226px' }} className="aCT-wire-frame-loading-agencies cCT-layout-II uCT-cardImg-holder uCT-f-radius-smid-A uCT-img-contain mt-4 position-relative">
                            {/* Agencies Rate */}
                            <div className="aCT-wire-frame-loading-agencies cCT-layout-I uCT-StarRate-Holder d-flex shadow-sm  uCT-f-radius-smid-A uCT-StarRate-position justify-content-center align-items-center">
                            </div>
                        </i>
                    </div>
                    {/* Agencies Text Name */}
                    <div className="mt-2 mb-4 h-50">
                        <p style={{ width: '180px' }} className="mb-2 aCT-wire-frame-loading-agencies uCT-f-radius fCT-wireFrame-hG"></p>
                        <p style={{ width: '150px' }} className="mx-3 my-1 aCT-wire-frame-loading-agencies uCT-f-radius fCT-wireFrame-hF "></p>
                    </div>
                    {/* Agencies Details */}
                    <div className="row w-100 d-flex justify-content-around justify-content-center px-2 h-50  ">
                        {/* Agent Holder */}
                        <div className="uCT-f-radius uCT-icon-holder cCT-layout-I">
                            {/* Agent Icon */}
                            <div className=" h-100 uCT-f-radius d-flex aCT-wire-frame-loading-agencies align-items-center justify-content-center  cCT-layout-II">
                            </div>
                        </div>
                        {/* Properties Holder */}
                        <div className="uCT-f-radius uCT-icon-holder cCT-layout-I">
                            {/* Properties Icon */}
                            <div className=" h-100 uCT-f-radius d-flex aCT-wire-frame-loading-agencies align-items-center justify-content-center cCT-layout-II">
                            </div>
                        </div>
                        {/* Available Holder */}
                        <div className="uCT-f-radius uCT-icon-holder cCT-layout-I">
                            {/* Available Icon */}
                            <div className=" h-100 uCT-f-radius d-flex aCT-wire-frame-loading-agencies align-items-center justify-content-center cCT-layout-II">
                            </div>
                        </div>
                    </div>
                    {/* Agencies Ameneties Section */}
                    <div className="d-flex justify-content-center align-items-center h-50~ mt-4 uCT-b-radius-sC w-100 cCT-layout-II">
                    <div style={{ height: '40px' }} className={`mt-4`}></div>
                    </div>
                </div>
            </div>
        )
    }
    return agenciesWireFrameTemp()
}
