import './OurVision.scss'
import OurVisionFeature from 'view/features/deal360/aboutUs/OurVision/OurVision.feature'
export default function OurVision() {
    const ourVisionTemp = () => {
        return (
            <div className="CT-our-vision-holder overflow-hidden cCT-Gradian-Linear-mid-Main uCT-f-radius-sC ">
                <div className="uCT-f-radius-sC ">
                    <OurVisionFeature />
                </div>
            </div>
        )
    }
    return ourVisionTemp()
}