// import { useLocation } from 'react-router-dom'
import React, { useState, useEffect } from 'react';
import Icon from 'view/features/shared/Icon/Icon'
import $ from 'jquery'; 
import './send-message.popup.sass'
export default function SendMessagePopup({togglePopups, APIs = {}}){

    const [APIsBundles, setAPIsBundles] = useState({
          fullname: String,
          image: String
      });

    useEffect(() => {
        switch(APIs.APIsKey){
            case "AgentProfile" :
                setAPIsBundles(prevVal =>({...prevVal, 
                    fullname : APIs?.data?.profile_card?.fullname,
                    image : APIs?.data?.profile_card?.image
                }))
                break;
            case "AgencyProfile" :
                setAPIsBundles(prevVal =>({...prevVal, 
                    fullname : APIs?.data?.profile_card?.fullname,
                    image : APIs?.data?.profile_card?.image
                }))
                break;
        }
      }, [APIs]);

    useEffect(() => {
        switch (togglePopups) {
            case true:
                $('#sendMessageModal').click()
                break;
            case false:
                break;
        }        
    },[togglePopups]);


    const SendMessagePopupTemp = ()=>{
        return(
            <>
                {/* Displayed None */}
                <button  id='sendMessageModal' type="button" className="btn btn-primary d-none" data-toggle="modal" data-target="#exampleModal">
                Launch demo modal
                </button>
                {/* <!-- Modal --> */}
                <div className="modal fade" id="exampleModal" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
                    <div className="modal-dialog modal-dialog-centered">
                        <div className="modal-content">
                            {/* Header Section */}
                            <div className="modal-header">
                                <h5 className="modal-title" id="exampleModalLabel">Send a message to <span className={`cCT-main-text`}>@{APIsBundles.fullname}</span></h5>
                                <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                                <span aria-hidden="true">&times;</span>
                                </button>
                            </div>
                            {/* Content Model Section */}
                            <div className="modal-body cCT-smoke-bg">
                                {/* text area */}
                                <div className='d-flex shadow-sm uCT-f-radius-sA '>
                                    <div className='uCT-l-radius-sA border-0 px-4 py-3 d-flex cCT-white-bg'>
                                        <i className=' uCT-bd-main-sA iCT-x-agentProfile rounded-circle iCT-sE cCT-white-bg uCT-img-contain' style={{backgroundImage: `url('${APIsBundles.image}')`}}></i>
                                    </div>
                                    <textarea name="" id="" placeholder='Write a message...' class='py-4 uCT-r-radius-sA CT-article-text-area w-100 uCT-border-sA border-0 cCT-white-bg m-0'>
                                    </textarea>
                                </div>
                            </div>
                            {/* Footer Section */}
                            <div className="modal-footer">
                            <button type="button" className={`border-0 p-0 uCT-f-radius-sA btn` } data-dismiss="modal">
                                <Icon iconName='iCT-closewhite' spaces='iCT-sE' bgColor='cCT-secondary-bg' radiusSize='uCT-f-radius-sA' iconSize='iCT-sB' />
                            </button>
                            <button className={`btn p-0 border-0`}>
                                <Icon iconName='iCT-messagewhite' spaces='px-md-5 px-4 py-2  fCT-Regular-sC' bgColor='cCT-Gradian-Linear-Main cCT-white-text' radiusSize='uCT-f-radius-sA' iconSize='iCT-sB mr-2' textPosition='right' innerText='Send'/>
                            </button>
                        </div>
                        </div>
                    </div>
                </div>
            </>
        )
    }
    return SendMessagePopupTemp()
}