import { API } from 'environment/environment'

//Reducers
export const PropertiesReducer = (state = {}, action) => {
    switch (action.type) {
        // case 'property/allPropertiesList': state.allPropertiesList = action.payload ; break
        default: return state
    }
    return state
}
// APIs
export const _GET_AllProperties = async (language, paginate, filterVals = {}, currentPage) => {
    language = (typeof language !== 'undefined') ? language : 'en' // language Default
    paginate = (typeof paginate !== 'undefined') ? paginate : 10 // paginate Default
    filterVals.country_name = (typeof filterVals?.country_name !== 'undefined') ? filterVals?.country_name : '' // country_name Default
    filterVals.property_name = (typeof filterVals?.property_name !== 'undefined') ? filterVals?.property_name : '' // property_name Default
    filterVals.category = (typeof filterVals?.category !== 'undefined') ? filterVals?.category : '' // category_id Default
    filterVals.city_id = (typeof filterVals?.city_id !== 'undefined') ? filterVals?.city_id : '' // city_id Default
    filterVals.type_id = (typeof filterVals?.type_id !== 'undefined') ? filterVals?.type_id : '' // type_id Default
    filterVals.amenity_IDs = (typeof filterVals?.amenity_IDs !== 'undefined') ? filterVals?.amenity_IDs: '' // amenity_id Default
    currentPage = (typeof currentPage !== 'undefined') ? currentPage : '' // current Page Default
    var amenity_idIndexes = "";
    for (var i = 0; i < filterVals?.amenity_IDs.length; i++) {
        amenity_idIndexes += `&amenity_id[${i}]=` + filterVals?.amenity_IDs[i];
    }
    filterVals.bedroom = (typeof filterVals?.bedroom !== 'undefined') ? filterVals?.bedroom : '' // bedroom Default
    filterVals.bathroom = (typeof filterVals?.bathroom !== 'undefined') ? filterVals?.bathroom : '' // bedroom Default
    filterVals.max_price = (typeof filterVals?.max_price !== 'undefined') ? filterVals?.max_price : '' // Max Price Default
    filterVals.min_price = (typeof filterVals?.min_price !== 'undefined') ? filterVals?.min_price : '' // Max Price Default
    filterVals.latitude = (typeof filterVals?.latitude !== 'undefined') ? filterVals?.latitude : '' // Max Price Default
    filterVals.longitude = (typeof filterVals?.longitude !== 'undefined') ? filterVals?.longitude : '' // Max Price Default
    filterVals.center_latitude = (typeof filterVals?.center_latitude !== 'undefined') ? filterVals?.center_latitude : '' // Max Price Default
    filterVals.center_longitude = (typeof filterVals?.center_longitude !== 'undefined') ? filterVals?.center_longitude : '' // Max Price Default
    return await API.get(`api/property/index?lang=${language}
    &paginate=${paginate}
    &page=${currentPage}
    &country_name=${filterVals?.country_name}
    &property_name=${filterVals?.property_name}
    &category_id=${filterVals?.category}
    &country_id=&type_id=${filterVals?.type_id}
    &city_id=${filterVals?.city_id}
    &rejoin_name=
    &rejoin_id=
    &high_light_name=
    &high_light_id=
    &min_price=${filterVals?.min_price}
    &max_price=${filterVals?.max_price}
    &bedroom=${filterVals?.bedroom}
    &min_area=&max_area=
    &bathroom=${filterVals?.bathroom}
    &amenity_id=${amenity_idIndexes}
    &amenity_name=
    &filter_rating=
    &filter_new=
    &auth_id=
    &ip_address=
    &latitude=${filterVals.latitude}
    &longitude=${filterVals.longitude}
    &center_latitude=${filterVals.center_latitude}
    &center_longitude=${filterVals.center_longitude}
    `)
}

export const _GETID_Property = async (id, language) => {
    id = (typeof id !== 'undefined') ? id : undefined // id Default
    language = (typeof language !== 'undefined') ? language : 'en' // language Default
    return await API.get(`api/property/profile?lang=${language}&id=${id}&user_auth&auth_id=`)
}

