import './CircleIndicators.feature.scss'
import React, { useState, useEffect } from 'react';
import $, { event } from 'jquery';
export default function CircleIndicatorsFeature({ screen, setChangeScreen }) {
    const [isScrolling, setIsScrolling] = useState(null);

    const mouseDown = e => {
        e.stopPropagation();
        if (screen <= 6 && e.deltaY > 0) {
            e.stopPropagation();
            window.clearTimeout(isScrolling);
            setIsScrolling(
                setTimeout(() => {
                    // Run the callback
                    setChangeScreen(screen += 1)
                }, 1000)
            )
            window.removeEventListener("wheel", mouseDown);
            // scroll up
        } else if (screen > 1 && e.deltaY < 0) {
            e.stopPropagation();
            window.clearTimeout(isScrolling);
            setIsScrolling(
                setTimeout(() => {
                    // Run the callback
                    setChangeScreen(screen -= 1)
                }, 1000)
            )
            window.removeEventListener("wheel", mouseDown);
        }
    }
    window.addEventListener("wheel", mouseDown);
    document.onkeydown = function () {
        switch (window.event.keyCode) {
            case 38:
                if (screen > 1) {
                    setIsScrolling(
                        setTimeout(() => {
                            setChangeScreen(screen -= 1)
                        }, 1000)
                    )
                }
                break;
            case 40:
                if (screen <= 5) {
                    setIsScrolling(
                        setTimeout(() => {
                            setChangeScreen(screen += 1)
                        }, 1000)
                    )
                }
                break;
        }
    };

    // Cancel Event Listener
    useEffect(() => () => {
        window.removeEventListener("wheel", mouseDown);
    }
        , []);
    const circleIndicatorsTemp = () => {
        return (
            <div className="CT-Indicators-holder py-2 py-xl-0  justify-content-center align-items-center" >
                {/* Indicators */}
                {/* <p id="someElement" className={``} onTouchMove={(e) => {console.log(e.changedTouches.length)}} style={{ cursor: 'grabbing' }} >ssss</p> */}
                <div onClick={(e) => { setChangeScreen(1) }} className={`CT-Indicators uCT-t-6-all ${screen == 1 ? 'cCT-main-bg' : false} `}></div>
                <div onClick={(e) => { setChangeScreen(2) }} className={`CT-Indicators uCT-t-6-all ${screen == 2 ? 'cCT-main-bg' : false} `}></div>
                <div onClick={(e) => { setChangeScreen(3) }} className={`CT-Indicators uCT-t-6-all ${screen == 3 ? 'cCT-main-bg' : false} `}></div>
                <div onClick={(e) => { setChangeScreen(4) }} className={`CT-Indicators uCT-t-6-all ${screen == 4 ? 'cCT-main-bg' : false} `}></div>
                <div onClick={(e) => { setChangeScreen(5) }} className={`CT-Indicators uCT-t-6-all ${screen == 5 ? 'cCT-main-bg' : false} `}></div>
                <div onClick={(e) => { setChangeScreen(6) }} className={`CT-Indicators uCT-t-6-all ${screen == 6 ? 'cCT-main-bg' : false} `}></div>
                <div onClick={(e) => { setChangeScreen(7) }} className={`CT-Indicators uCT-t-6-all ${screen == 7 ? 'cCT-main-bg' : false} `}></div>
            </div>
        )
    }
    return circleIndicatorsTemp()
}
