// import { useLocation } from 'react-router-dom'
import Icon from '../../shared/Icon/Icon'
import React, { useState, useRef, useEffect } from 'react';
import { Button, Overlay, Tooltip } from 'react-bootstrap';

export default function ProfiileContentNav({APIs = {}, popupHandlers}){
    const [show, setShow] = useState(false);
    const target = useRef(null);

    const [APIsBundles, setAPIsBundles] = useState({
          website: String,
          phone: String
      });

      useEffect(() => {
          switch(APIs.APIsKey){
              case "AgentProfile" :
                  setAPIsBundles(prevVal =>({...prevVal, 
                      website : APIs?.data?.contact_card?.website,
                      phone : APIs?.data?.contact_card?.phone
                  }))
                  break;
              case "AgencyProfile" :
                  setAPIsBundles(prevVal =>({...prevVal, 
                      website : APIs?.data?.contact_card?.website,
                      phone : APIs?.data?.contact_card?.phone
                  }))
                  break;
          }
      }, [APIs]);

    const profiileContentNavTemp = ()=>{
        return(
            <div className='mt-4 shadow d-flex py-3 justify-content-center cCT-white-bg uCT-f-radius-sB px-4'>
                {/* Message BTN */}
                <button onClick={() => popupHandlers()} className={`btn p-0 border-0`}>
                    <Icon iconName='iCT-messagewhite' spaces='px-md-5 px-4 py-2  mr-3 mr-2~ fCT-Regular-sC' bgColor='cCT-Gradian-Linear-Main cCT-white-text' radiusSize='uCT-f-radius-sA' iconSize='iCT-sB mr-2' textPosition='right' innerText='Message'/>
                </button>

                {/* Website BTN */}
              { APIsBundles.website ?
              <a href={APIsBundles.website} target='_blank'>
                  <Icon iconName='iCT-earthblue' spaces='iCT-sE mr-3 mr-2~' bgColor='cCT-opacity-main-bg-20' radiusSize='uCT-f-radius-sA' iconSize='iCT-sB' />
              </a> 
                : false
              }
              {/* Call BTN */}
              <Button ref={target} onClick={() => setShow(!show)} className={`border-0 p-0 bg-light uCT-f-radius-sA btn`}>
                <Icon iconName='iCT-callgreen' spaces='iCT-sE' bgColor='cCT-opacity-green-bg-20' radiusSize='uCT-f-radius-sA' iconSize='iCT-sB' />
              </Button>
                <Overlay target={target.current} show={show} placement="bottom">
                    {(props) => (
                    <Tooltip id="overlay-example" {...props}>
                        {APIsBundles.phone}
                    </Tooltip>
                    )}
                </Overlay>
            </div>
        )
    }
    return profiileContentNavTemp()
}