import './NotificationTime.sass'
import { giveClassOnScroll, MediaCT } from 'view/viewLogic/view-logic'

export default function NotificationTime() {
    const notificationTimeTemp = () =>{
        return (
            // Current Time
            <div className="my-4 d-flex align-items-center">
                <p className="fCT-Regular-sE mb-0">Today</p>
                <div className={`${MediaCT('md') ? 'CT-notificate-sm-horizLine' : 'CT-notificate-md-horizLine'} ml-5 cCT-Gradian-Linear-Main`}></div>
            </div>
        ) 
    }
    return notificationTimeTemp()
}