import './images-slider.layout.scss'
import React, { useState, useEffect, useRef } from 'react';
import Icon from 'view/features/shared/Icon/Icon'


export default React.memo(function ImagesSlider({_ID, propertyImages = [], rightHandSectionSize = new Object()}) {
    const [count, setCount] = useState(0);
    // console.log(rightHandSectionSize);
    useEffect(() => {
        
        
    }, []);
    
    const ImagesSliderTemp = () => {
        return (
            <div className={`d-flex h-100 w-100 position-relative`} onClick={(e) => {e.preventDefault()}}>
                <div id={`id${_ID}`} className="carousel slide w-100 uCT-f-radius-sB cCT-smoke-bg" data-interval="false">
              
                    {/* Hot Offer Holder */}
                    {/* <div className='ml-auto mr-2 mt-2 position-absolute rounded-circle' style={{zIndex: '1', left: '-13px', top: '-21px'}}>
                        <Icon iconName='iCT-hotoffer-orange' spaces='cCT-gray-text fCT-Regular-sB p-2 ml-auto' iconSize='iCT-sG~ CT-Hotoffer-holder-size mr-auto' radiusSize='rounded-circle' bgColor='cCT-opacity-main-bg-20~ cCT-opacity-white-bg-40~'/>
                    </div> */}
                        {/* Love Icon Holder */}
                    <div className='ml-auto mr-2 mt-2 position-absolute rounded-circle' style={{right: '0', zIndex: '1'}}>
                        <Icon iconName='iCT-heartgray' spaces='' iconSize={`${ rightHandSectionSize?.width < 200 ? 'iCT-sC' : 'iCT-sD'}`} radiusSize='rounded-circle' bgColor='cCT-opacity-main-bg-20~ cCT-opacity-white-bg-40~'/>
                    </div>

                    {/* Pages List */}
                    <ol className="carousel-indicators pb-2 slider-holder">
                        { propertyImages?.slice(0, 8)?.map((image, index, arr) => {
                            return <li onChange={(e) => {console.log('done');}} data-target={`#id${_ID}`}  data-slide-to={index} className={`CT-slider-Indecators ${index==0  ? 'active' : ''}`}></li>
                        })
                        }
                    </ol>
                    {/* Images Holders */}
                    <div className="carousel-inner overflow-hidden~ uCT-f-radius-sB uCT-bd-white-sB~ w-100 h-100">
                        { propertyImages?.slice(0, 8)?.map((image, index, arr) => {
                            return <div className={`carousel-item uCT-img-cover ${index == 0  ? 'active' : ''} h-100`} >
                                        <img className={`w-100`} style={{height: rightHandSectionSize?.height ? rightHandSectionSize?.height : '240' + 'px'}} src={image} ></img>
                                        {/* <i className={`w-100 d-flex uCT-img-cover`} style={{height: rightHandSectionSize?.height ? rightHandSectionSize?.height : '260' + 'px', backgroundImage: `url(${image})`}}  ></i> */}
                                    </div> 
                            }) 
                        }
                    </div>
                    {/* Next And Prev BTN */}
                    { rightHandSectionSize?.width > 300 || !rightHandSectionSize?.width ?
                        <>
                            <a  className="carousel-control-prev" href={`#id${_ID}`}  role="button" data-slide="prev">
                                <div className="bg-dark rounded-circle p-1 d-flex">
                                    <span className="carousel-control-prev-icon" aria-hidden="true"></span>
                                    <span className="sr-only">Previous</span>
                                </div>
                            </a>
                            {/* {count != 'active' ? */}
                            <a className="carousel-control-next" onClick={() => setCount(prevVal => prevVal + 1)}  href={`#id${_ID}`} role="button" data-slide="next">
                                <div className='bg-dark rounded-circle p-1 d-flex'>
                                    <span className="carousel-control-next-icon" aria-hidden="true"></span>
                                    <span className="sr-only">Next</span>
                                </div>
                            </a>
                        </>
                    : false }
                </div>
            </div>   
        )
    }
    return ImagesSliderTemp()
})
