import './NotificationCard.sass'
import { MediaCT } from 'view/viewLogic/view-logic'
export default function NotificationCard() {
    const notificationCardTemp = () => {
        return (
            <div className={`${MediaCT('md') ? 'cCT-Gradian-Linear-supp' : 'cCT-support2-bg'} container p-0 uCT-f-radius-sC d-flex shadow-lg mb-4`}>
                {/* Image Card */}
                <div className={`${MediaCT('md') ? '' : 'cCT-Gradian-Linear-supp' } d-flex align-items-start align-items-md-center uCT-l-radius-sC  pt-2 py-md-4 px-2 px-md-5`}>
                    <i className="imCT-Women-Notificate uCT-img-cover iCT-sG" ></i>
                </div>
                {/* Image Text */}
                <div className="pt-3 pt-md-4 w-100 cCT-dark-text ">
                    {/* Surname */}
                    <p className="fCT-Regular-s m-0">Name Surname</p>
                    {/* Day And Date */}
                        <small className='fCT-Light-sA cCT-gray-text'>Satarday 20/3/2021</small>
                    {/* Notification Text */}
                    <p className={` ${MediaCT('md') ? 'ml-2 w-100 cCT-dark-text' : 'w-75 mx-4'} fCT-Light-sB pt-2`}>Lorem Ipsum is simply dummy text of the printing and typesetting industry.
                        Lorem Ipsum has been the industry's standard dummy text ever since the 1500s,when an unknown printer took a galley
                    </p>
                </div>
                {/* Delete Card Button */}
                <div className="pr-4 pt-3 p-md-4 ">
                <button className="btn p-0 d-flex border-0 p-0~ uCT-r-radius-sC align-items-start justify-content-center cCT-support2-bg">
                        <i className="iCT-dashred uCT-img-cover iCT-sB uCT-click"></i>
                </button>
                </div>
            </div>
        )
    }
    return notificationCardTemp()
}