import './pageHead.sass'
import $ from 'jquery'; 
function PageHead({title,subTitle, pImg}){
    const pageHeadTemp = ()=>{
        return(
            <div className={`${pImg} uCT-img-parallax~ Head-BlackScreen CT-pageHead-holder uCT-img-cover d-flex flex-column justify-content-center`} >
                <div >
                <div className="container cCT-support1-text">
                    <p className="mb-0 h1 fCT-bold-sF">{title}</p>
                    <p className="mb-0 h5 fCT-Regular-sE">{subTitle}</p>
                </div>
                </div>
            </div>
        )
    }
    return pageHeadTemp()
}
export default PageHead