import './content-slider.sass'
import Icon from 'view/features/shared/Icon/Icon'
import React, { useState } from 'react';
import PagesTitle from 'view/features/shared/pagesTitle/PagesTitle'
export default function ContentSlider({ content = [], title}){
    let contentArr = [1, 2]
    const [contentPage, setContentPage] = useState(()=>{return 1})
    const [slidScreen, setslidScreen] = useState(()=>{return 0})
    function move(btnDIR) {
        switch (btnDIR) {
            case 'previous':
                setslidScreen(prevScreen => contentPage > 1 ? prevScreen - 100 : prevScreen)
                setContentPage(prevCount => contentPage > 1 ? prevCount - 1 : prevCount)
                break;
            case 'next':
                setslidScreen(prevScreen => contentPage < contentArr.length ? prevScreen + 100 : prevScreen = 0)
                setContentPage(prevCount => contentPage < contentArr.length ? prevCount+1 : prevCount = 1) 
                break
        }
    }
    const contentSliderTemp = ()=>{
        return (
            <div className='w-100 d-flex flex-column mb-4~ uCT-f-radius-sB'>
                {/* controller */}
                {content[4] ?
                    <nav className='d-flex justify-content-between'>
                            {title ? <PagesTitle title={title} />: <div></div>}
                        <div className='d-flex align-items-center'>
                            <p className='my-0 mx-3 fCT-Light-sE'>{contentPage} / {contentArr.length}</p>
                            {/* previous button */}
                            <button onClick={() => { move('previous') }} className='btn m-0 p-0' >
                                <Icon iconName='iCT-slidersArrowLeft' spaces='p-3 mr-3' bgColor='cCT-white-bg shadow-sm' iconSize='iCT-sB' radiusSize='rounded-circle' />
                            </button>
                            {/* next button */}
                            <button onClick={() => { move('next')}} className='btn m-0 p-0'>
                                <Icon iconName='iCT-slidersArrowRight' spaces='p-3' bgColor='cCT-white-bg shadow-sm' iconSize='iCT-sB' radiusSize='rounded-circle' />
                            </button>
                        </div>
                    </nav>
                :false}
                {/* slider screens holder */}
                <div className='w-100 h-100 mt-4~ px-0 container-fluid '>
                    <div className='row h-100 m-0~ flex-nowrap py-4 overflow-hidden uCT-f-radius-sB pl-2'>
                        {/* slider screens */}
                        <div style={{ right: slidScreen + '%' }} className={` CT-slider-motion h-100 col-12 p-0 h-100 uCT-card-grid-holder-slider ${content[4] ? 'justify-content-center':false}`}>
                            {content?.slice(0, 4).map((elem) => {
                                return elem
                            })}
                        </div>
                        {content[4]  ?
                            <div style={{ right: slidScreen + '%' }} className={` CT-slider-motion h-100 col-12 p-0 h-100 uCT-card-grid-holder-slider ${content[8] ? 'justify-content-center' : false}`}>
                                {content?.slice(4, 8).map((elem) => {
                                    return elem
                                })
                                }
                            </div>
                        :false}
                    </div>
                </div>
            </div>
        )
    }
    return contentSliderTemp()
}