import './PropertyGallery.sass'
// import { giveClassOnScroll, MediaCT } from 'view/viewLogic/view-logic'
import LightBoxPopup  from 'view/layout/Components/popups/LightBoxPopup/LightBox.popups'
import { useState, useEffect } from 'react'
import { useMediaQuery } from 'react-responsive';

export default function PropertyGallery({APIs = {}}) {
    const mediaLG = useMediaQuery({ query: `(max-width: 992px)` })
    const [showAllPhotos, setShowAllPhotos] = useState('close')
    const closeButton = (<i onClick={() => btnShowImages()} className={`iCT-sE uCT-img-cover iCT-closewhite`}></i>)
    const [APIsBundles, setAPIsBundles] = useState({
        images: [],
        virtual_tour: ''
    });
    useEffect(() => {
        switch(APIs.APIsKey){
            case "PropertyDetails" :
                setAPIsBundles(prevVal =>({...prevVal, 
                    virtual_tour: APIs?.data?.virtual_tour,
                    images : APIs?.data?.images
                }))
                break;
        }
        console.log('here', APIs)
    }, [APIs]);

    let btnShowImages = () => {
        switch (showAllPhotos) {
            case 'open':
                setShowAllPhotos('close')
                break;
            case 'close':
                setShowAllPhotos('open')
                break;
        }
    }

    const propertyGalleryTemp = () => {
        return (
            <div className="uCT-f-radius-sC">
                {/* Video 360 Section small screen */}
                <div className={`d-flex m-0 CT-galleryHolder d-lg-none`}>
                    <div className={`overflow-hidden w-100~ m-0 imCT-homeCoverPageImg~ uCT-img-cover uCT-bd-white-sB shadow ${mediaLG ? 'w-100 CT-gallery-grid-sm-holder~ uCT-f-radius-smid-A mt-4' : 'd-none'}`}>
                        <iframe src={APIsBundles?.virtual_tour} frameborder="0" className='w-100 h-100 p-0 uCT-l-radius-sC'></iframe>
                    </div>
                </div>
                {/* Property Gallery Holder */}
                <div className={`${mediaLG ? 'm-0 mt-3 CT-list-gallery-holder uCT-f-radius-smid-A' : 'CT-gallery-holder'}`}>
                    {/* Main Video 360 Section */}
                    <div className={`overflow-hidden imCT-homeCoverPageImg~ uCT-img-cover uCT-bd-white-sB shadow ${mediaLG ? 'w-0 d-none' : 'CT-virtual-360 uCT-l-radius-sC'}`} >
                        {/* <iframe src={APIsBundles?.virtual_tour} frameborder="0" className='w-100 h-100 p-0'></iframe> */}
                        <iframe src={APIsBundles?.virtual_tour} frameborder="0"  className='w-100 h-100 p-0 uCT-l-radius-sC'></iframe>
                    </div>
                    {/* Main Images Section */}
                    <div className={`w-100~ uCT-f-radius-smid-A ${mediaLG ? 'CT-gallery-grid-sm-holder overflow-auto d-flex uCT-f-radius-smid-A' : 'CT-prop-gallery-grid  position-relative'}`}>
                        {/* Simi Main Images */}
                        { APIsBundles?.images?.slice(0,1).map((image, i) => {
                            return (
                                <div key={i} className={`uCT-click imCT-Profile~ uCT-img-cover uCT-bd-white-sB shadow ${mediaLG ? 'mr-3 CT-list-gallery-grid CT-gallery-grid-sm-holder uCT-f-radius-smid-A' : ' prop-mini-card'}`} >
                                    <LightBoxPopup closeButton={closeButton} image={image} multiImages={APIsBundles.images}/>
                                </div>
                                )
                            })}
                        { APIsBundles?.images?.slice(1,2).map((image, i) => {
                            return (
                                <div key={i} className={`imCT-Profile~ uCT-click uCT-img-cover uCT-bd-white-sB shadow ${mediaLG ? 'mr-3 CT-gallery-grid-sm-holder uCT-f-radius-smid-A' : ' prop-mini-card-2'}`} >
                                    <LightBoxPopup closeButton={closeButton} image={image} multiImages={APIsBundles.images}/>
                                </div>
                                )
                            })}
                        { APIsBundles?.images?.slice(2, !mediaLG ? 6 : APIsBundles?.image?.length).map((image, i) => {
                            return (
                                <div key={i} className="uCT-img-cover uCT-click uCT-bd-white-sB shadow mr-3 mr-lg-0 CT-gallery-grid-sm-holder prop-small-card" >
                                    <LightBoxPopup closeButton={closeButton} btnToggle={showAllPhotos} btnEventCheck={btnShowImages} image={image} multiImages={APIsBundles.images}/>
                                </div>
                                )
                            })}
                        {/* Show More Button */}
                        { APIsBundles?.images?.length >= 7 ?
                        <button onClick={() => btnShowImages()} className={`cCT-Gradian-Linear-Main py-2 px-4 text-uppercase fCT-Regular-sA cCT-white-text uCT-f-radius-smid-A  ${mediaLG ? ' btn CT-list-gallery-grid-button' : 'btn CT-btnPosition'} `} >
                            <p className="m-0 text-center ">show all photos</p>
                        </button> : false
                        }
                    </div>
                </div>
            </div>
        );
    }
    return propertyGalleryTemp()
}