import { API } from "../../../environment/environment";


export const subscribeReducer = (state = {}, action) => {
    switch (action.type) {

        default:
            return state;
    }
    return state
}

export const _GET_Subscribe = async (email) => {
    const requestOption = { email: email }
    return await API.post(`api/contacts/subscribe`, requestOption)
}