import './left-widget-layout.sass'
import React, { useEffect } from 'react';
import { SideNavScreenHeight, MediaCT} from 'view/viewLogic/view-logic'
export default function LeftWidgetLayout({content}){
    // USE_Effect call after render 
    useEffect(() => {
        window.addEventListener('resize', onResize);
        function onResize() {
            SideNavScreenHeight('leftWidget', 'giveMeSpace')
        }
        SideNavScreenHeight('leftWidget', 'giveMeSpace')
    });
    const leftWidgetLayoutTemp = () =>{
        return(
            <aside id='leftWidget' className=
            {`
                ${MediaCT('lg') ? 'd-flex w-100 justify-content-center' : 'pr-4'}
                CT-left-widget h-100 overflow-auto
            `}>
                {content}
                <div id='giveMeSpace' className='d-none'></div>
            </aside>
        )
    }
    return leftWidgetLayoutTemp()

}