import Icon from 'view/features/shared/Icon/Icon'
import './AgenciesCard.sass'
import { Link } from "react-router-dom";
import React, { useState, useEffect } from 'react';


export default function AgencieCard({APIs = {}}){

    const [APIsBundles, setAPIsBundles] = useState({
    id: String,
    image: String,
    avg_rating: Number,
    fullname: String,
    country: String,
    count_agents: String,
    count_properties: String,
    status_work: String,
    buy_count: String,
    rent_count: String,
    commercial_count: String
    });
    useEffect(() => {
        switch(APIs.from){
            case "AgenciesList" :
                setAPIsBundles(prevVal =>({...prevVal, 
                    id : APIs?.data?.agency?.id,
                    image : APIs?.data?.agency?.image,
                    avg_rating : APIs?.data?.agency?.avg_rating,
                    fullname : APIs?.data?.agency?.fullname,
                    country : APIs?.data?.agency?.country?.title,
                    count_agents : APIs?.data?.agency?.count_agents,
                    count_properties : APIs?.data?.agency?.count_properties,
                    status_work : APIs?.data?.agency?.status_work,
                    buy_count : APIs?.data?.agency?.buy_count,
                    rent_count : APIs?.data?.agency?.rent_count,
                    commercial_count : APIs?.data?.agency?.commercial_count
                }))
                break;
        }
    }, [APIs]);
    const agencieCardTemp = () =>{
        return (
            <Link to={`/agency/${APIsBundles.id}`} className='uCT-card uCT-scale-in-animation uCT-click'>
                <div className="w-100 h-100 flex-column uCT-f-radius-sC cCT-white-bg d-flex align-items-center shadow ">
                    {/* Agencies Logo */}
                    <div className="d-flex mb-4 ">
                        <i style={{ backgroundImage: `url(${APIsBundles.image})` }} className="imCT-LoremIpsum-Img uCT-cardImg-holder uCT-f-radius-smid-A uCT-img-contain mt-4 position-relative">
                            {/* Agencies Rate */}
                            <div className="d-flex cCT-dark-bg shadow uCT-StarRate-Holder uCT-f-radius-smid-A uCT-StarRate-position justify-content-center align-items-center uCT-border-sB">
                                <Icon  iconName  = 'iCT-goldStar' iconSize  = 'iCT-sB ml-1' textPosition = 'left' innerText = {APIsBundles.avg_rating} textSpaces= 'mt-1 cCT-white-text fCT-bold-sA' />
                            </div>
                        </i>
                    </div>
                    {/* Agencies Text Name */}
                    <div className="text-center text-uppercase mt-2 mb-4 h-50">
                        <p className="fCT-bold-sE cCT-dark-text mb-0 text-capitalize">{APIsBundles.fullname}</p>
                        <p className="fCT-Regular-sB cCT-gray-text my-1 text-capitalize">{APIsBundles.country}</p>
                    </div>
                    {/* Agencies Details */}
                    <div className="row w-100 d-flex justify-content-around justify-content-center px-2 h-50  ">
                        {/* Agent Holder */}
                        <div className="uCT-icon-holder">
                            {/* Agent Icon */}
                            <div className="uCT-click cCT-support2-bg uCT-f-radius-smid-A uCT-bg h-100 d-flex align-items-center justify-content-center  ">
                                <i className="w-100 d-flex iCT-sB uCT-img-contain iCT-Agentsblue"></i>
                            </div>
                            {/* Agent Count */}
                            <div className="mt-2 d-flex justify-content-center justify-content-center">
                                <p className="m-0 mr-1 fCT-Regular-sA cCT-secondaryA-tx">{APIsBundles.count_agents}</p>
                                <p className="m-0 fCT-Regular-sA cCT-secondaryA-tx">Agent</p>
                            </div>
                        </div>
                        {/* Properties Holder */}
                        <div className="uCT-icon-holder">
                            {/* Properties Icon */}
                            <div className="uCT-click cCT-support2-bg uCT-f-radius-smid-A uCT-bg h-100 d-flex align-items-center justify-content-center ">
                                <i className="w-100 d-flex iCT-sB uCT-img-contain iCT-Propertiesmoove"></i>
                            </div>
                            {/* Properties Count */}
                            <div className="mt-2 d-flex justify-content-center justify-content-center">
                                <p className="m-0 mr-1 fCT-Regular-sA cCT-secondaryA-tx">{APIsBundles.count_properties}</p>
                                <p className="m-0 fCT-Regular-sA cCT-secondaryA-tx">Properties</p>
                            </div>
                        </div>
                        {/* Available Holder */}
                        <div className="uCT-icon-holder">
                            {/* Available Icon */}
                            <div className="uCT-click cCT-opacity-green-bg-20 uCT-f-radius-smid-A uCT-bg h-100 d-flex align-items-center justify-content-center ">
                                <i className="w-100 d-flex iCT-sB uCT-img-contain iCT-availablegreen"></i>
                            </div>
                            {/* Available Count */}
                            <div className="mt-2 d-flex justify-content-center justify-content-center">
                                {/* <p className="m-0 mr-1 fCT-Regular-sA cCT-secondaryA-tx"></p> */}
                                <p className="m-0 fCT-Regular-sA cCT-green-text text-capitalize">{APIsBundles.status_work}</p>
                            </div>
                        </div>
                    </div>
                    {/* Agencies Ameneties Section */}
                    <div className="d-flex justify-content-center align-items-center h-50 mt-4 uCT-b-radius-sC w-100 cCT-support2-bg fCT-Regular-sA">
                        <div className="d-flex flex-column justify-content-start">
                            <p className='mb-0'>{APIsBundles.buy_count}</p>
                            <p className='mb-0 cCT-gray-text'>Buy</p>
                        </div>
                            <div className='mb-0 uCT-Horizontal-Line mx-4'></div>
                        <div className="d-flex flex-column">
                            <p className='mb-0'>{APIsBundles.rent_count}</p>
                            <p className='mb-0 cCT-gray-text'>Rent</p>
                        </div>
                            <div className='mb-0 uCT-Horizontal-Line mx-4'></div>
                        <div className="d-flex flex-column">
                            <p className='mb-0'>{APIsBundles.commercial_count}</p>
                            <p className='mb-0 cCT-gray-text'>Commercial</p>
                        </div>
                    </div>
                </div>
            </Link>
        )
    }
    return agencieCardTemp()
}
