import PropertyListCard from 'view/features/property/Property-List-Card/PropertyListCard'
import PropertySMListCard from 'view/features/property/Property-SM-List-View/PropertySMListCard'
import PagesTitle from 'view/features/shared/pagesTitle/PagesTitle'
import Icon from 'view/features/shared/Icon/Icon'
import PaginationFeature from 'view/features/shared/pagination/Pagination.feature'
import HorizentalLine from 'view/features/shared/HorizentalLine/HorizentalLine'
import React, { useState, useEffect, useRef } from 'react'
import './PropertyMapView.sass'
import $   from 'jquery'; 
import {  MediaCT } from 'view/viewLogic/view-logic'
import GoogleMapView from 'view/features/google-map/Google-Map/GoogleMapView'

import { _GET_AllProperties } from 'Controllers/services/property/property.services'
import {useDispatch } from 'react-redux';

export default function MapView(){

    const [APIsKey, setAPIsKey] = useState('MapView')
    const dispatch = useDispatch()
    const [allPropertiesList, setAllPropertiesList] = useState([])
    const [currentPage, setCurrentPage] = useState()
    let [positionMovement, setPositionMovement] = useState(0)
    let [switchSection, setswitchSection] = useState('140px')
    let [overFlowHidden, setoverFlowHidden] = useState('auto')
    let [onHover, setOnHover] = useState()
    const [geoGraph, setGeoGraph] = useState({
        latitude : String,
        longitude : String,
        center_latitude : String,
        center_longitude : String,
    })   
    console.log(currentPage);
    console.log(allPropertiesList);
    // APIs
    useEffect(() => {
        _GET_AllProperties('en', 50, geoGraph ? geoGraph : '' , currentPage).then(res => {
            setAllPropertiesList(res)
            console.log(res)
            window.scrollTo(0, 0)
        }).catch()
        console.log(geoGraph);
    }, [geoGraph, currentPage])
    useEffect(() => {
        dispatch({type: 'route/navPosition', payload: true })
    }, []);
    useEffect(() => () => {
        dispatch({type: 'route/navPosition', payload: false })
    }, []);
    
    const btnToggle = _ => {
        switch(positionMovement){
            case 0:
                setPositionMovement(positionMovement = -5)
                setTimeout(() => {
                    setPositionMovement(positionMovement = 50)
                }, 300);
                break;
            case 50:
                setPositionMovement(positionMovement = -15)
                setTimeout(() => {
                    setPositionMovement(positionMovement = 0)
                }, 400);
            break;
        }
    }
    const sectionToggle = _ => {
        // Change Height In Small Screen Properties
        switch(switchSection){
            case '140px':
                setswitchSection('calc(100vh - 140px)')
                break;
            case 'calc(100vh - 140px)':
                setswitchSection('140px')
                break;
        }
        switch(overFlowHidden){
            case 'auto':
                $(`body`).css('overflow', 'hidden')
                setoverFlowHidden('hidden')
                break;
            case 'hidden':
                $(`body`).css('overflow', 'auto')
                setoverFlowHidden('auto')
                break;
        }
    }
    const clickableBundlesHandler = () => {
        sectionToggle()
        btnToggle()
    }

    const PropertyMapViewTemp = _ => {
        return(
            <div className='container-fluid p-0 '>
                <section className='CT-parent-holder p-0 d-flex' >
                    {/* Property Section View */}
                    <div className={`${MediaCT('xl') ? `CT-Properties-Holder-sm w-100 uCT-t-radius-sC overflow-hidden cCT-support2-bg px-2` : 'CT-Properties-Holder overflow-auto px-4'} shadow-sm  pb-4 `} 
                    style={{marginRight: -positionMovement + '%', right: positionMovement+'%', top: MediaCT('xl') ? switchSection : '0%'}}>
                        {/* Page Title Section 1st Section */}
                        <div className={`mt-4 d-flex flex-column`}>
                            {/* Dashed BTN CLickable */}
                            { MediaCT('xl') ?
                            <div className="w-100 d-flex justify-content-center">
                                <button onClick={ _ => clickableBundlesHandler()} className='iCT-dashlonggray border-0 btn px-5 py-1'></button>
                            </div> : false
                            }
                            <PagesTitle title='Find Your Home Easy'/>
                        </div>
                        {/* Search Input 2nd Section */}
                        <div className="d-flex flex-column">
                            {/* Filter BTN */}
                            <div className={`d-flex flex-wrap mb-2 fCT-Regular-sB`}>
                                <div className={`cCT-inherit-bg mb-3 mr-2 w-100~ shadow-sm  uCT-f-radius-sC px-4 py-2 CT-Border uCT-click`} >
                                    <p className={`mb-0 cCT-dark-text w-100`}>Cancellation flexibility</p>
                                </div>
                                <div className={`cCT-inherit-bg mb-3 mr-2 w-100~ shadow-sm  uCT-f-radius-sC px-4 py-2 CT-Border uCT-click`} >
                                    <p className={`mb-0 cCT-dark-text w-100`}>Type of place</p>
                                </div>
                                <div className={`cCT-inherit-bg mb-3 mr-2 w-100~ shadow-sm  uCT-f-radius-sC px-4 py-2 CT-Border uCT-click`} >
                                    <p className={`mb-0 cCT-dark-text w-100`}>Price</p>
                                </div>
                                <div className={`cCT-inherit-bg mb-3 mr-2 w-100~ shadow-sm  uCT-f-radius-sC px-4 py-2 CT-Border uCT-click`} >
                                    <p className={`mb-0 cCT-dark-text w-100`}>Rooms and beds</p>
                                </div>
                                <div className={`cCT-inherit-bg mb-3 mr-2 w-100~ shadow-sm  uCT-f-radius-sC px-4 py-2 CT-Border uCT-click`} >
                                    <p className={`mb-0 cCT-dark-text w-100`}>More filters</p>
                                </div>
                            </div>
                            {/* Search Icone Holder */}
                            <div className='shadow-sm d-flex w-100 uCT-f-radius-sC cCT-Gradian-Linear-mid-Main'>
                                <Icon iconName='iCT-searchblue' spaces='pl-4 py-3 pr-2' iconSize='iCT-sB' />
                                {/* Search Bar */}
                                <input className="cCT-inherit-bg border-0" placeholder='Search...' type="text" />
                            </div>
                        </div>
                        {/* Property Seaction Title */}
                        <div className="">
                            <PagesTitle title='Properties in Abu Dhabi' smallUnderTitle={`${allPropertiesList?.data?.data?.paginate?.total} Property`}/>
                        </div>
                        {/* Property List Cards */}
                            <div className='d-flex uCT-card-grid-holder flex-column pr-4~'>
                                {allPropertiesList?.data?.data?.properties?.slice(0, 15).map((property, index) => {
                                    return (
                                        <div className="" onMouseEnter={() => setOnHover(property?.id)} onMouseLeave={() => setOnHover( )}>
                                            <PropertyListCard  key={property?.id} APIs={{ APIsKey, data: { property: property } }} index={index}/>
                                        </div>
                                    )
                                })}
                            </div>
                         
                        {/* Pagination Section */}
                        {allPropertiesList?.data?.data?.paginate ?
                            <div className='d-flex justify-content-center mt-4 p-5 cCT-opacity-main-bg-40~ uCT-f-radius-sC mx-5~ shadow-sm'>
                                <PaginationFeature APIs={{APIsKey, data: { pagination: allPropertiesList?.data?.data?.paginate } }} currentPage={setCurrentPage}/>
                            </div>
                        :false}
                    </div> 
                    {/* MapView Section Holder */}
                    <div className={`CT-MapView-Parent-Holder overflow-hidden flex-grow-1`}>
                        { !MediaCT('xl') ?
                        <button onClick={clickableBundlesHandler} className={`CT-btnToggle-Map border-0 btn p-2 uCT-f-radius-sA cCT-dark-bg shadow-sm uCT-t-2-all`} 
                        style={{left: positionMovement == -5 ? '100px' : positionMovement == -15 ? '100px' : '10px', 
                        width: positionMovement == -5 ? '100px' : positionMovement == -15 ? '100px' : '50px'}}>
                            <Icon iconName={'iCT-slidersArrowRightWhite'} iconSize={`${positionMovement == 0 ? 'CT-rotate-icon-l' : 'CT-rotate-icon-r' } iCT-sD uCT-t-2-all`}/>
                        </button> : false }
                        <GoogleMapView APIs={allPropertiesList?.data?.data?.properties} setGeoGraph={setGeoGraph} hoverPropertyID={onHover} onResize={[switchSection, positionMovement]}/>
                    </div>
                </section>
            </div>
        )
    }
    return PropertyMapViewTemp()
}
