import { NavLink } from 'react-router-dom';
import LoginForm from '../Login-Form/LoginForm'
import RegisterForm from '../Register-Form/RegisterForm'
import $ from 'jquery';
// import React, { useEffect } from 'react';
import { useLocation } from 'react-router-dom'
import { useDispatch } from 'react-redux';
import { useEffect } from 'react'
import './Auth-Holder.sass'
export default function AuthHolder() {
    const location = useLocation();
    const dispatch = useDispatch()
    useEffect(() => {
        dispatch({ type: 'route/layout', payload: true})
    }, []);
    useEffect(() => () => dispatch({ type: 'route/layout', payload: false}), []);
    const authHolderTemp = () => {
        return (
            <div className="">
                <div className="container-fluid px-0 text-capitalize CT-auth-height-holder">
                    <div className="row d-flex w-100 m-0 h-100 ">
                        {/* Man With Glasses Virtual */}
                        <div className="d-none d-lg-flex flex-column w-50">
                            <i className="imCT-login-virtual uCT-img-cover uCT-Man-Virtual "> </i>
                        </div>
                        {/* Login Content */}
                        <div className="d-flex flex-column d-sm-block CT-loginForm-holder-width CT-login-bg-color~ align-items-start justify-content-center pl-md-4">
                            {/* Main Dark Logo */}
                            <div className="flex-column pl-4 pl-md-0 flex-sm-row d-flex pt-4 pb-3 py-md-5 align-items-center align-items-md-center justify-content-start">
                                <p className="d-flex fCT-Regular-sF m-0">welcome to</p>
                                <i className="CT-Nav-logo uCT-img-100 imCT-darkLogo"></i>
                            </div>
                            {/* Switch Buttons */}
                            <nav>
                                <div className="nav px-4 pl-md-0" id="nav-tab" role="tablist">
                                    {/* Sign In */}
                                    <NavLink to="/login" exact className=" nav-link m-0 px-0 pb-0 cCT-gray-text nav-link cCT-Main-Hover px-0 CT-loginNav-fontSize fCT-Light-sE" id="nav-home-tab" data-toggle="tab" href="#nav-home" role="tab" aria-controls="nav-home" aria-selected="true" activeStyle={{ color: "#4bbef7", borderBottom: "1px solid #4bbef7" }}>
                                        Sign In
                                    </NavLink>
                                    {/* Create Account */}
                                    <NavLink to="/register" exact className="nav-link m-0 pb-0 px-0 cCT-gray-text nav-link px-0 ml-4 cCT-Main-Hover CT-loginNav-fontSize fCT-Light-sE" id="nav-profile-tab" data-toggle="tab" href="#nav-profile" role="tab" aria-controls="nav-profile" aria-selected="false" activeStyle={{ color: "#4bbef7", borderBottom: "1px solid #4bbef7" }}>
                                        Create Account
                                    </NavLink>
                                </div>
                            </nav>
                            <div className="tab-content CT-loginForm-holder-width mt-4 px-4 pl-md-0" id="nav-tabContent ">
                                {/* Sign In Page */}
                                <div id='loginForm' className={`w-100 tab-pane fade ${location.pathname === '/login' ? 'show active' : false}`} id="nav-home" role="tabpanel" aria-labelledby="nav-home-tab">
                                    <LoginForm />
                                </div>
                            </div>
                            <div className="tab-content CT-loginForm-holder-width mt-4 px-4 pl-md-0 " id="nav-tabContent">
                                {/* Sign In Page */}
                                {location.pathname === '/register' ? $('#registerForm').addClass('show active') : $('#loginForm').removeClass('show active')}
                                <div id='registerForm' className={`w-100 tab-pane fade ${location.pathname === '/register' ? 'show active' : false}`} id="nav-profile" role="tabpanel" aria-labelledby="nav-profile-tab">
                                    <RegisterForm />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

            </div>
        )

    }
    return authHolderTemp()
}