import './agecny-profile.sass'
import React, { useState, useEffect } from 'react';
import AgencyProfileCard from 'view/features/profiles/Profile-Card/agency/AgencyProfileCard'
import ProfiileContentNav from 'view/features/profiles/Profile-Content-Nav/ProfileContentNav'
import ProfileSideNavWireFrame from 'view/features/profiles/Profiile-Side-Nav/ProfileSideNav-WireFrame/ProfileSideNav.WireFrame'
import ProfiileContentNavWireFrame from '../../../features/profiles/Profile-Content-Nav/ProfileContentNav-WireFrame/ProfileContentNav.WireFrame'
import ProfileCover from 'view/features/profiles/Profile-Cover/ProfileCover'
import PagesTitle from 'view/features/shared/pagesTitle/PagesTitle'
import HorizentalLine from 'view/features/shared/HorizentalLine/HorizentalLine'
import ProfiileSideNav from 'view/features/profiles/Profiile-Side-Nav/ProfiileSideNav'
import Overview from 'view/features/profiles/Overview/Overview'
import RatingOverview from 'view/features/profiles/Rating-Overview/RatingOverview'
import Contacts from 'view/features/profiles/Contacts/Contacts'
import Specialties from 'view/features/profiles/Specialties/Specialties'
import ProfiileAbout from 'view/features/profiles/Profile-About/ProfileAbout'
import ProfiileMiniNav from 'view/features/profiles/Profiile-Mini-Nav/ProfileMiniNav'
import PropertyGridCard from 'view/features/property/Property-Grid-Card/PropertyGridCard'
import ButtonRightIcon from 'view/features/shared/buttons/Button-Right-Icon/ButtonRightIcon'
import MiniAgentCard from 'view/features/profiles/Mini-Agent-Card/MiniAgentCard'
import MiniAgentCardWireFrame from 'view/features/profiles/Mini-Agent-Card/MiniAgentCard-WireFrame/MiniAgentCard.WireFrame'
import ReviewCard from 'view/features/profiles/Review-Card/ReviewCard'
import ReviewCardWireFrame from 'view/features/profiles/Review-Card/ReviewCard-WireFrame/ReviewCard.WireFrame'
import { giveClassOnScroll, MediaCT } from 'view/viewLogic/view-logic'
import LeftWidgetLayout from 'view/layout/Components/widgets/Left-Widget/LeftWidget.layout'
import SendMessagePopup from 'view/layout/Components/popups/SendMessage/SendMessage.popup'
import PageTitleWireframe from 'view/features/shared/pagesTitle/PageTitle-wireFrame/PageTitle.wireframe'
import PropertyCardWireFrame from 'view/features/property/Property-Grid-Card/PropertyCard-wireframe/PropertyCard.wireframe'
import ProfileCoverWireFrame from 'view/features/profiles/Profile-Cover/ProfileCover-WireFrame/ProfileCover.WireFrame'
import ProfileAboutWireFrame from 'view/features/profiles/Profile-About/ProfileAbout-WireFrame/ProfileAbout.WireFrame'
import OverviewWireFrame from 'view/features/profiles/Overview/Overview-WireFrame/Overview.WireFrame'
import RatingOverviewWireFrame from 'view/features/profiles/Rating-Overview/RatingOverview-WireFrame/RatingOverview.WireFrame'
import ContactsWireFrame from 'view/features/profiles/Contacts/Contacts-WireFrame/Contacts.WireFrame'
import SpecialtiesWireFrame from 'view/features/profiles/Specialties/Specialties-WireFrame/Specialties.WireFrame'
import AgentProfileWireFrame from 'view/features/profiles/Profile-Card/agent/AgentProfile-WireFrame/AgentProfile.WireFrame'
import PlaceHolder from 'view/features/shared/PlaceHolder/PlaceHolder.feature'


import { useMediaQuery } from 'react-responsive';
import { _GETID_Agency, _GET_Agency_Properties } from 'Controllers/services/agency/agency.services'
import { useParams } from "react-router-dom";

export default function AgencyProfile() {
    const [APIsKey, setAPIsKey] = useState('AgencyProfile')
    const [togglePopup, setTogglePopup] = useState(false);
    const [checkEmpty, setCheckEmpty] = useState(new Object({
        properties : false,
        agents : false,
        agency_reviews : false
    }));

    function onScroll() {
        giveClassOnScroll('propNavHolder', 'p-4 shadow cCT-Gradian-Linear-supp2', 'cCT-smoke-bg', 80)
        giveClassOnScroll('propNavWhiteHolder', '', 'cCT-white-bg mb-4 shadow-sm', 104)
    }

    const popupHandler = () => {
        setTogglePopup(true)
        setTimeout(() => {
            setTogglePopup(false)
        }, 10);
    }

    let MediaCT768 = useMediaQuery({ query: `(max-width: 768px)` })
    const [agencyById, setAgencyById] = useState()
    const [agencyProperties, setAgencyProperties] = useState()

    let { id } = useParams();

    // GET API
    useEffect(() => {
        _GETID_Agency(id, 4).then(res => {
            setAgencyById(res)
            setCheckEmpty(prevVal => ({
                ...prevVal,
                agents : res?.data?.data?.agents.length != 0 && true,
                agency_reviews : res?.data?.data?.reviews?.all_review.length != 0 && true,
            }))
            console.log(res);
        }).catch()
        _GET_Agency_Properties(id, 4, 1, 1).then(res =>{
            setAgencyProperties(res)
            setCheckEmpty(prevVal => ({
                ...prevVal,
                properties : res?.data?.data?.properties.length != 0 && true,
            }))
        }).catch()
    },[id])
    window.scrollTo(0, 0)   

    // USE_Effect call after render 
    useEffect(() => {
        !MediaCT768 && window.addEventListener('scroll', onScroll);
        clearCacheData()
        return () => {
            window.removeEventListener('scroll', onScroll)
        }
    }, []);
    function clearCacheData(){
        caches.keys().then((names) => {
            console.log(names)
            names.forEach((name) => {
                caches.delete(name);
            });
        });
        // alert('Complete Cache Cleared')
    };

    // WireFrame Loop



    const leftWidgetLayoutContent = () => {
        return (
            <aside className={`${MediaCT('md') ? 'w-100' : false}`}>
                {/* profile Card */}
                {agencyById ?
                    <>
                        {/* profile Content */}
                        <AgencyProfileCard APIs={{ from: 'AgencyProfile', data: { profileCard: agencyById?.data?.data?.profile_card } }} />
                        {/* profile nav */}
                        <ProfiileContentNav APIs={{ APIsKey, data: { contact_card: agencyById?.data?.data?.contact_card } }} popupHandlers={popupHandler} />
                        {/* profile Card */}
                        <ProfiileSideNav />
                    </>
                    :
                    <>
                        <AgentProfileWireFrame />
                        <ProfiileContentNavWireFrame />
                        <ProfileSideNavWireFrame />
                    </>
                }
            </aside>
        )
    }
    const agencyProfileTemp = () => {
        return (
            <div className='container p-md-4 p-3'>
                <div className='row m-0 flex-md-row flex-column'>
                    {/* Popup Section */}
                    <div>
                        <SendMessagePopup togglePopups={togglePopup} APIs={{ APIsKey, data: { profile_card: agencyById?.data?.data?.profile_card } }} />
                    </div>
                    {/* left widget */}
                    <LeftWidgetLayout content={leftWidgetLayoutContent()} />
                    {/* content */}
                    <section className='uCT-content-with-widget'>
                        {/* cover */}
                        <div>
                            {agencyById ?
                            <ProfileCover APIs={{ APIsKey, data: { image: agencyById?.data?.data?.image } }} />
                            :
                            <ProfileCoverWireFrame />}
                        </div>
                        {/* About Us */}
                        <div>
                            {agencyById ?
                                <>
                                    <PagesTitle title='About Us' />
                                    <ProfiileAbout APIs={{ APIsKey, data: { about_me: agencyById?.data?.data?.about_me } }} />
                                </>
                                :
                                <>
                                    <PageTitleWireframe />
                                    <ProfileAboutWireFrame />
                                </>
                            }
                        </div>
                        {/* Overview */}
                        <div>
                            {agencyById ?
                                <>
                                    <PagesTitle title='Overview' />
                                    <Overview APIs={{ APIsKey, data: agencyById?.data?.data }} />
                                </>
                                :
                                <>
                                    <PageTitleWireframe />
                                    <OverviewWireFrame />
                                </>

                            }
                        </div>
                        {/* Specialties */}
                        <div>
                            {agencyById ?
                                <>
                                    <PagesTitle title='Specialties & Service Areas' />
                                    <Specialties />
                                </>
                                :
                                <>
                                    <PageTitleWireframe />
                                    <SpecialtiesWireFrame />
                                </>
                            }
                        </div>
                        {/* Contacts */}
                        <div>
                            {agencyById ?
                                <>
                                    <PagesTitle title='Contacts' />
                                    <Contacts APIs={{ APIsKey, data: { contact_card: agencyById?.data?.data?.contact_card, country: agencyById?.data?.data?.country } }} />
                                </>
                                :
                                <>
                                    <PageTitleWireframe />
                                    <ContactsWireFrame />
                                </>
                            }

                        </div>
                        {/* RatingOverview */}
                        <div>
                            {agencyById ?
                                <>
                                    <PagesTitle title='Rating Overview' />
                                    <div className='pl-md-4'>
                                        <RatingOverview APIs={{ APIsKey, data: { reviews: agencyById?.data?.data?.reviews } }} />
                                    </div>
                                </>
                                :
                                <>
                                    <PageTitleWireframe />
                                    <RatingOverviewWireFrame />
                                </>

                            }
                            <HorizentalLine classes='sm' />
                        </div>
                        {/* Our properties  */}
                        <div>
                            { agencyById ?
                                <PagesTitle title='Agency Properties' smallUnderTitle={agencyProperties?.data?.data?.count_properties + ' Properties'}/>
                                :
                                <PageTitleWireframe/>
                            }
                            {checkEmpty.properties ?
                                <>
                                    <nav id='propNavHolder' className='uCT-b-radius-sB CT-blog-nav cCT-smoke-bg'>
                                        <div id='propNavWhiteHolder' className='shadow-sm cCT-white-bg uCT-f-radius-sB mb-4'>
                                            <ProfiileMiniNav />
                                        </div>
                                    </nav>
                                    <div className='uCT-cards-grid justify-content-center pb-4 mb-n4'>
                                        { agencyById ?
                                        <>
                                            {agencyProperties?.data?.data?.properties?.slice(0,6)?.map((agencyProperties, index) => {
                                                return <PropertyGridCard  APIs={{APIsKey, data: {agencyProperties : agencyProperties}}} key={index}/>
                                            })}
                                        </> 
                                        : 
                                        <div className='uCT-card-grid-holder w-100'>
                                            {Array(6).fill().map((elem, index) => {
                                                return <PropertyCardWireFrame key={index} />
                                            })}
                                        </div>
                                        }
                                        {/* dummy Cards */}
                                        <div className='uCT-mini-dummy'></div>
                                        <div className='uCT-mini-dummy'></div>
                                    </div>
                                    <ButtonRightIcon innerText='Load More Properties' btnICON='iCT-down-arrow' btnCOLOR='cCT-Gradian-Linear-Main' iconBgCOLOR='cCT-inherit-bg' position='center' />
                                </>
                                :
                                <PlaceHolder textContent='No Properties Yet...'/>
                            }
                            <HorizentalLine classes='sm' />
                        </div>
                        {/* Our Agents */}
                        <div>
                            {agencyById ?
                                <PagesTitle title='Agents' smallUnderTitle={agencyById?.data?.data?.agents?.length + ' Agents'}/>
                                :
                                <PageTitleWireframe />
                            }
                            { checkEmpty.agents ?
                                <div className='uCT-cards-grid pb-4 justify-content-center mb-n4'>
                                    {agencyById ?
                                        <>
                                            {agencyById?.data?.data?.agents?.slice(0,6)?.map((agentsFromAgency, index) => {
                                                return <MiniAgentCard  APIs={{APIsKey, data: {agentsFromAgency : agentsFromAgency}}} key={index}/>
                                            })}
                                        </>
                                        :
                                        <div className='uCT-card-grid-holder w-100'>
                                            {Array(6).fill().map((elem, index) => {
                                                return <MiniAgentCardWireFrame key={index} />
                                            })}
                                        </div>
                                    }
                                    {/* dummy Cards */}
                                    <div className='uCT-mini-dummy'></div>
                                    <div className='uCT-mini-dummy'></div>
                                    <ButtonRightIcon innerText='Load More Agents' btnICON='iCT-down-arrow' btnCOLOR='cCT-Gradian-Linear-Main' iconBgCOLOR='cCT-inherit-bg' position='center' />
                                </div>
                                :
                                <PlaceHolder textContent='No Agents Yet...'/>
                            }
                            <HorizentalLine classes='sm' />
                        </div>
                        {/* Agency Reviews */}
                        <div>
                            {agencyById ?
                                <PagesTitle title='Agency Reviews' smallUnderTitle={agencyById?.data?.data?.reviews.all_review?.length + ' Reviews'}/>
                                :
                                <PageTitleWireframe />
                            }
                            { checkEmpty.agency_reviews ?
                            <div>
                                {agencyById ?
                                    <>
                                        <div className='uCT-cards-grid pt-4 justify-content-center mb-n4'>
                                            {agencyById?.data?.data?.reviews?.all_review?.slice(0,6)?.map((agencyReviews, index) => {
                                                return <ReviewCard  APIs={{APIsKey, data: {agencyReviews : agencyReviews}}} key={index}/>
                                            })}
                                            {/* dummy Cards */}
                                            <div className='uCT-mini-dummy'></div>
                                            <div className='uCT-mini-dummy'></div>
                                        </div>
                                    </>
                                    :
                                    <>
                                        <PageTitleWireframe />
                                        <div className='uCT-cards-grid pt-4 justify-content-center mb-4'>
                                            {Array(6).fill().map((elem, index) => {
                                                return <ReviewCardWireFrame key={index} />
                                            })}
                                        </div>
                                    </>
                                }
                                <ButtonRightIcon innerText='Load More Reviews' btnICON='iCT-down-arrow' btnCOLOR='cCT-Gradian-Linear-Main' iconBgCOLOR='cCT-inherit-bg' position='center' />
                            </div>
                            :
                            <PlaceHolder textContent='No Agency Reviews Yet...'/>
                            }
                        </div>
                    </section>
                </div>
            </div>
        )
    }
    return agencyProfileTemp()
}