import { MediaCT } from 'view/viewLogic/view-logic'
export default function PropertyLocationWireFrame(){
    const propertyLocationWireFrameTemp = () => {
        return(
            <div className="uCT-wire-frame-opacity-5">
                <div className="position-relative">
                    {/* Background Image */}
                    <iframe className={`d-flex border-0 uCT-f-radius-sC ${MediaCT('lg') ? ' aCT-wire-frame-loading cCT-layout-I CT-propertyMap-sm-holder' : ' aCT-wire-frame-loading-cover cCT-layout-I CT-propertyMap-lg-holder'}`}>
                    </iframe>
                </div>
                {/* Property Location Icons Row*/}
                <div className="col pr-0 pl-2 pl-lg-4 pt-4">
                    {/* Location Icons Row One*/}
                <p style={{ width: '200px' }} className='fCT-wireFrame-hB uCT-f-radius shadow-sm aCT-wire-frame-loading cCT-layout-I'></p>
                    {/* Location Icons Row Two*/}
                <p style={{ width: '140px' }} className='fCT-wireFrame-hB uCT-f-radius shadow-sm aCT-wire-frame-loading cCT-layout-I'></p>
                    {/* Location Icons Row Three*/}
                <p style={{ width: '170px' }} className='fCT-wireFrame-hB uCT-f-radius shadow-sm aCT-wire-frame-loading cCT-layout-I'></p>
                </div>
            </div>
        )
    }
    return propertyLocationWireFrameTemp()
} 