import './OurVision.feature.scss'
import Icon from '../../../shared/Icon/Icon'
import $ from 'jquery';
import ourVisionImg from '../../../../../assets/images/OurVisionChart.svg'
import { useEffect } from 'react';

export default function OurVisionFeature() {
    const visionSvg = () => {
        let visionSVG = document.getElementById('visionSVG')
        visionSVG.addEventListener("load", () => {
            let _SVGImageI = visionSVG.contentDocument.children[0].children[6].children[0]
            _SVGImageI.animate([
                { transform: 'translateY(100px)', opacity: '0' },
                { opacity: '0' },
                { transform: 'translateY(0px)', opacity: '1' }
            ], {
                // timing options
                duration: 500,
            });
            let _SVGImageII = visionSVG.contentDocument.children[0].children[5].children[0]
            _SVGImageII.animate([
                { transform: 'translateY(150px)', opacity: '0' },
                { opacity: '0' },
                { transform: 'translateY(0px)', opacity: '1' }
            ], {
                // timing options
                duration: 800,
            });
            let _SVGImageIII = visionSVG.contentDocument.children[0].children[4].children[0]
            _SVGImageIII.animate([
                { transform: 'translateY(200px)', opacity: '0' },
                { opacity: '0' },
                { transform: 'translateY(0px)', opacity: '1' }
            ], {
                // timing options
                duration: 1000,
            });
            let _SVGImageIV = visionSVG.contentDocument.children[0].children[3].children[0]
            _SVGImageIV.animate([
                { transform: 'translateY(250px)', opacity: '0' },
                { opacity: '0' },
                { transform: 'translateY(0px)', opacity: '1' }
            ], {
                // timing options
                duration: 1200,
            });
            let _SVGImageV = visionSVG.contentDocument.children[0].children[2].children[0]
            _SVGImageV.animate([
                { transform: 'translateY(300px)', opacity: '0' },
                { opacity: '0' },
                { transform: 'translateY(0px)', opacity: '1' }
            ], {
                // timing options
                duration: 1400,
            });
            let _SVGImageVI = visionSVG.contentDocument.children[0].children[1].children[0]
            _SVGImageVI.animate([
                { transform: 'translateY(350px)', opacity: '0' },
                { opacity: '0' },
                { transform: 'translateY(0px)', opacity: '1' }
            ], {
                // timing options
                duration: 1600,
            });
            let _SVGImageVII = visionSVG.contentDocument.children[0].children[0].children[0]
            _SVGImageVII.animate([
                { transform: 'translateY(400px)', opacity: '0' },
                { opacity: '0' },
                { transform: 'translateY(0px)', opacity: '1' }
            ], {
                // timing options
                duration: 2000,
            });

            $('#visionSVG').addClass('visible')
            $('#visionSVG').removeClass('invisible')
        })

    }
    useEffect(() => { visionSvg() })
    const ourVisionFeatureTemp = () => {
        return (
            <div className="px-3 pt-3 pt-lg-4 px-lg-4 pl-xl-5 mt-xl-5 d-xl-flex flex-xl-column justify-content-xl-start h-100 position-relative overflow-hidden">
                {/* Layer Shape */}
                <div className="imCT-our-missi~on-layer CT-our-vision-layer-shape"></div>
                <div className="CT-ourVision-holder imCT-our-vision~ h-100">
                    <object id='visionSVG' data={ourVisionImg} className='invisible~ uCT-img~-100 sas' ></object>
                    <div className="imCT-line-group CT-ourVision-line w-100 h-100"></div>
                    <div className="pb-3 pb-xl-0 ml-xl-2">
                        <p className="mb-0 CT-bold-72">What we expect <br /> for our <span className='cCT-secondary-text'>Clients</span> ?!</p>
                    </div>
                    <div className="pl-0 pt-lg-4 pl-lg-4 pt-xl-5 mt-xl-3 ml-xl-2 CT-vision-description-text">
                        <Icon iconSize='iCT-sC CT-ourVision-iconSize-small mr-2 mr-lg-3 mr-xl-4' textPosition='right' spaces='' innerText='The standard chunk of Lorem Ipsum' />
                        <Icon iconSize='iCT-sC CT-ourVision-iconSize-small mr-2 mr-lg-3 mr-xl-4' textPosition='right' spaces='' innerText='The standard chunk of Lorem Ipsum' />
                        <Icon iconSize='iCT-sC CT-ourVision-iconSize-small mr-2 mr-lg-3 mr-xl-4' textPosition='right' spaces='' innerText='The standard chunk of Lorem Ipsum' />
                        <Icon iconSize='iCT-sC CT-ourVision-iconSize-small mr-2 mr-lg-3 mr-xl-4' textPosition='right' spaces='' innerText='The standard chunk of Lorem Ipsum' />
                        <Icon iconSize='iCT-sC CT-ourVision-iconSize-small mr-2 mr-lg-3 mr-xl-4' textPosition='right' spaces='' innerText='The standard chunk of Lorem Ipsum' />
                    </div>
                </div>

            </div>
        )
    }
    return ourVisionFeatureTemp()
}