import Home from 'view/pages/Home/Home'
import AgentList from 'view/pages/AgentList/AgentList'
import Dash from 'view/dash'
import AgencyProfile from 'view/pages/profiles/AgencyProfile/AgencyProfile'
import AgentProfile from 'view/pages/profiles/AgentProfile/AgentProfile'
import PropertyList from 'view/pages/Properties-List/PropertiesList'
import AuthPage from 'view/pages/auth/Auth'
import BlogList from 'view/pages/Blog-List/BlogList'
import Notification from 'view/pages/Notifications/Notification'
import BlogArticle from 'view/pages/Blog-Article/Blog-Article'
import AgenciesList from 'view/pages/Agencies-List/AgenciesList'
