import AdvancedSearchStyle2Feature from 'view/features/AdvancedSearch/AdvancedSearchStyle2/AdvancedSearchStyle2.feature'
// Reducers
export const RoutesReducers = (state = {}, action) => {
    switch (action.type) {
        case 'route/layout': state.componentsWithoutLayout = action.payload; break
        case 'route/navPosition': state.navPosition = action.payload; break

        default: return state
    }
    return state
}
const routes = {
    data: [
        {
            key: Home,
            path: "/"

        },
        {
            key: AgentList,
            path: "/agents-list"
        },
        {
            key: AgencyProfile,
            path: "/agency/:id"
        },
        {
            key: AgentProfile,
            path: "/agent/:id"
        },
        {
            key: PropertyList,
            path: "/properties-list"
        },
        {
            key: AuthPage,
            path: "/login"
        },
        {
            key: AuthPage,
            path: "/register"
        },
        {
            key: Notification,
            path: "/notification"
        },
        {
            key: BlogList,
            path: "/blog"
        },
        {
            key: BlogArticle,
            path: "/blog-article"
        },
        {
            key: AgenciesList,
            path: "/agencies-list"
        },
        {
            key: CompareListing,
            path: "/compare-listing"
        },
        {
            key: PropertyDetails,
            path: "/property-details/:id"
        },
        {
            key: MapView,
            path: "/map"
        },
        {
            key: HowItWorks,
            path: "/how-it-works"
        },
        {
            key: MarketingAndServicePage,
            path: "/services"
        },
        {
            key: Dash,
            path: "/dash"
        },
        // Carousel Page Layout !ROUTER
        {
            key: CarouselPageLayout,
            path: "/about-us"
        },
        {
            key: AdvancedSearchStyle2Feature,
            path: "/search"
        }
    ]
}

export default routes;