import React, { useState, useEffect } from 'react';
import AgentProfileCard from 'view/features/profiles/Profile-Card/agent/AgentProfileCard'
import ProfileCover from 'view/features/profiles/Profile-Cover/ProfileCover'
import PagesTitle from 'view/features/shared/pagesTitle/PagesTitle'
import PageTitleWireframe from 'view/features/shared/pagesTitle/PageTitle-wireFrame/PageTitle.wireframe'
import ProfiileSideNav from 'view/features/profiles/Profiile-Side-Nav/ProfiileSideNav'
import ProfileSideNavWireFrame from 'view/features/profiles/Profiile-Side-Nav/ProfileSideNav-WireFrame/ProfileSideNav.WireFrame'
import ProfiileContentNav from '../../../features/profiles/Profile-Content-Nav/ProfileContentNav'
import ProfiileContentNavWireFrame from '../../../features/profiles/Profile-Content-Nav/ProfileContentNav-WireFrame/ProfileContentNav.WireFrame'
import ProfiileAbout from 'view/features/profiles/Profile-About/ProfileAbout'
import ProfiileMiniNav from 'view/features/profiles/Profiile-Mini-Nav/ProfileMiniNav'
import PropertyGridCard from 'view/features/property/Property-Grid-Card/PropertyGridCard'
import MiniAgentCard from 'view/features/profiles/Mini-Agent-Card/MiniAgentCard'
import ReviewCard from 'view/features/profiles/Review-Card/ReviewCard'
import ReviewCardWireFrame from 'view/features/profiles/Review-Card/ReviewCard-WireFrame/ReviewCard.WireFrame'
import { SideNavScreenHeight, giveClassOnScroll, MediaCT } from 'view/viewLogic/view-logic'
import Overview from 'view/features/profiles/Overview/Overview'
import RatingOverview from 'view/features/profiles/Rating-Overview/RatingOverview'
import Specialties from 'view/features/profiles/Specialties/Specialties'
import Contacts from 'view/features/profiles/Contacts/Contacts'
import HorizentalLine from 'view/features/shared/HorizentalLine/HorizentalLine'
import AgencyListView from '../../../features/agency/Agency-List-View/AgencyListView'
import ProfileCoverWireFrame from 'view/features/profiles/Profile-Cover/ProfileCover-WireFrame/ProfileCover.WireFrame'
import ProfileAboutWireFrame from 'view/features/profiles/Profile-About/ProfileAbout-WireFrame/ProfileAbout.WireFrame'
import OverviewWireFrame from 'view/features/profiles/Overview/Overview-WireFrame/Overview.WireFrame'
import RatingOverviewWireFrame from 'view/features/profiles/Rating-Overview/RatingOverview-WireFrame/RatingOverview.WireFrame'
import ContactsWireFrame from 'view/features/profiles/Contacts/Contacts-WireFrame/Contacts.WireFrame'
import SpecialtiesWireFrame from 'view/features/profiles/Specialties/Specialties-WireFrame/Specialties.WireFrame'
import AgencyListWireFrame from '../../../features/agency/Agency-List-View/AgencyList-WireFrame/AgencyList.WireFrame'
import PropertyCardWireFrame from 'view/features/property/Property-Grid-Card/PropertyCard-wireframe/PropertyCard.wireframe'
import AgentProfileWireFrame from 'view/features/profiles/Profile-Card/agent/AgentProfile-WireFrame/AgentProfile.WireFrame'
import LeftWidgetLayout from 'view/layout/Components/widgets/Left-Widget/LeftWidget.layout'
import ButtonRightIcon from 'view/features/shared/buttons/Button-Right-Icon/ButtonRightIcon'
import GoogleMapView from 'view/features/google-map/Google-Map/GoogleMapView'
import SendMessagePopup from 'view/layout/Components/popups/SendMessage/SendMessage.popup'
import { _GETID_Agent, _GET_Agent_Properties } from 'Controllers/services/agent/agent.services'
import PlaceHolder from 'view/features/shared/PlaceHolder/PlaceHolder.feature'


import { useParams } from "react-router-dom";
export default function AgentProfile() {

    // PopUp Handler ===START
    const [togglePopup, setTogglePopup] = useState(false);
    const [APIsKey, setAPIsKey] = useState('AgentProfile')
    const [checkEmpty, setCheckEmpty] = useState(new Object({
        properties : false,
        agencies : false,
        agent_reviews : false
    }));

    const popupHandler = () => {
        setTogglePopup(true)
        setTimeout(() => {
            setTogglePopup(false)
        }, 10);
    }
    
    // PopUp Handler ===END

    function onScroll() {
        giveClassOnScroll('propNavHolder', 'p-4 shadow cCT-Gradian-Linear-supp2', 'cCT-smoke-bg', 80)
        giveClassOnScroll('propNavWhiteHolder', '', 'cCT-white-bg mb-4 shadow-sm', 104)
    }

    let { id } = useParams();

    const [agentPayloadByID , setAgentPayloadByID] = useState();
    const [agentProperties, setAgentProperties] = useState()

    const amentiesCounts = {
        buy_count: agentPayloadByID?.data?.data?.buy_count,
        commercial_count: agentPayloadByID?.data?.data?.commercial_count,
        rent_count: agentPayloadByID?.data?.data?.rent_count
    }

    useEffect(() => {
        _GETID_Agent(id)
        .then(res =>{
            setAgentPayloadByID(res)
            setCheckEmpty(prevVal => ({
                ...prevVal,
                agencies : res?.data?.data?.agents?.length != 0 && true,
                agent_reviews : res?.data?.data?.reviews?.all_review?.length != 0 && true,
            }))
            console.log(res);

        }).catch()
        _GET_Agent_Properties(id, 1, 1)
        .then(res =>{
            setAgentProperties(res)
            setCheckEmpty(prevVal => ({
                ...prevVal,
                properties : res?.data?.data?.properties?.length != 0 && true,
            }))
            console.log(res);

        }).catch()
    },[id])

    // USE_Effect call after render 
    useEffect(() => {
        window.addEventListener('scroll', onScroll);
        return () => {
            window.removeEventListener('scroll', onScroll);
        }
    }, []);

    const leftWidgetLayoutContent = () => {
        return (
            <aside className={`${MediaCT('md') ? 'w-100' : false}`}>
                {/* profile Card */}
                {agentPayloadByID ?
                    <>
                        <AgentProfileCard APIs={{ APIsKey, data: { profile_card: agentPayloadByID?.data?.data?.profile_card } }} />
                        {/* ProfiileContentNav */}
                        <ProfiileContentNav APIs={{ APIsKey, data: { contact_card: agentPayloadByID?.data?.data?.contact_card } }} popupHandlers={popupHandler} />
                        {/* profile nav */}
                        <ProfiileSideNav />
                    </>
                 :
                    <>
                        <AgentProfileWireFrame />
                        <ProfiileContentNavWireFrame />
                        <ProfileSideNavWireFrame />
                    </>
                 }

            </aside>
        )
    }
    const agentProfileTemp = () => {
        return (
            <div className='container p-md-4 p-3'>
                {/* {console.log(singleAgent)} */}
                <div className='row m-0  flex-md-row flex-column'>
                     
                    {/* Popup Section */}
                    <div>
                        <SendMessagePopup togglePopups={togglePopup} APIs={{ APIsKey, data: { profile_card: agentPayloadByID?.data?.data?.profile_card } }} />
                    </div>
                    {/* left widget */}
                    <LeftWidgetLayout content={leftWidgetLayoutContent()} />
                    {/* content */}
                    <section className='CT-content'>
                        {/* cover */}
                        <div>
                            {agentPayloadByID ? <ProfileCover APIs={{ APIsKey, data: { image: agentPayloadByID?.data?.data?.image } }} /> : <ProfileCoverWireFrame />}
                        </div>
                        {/* About Us */}
                        <div className=''>
                            {agentPayloadByID ?
                                <>
                                    <PagesTitle title='About Me' />
                                    <ProfiileAbout APIs={{ APIsKey, data: { about_me: agentPayloadByID?.data?.data?.about_me } }} />
                                </>
                                :
                                <>
                                    <PageTitleWireframe />
                                    <ProfileAboutWireFrame />
                                </>
                            }
                        </div>
                        {/* Overview */}
                        <div>
                            {agentPayloadByID ?
                                <>
                                    <PagesTitle title='Overview' />
                                    <Overview APIs={[amentiesCounts]} APIs={{ APIsKey, data: { amentiesCounts: amentiesCounts } }} />
                                </>
                                :
                                <>
                                    <PageTitleWireframe />
                                    <OverviewWireFrame />
                                </>
                            }
                        </div>
                        {/* Specialties */}
                        <div>
                            {agentPayloadByID ?
                                <>
                                    <PagesTitle title='Specialties & Service Areas' />
                                    <Specialties />
                                </>
                                :
                                <>
                                    <PageTitleWireframe />
                                    <SpecialtiesWireFrame />
                                </>
                            }
                        </div>
                        {/* Contacts */}
                        <div>
                            {agentPayloadByID ?
                                <>
                                    <PagesTitle title='Contacts' />
                                    <Contacts APIs={{ APIsKey, data: { contact_card: agentPayloadByID?.data?.data?.contact_card, address: agentPayloadByID?.data?.data?.address } }} />
                                </>
                                :
                                <>
                                    <PageTitleWireframe />
                                    <ContactsWireFrame />
                                </>}
                        </div>
                        {/* RatingOverview */}
                        <div>
                            {agentPayloadByID ?
                                <>
                                    <PagesTitle title='Agent Rate' />
                                    <div className='pl-md-4'>
                                        <RatingOverview APIs={{ APIsKey, data: { reviews: agentPayloadByID?.data?.data?.reviews } }} />
                                    </div>
                                </>
                                :
                                <>
                                    <PageTitleWireframe />
                                    <RatingOverviewWireFrame />
                                </>}
                            <HorizentalLine classes='sm' />
                        </div>
                        {/* Our properties  */}
                        <div>
                            <PagesTitle title='Agent Properties' smallUnderTitle={agentProperties?.data?.data?.properties?.length + ' Properties'}/>
                            {checkEmpty.properties ? 
                            <div className='uCT-cards-grid justify-content-center pb-4 mb-n4'>
                                <nav id='propNavHolder' className='uCT-b-radius-sB CT-blog-nav cCT-smoke-bg w-100'>
                                    <div id='propNavWhiteHolder' className='shadow-sm cCT-white-bg uCT-f-radius-sB mb-4'>
                                        <ProfiileMiniNav />
                                    </div>
                                </nav>
                                { agentProperties ? 
                                agentProperties?.data?.data?.properties?.map(properties => {
                                        return <PropertyGridCard key={properties?.id}  APIs={{APIsKey, data: {properties : properties}}}/>
                                }) :
                                Array(6).fill().map((w, i)=>{
                                    return <PropertyCardWireFrame key={i}/>
                                })
                                }
                                {/* dummy Cards */}
                                <div className='uCT-mini-dummy'></div>
                                <div className='uCT-mini-dummy'></div>
                                <ButtonRightIcon innerText='Load More Properties' btnICON='iCT-down-arrow' btnCOLOR='cCT-Gradian-Linear-Main' iconBgCOLOR='cCT-inherit-bg' position='center' />
                            </div>
                            :
                            <PlaceHolder textContent='No Properties Yet...'/>
                            }
                            <HorizentalLine classes='sm' />
                        </div>
                        {/* Agency List View */}
                        <div>
                            {agentPayloadByID ?
                                <>
                                    <PagesTitle title='Agencies' />
                                    <div className=' d-sm-block d-flex w-100 justify-content-center'>
                                        <AgencyListView />
                                    </div>
                                </>
                                :
                                <>
                                    <PageTitleWireframe />
                                    <AgencyListWireFrame />
                                </>}
                            <HorizentalLine classes='sm' />
                        </div>
                        {/* Agent Reviews */}
                        <div>
                            <PagesTitle title='Agent Reviews' smallUnderTitle={agentPayloadByID?.data?.data?.reviews?.all_review?.length + ' Reviews'}/>
                            {checkEmpty.agent_reviews ? 
                            <div>
                                {agentPayloadByID ?
                                    <>
                                        <div className='uCT-cards-grid pt-4 justify-content-center mb-n4'>
                                            {agentPayloadByID?.data?.data?.reviews?.all_review?.slice(0,6)?.map((agentReviews, index) => {
                                                return <ReviewCard  APIs={{APIsKey, data: {agentReviews : agentReviews}}} key={index}/>
                                            })}
                                            {/* dummy Cards */}
                                            <div className='uCT-mini-dummy'></div>
                                            <div className='uCT-mini-dummy'></div>
                                        </div>
                                    </>
                                    :
                                    <>
                                        <PageTitleWireframe />
                                        <div className='uCT-cards-grid pt-4 justify-content-center mb-n4'>
                                            <ReviewCardWireFrame />
                                            <ReviewCardWireFrame />
                                            <ReviewCardWireFrame />
                                            <ReviewCardWireFrame />
                                            <div className='uCT-mini-dummy'></div>
                                            <div className='uCT-mini-dummy'></div>
                                        </div>
                                    </>
                                }
                                <ButtonRightIcon innerText='Load More Reviews' btnICON='iCT-down-arrow' btnCOLOR='cCT-Gradian-Linear-Main' iconBgCOLOR='cCT-inherit-bg' position='center' />
                            </div>
                            :
                            <PlaceHolder textContent='No Agent Reviews Yet...'/>
                            }
                        </div>
                        {/* GoogleMap */}
                        {/* <div className={`h-25`} style={{position: 'fixed'}}>
                            <GoogleMapView />
                        </div> */}
                    </section>
                </div>
            </div>
        )
    }
    return agentProfileTemp()
}