export default function ReviewCardWireFrame(){
    const reviewCardWireFrameTemp = () => {
        return(
            <div style={{ width: '330px' }} className='d-flex justify-content-center CT-review-card-holder pb-4 uCT-f-radius-sB'>
                {/* image */}
                <div className='d-flex position-relative CT-review-img-holder '>
                    <i  className='shadow-sm cCT-layout-I position-absolute rounded-circle CT-review-img cCT-smoke-bg uCT-img-cover'></i>
                </div>
                {/* content */}
                <div style={{ zIndex: '-1' }} className='uCT-wire-frame-opacity-5 cCT-layout-I uCT-f-radius-sB CT-review-card-content-holder ml-auto mt-auto d-flex flex-column'>
                    <div className='CT-review-card-content ml-auto pt-3 pl-3 pr-3 d-flex'>
                        <div className={''}>
                            {/* name */}
                            <p style={{ width: '110px' }} className={`mb-2 aCT-wire-frame-loading fCT-wireFrame-hB cCT-layout-I uCT-f-radius`}></p>
                            {/* time */}
                            <p style={{ width: '80px' }} className={`ml-2 aCT-wire-frame-loading fCT-wireFrame-hB cCT-layout-I uCT-f-radius`}></p>
                        </div>
                        {/* stars */}
                        <div className='d-flex ml-auto aCT-wire-frame-opacity  '>
                                <i  className='CT-star uCT-img-cover mr-1'></i>
                                <i  className='CT-star uCT-img-cover mr-1'></i>
                                <i  className='CT-star uCT-img-cover mr-1'></i>
                                <i  className='CT-star uCT-img-cover mr-1'></i>
                                <i  className='CT-star uCT-img-cover mr-1'></i>
                         
                        </div>
                    </div>
                    {/* review comment */}
                    <div style={{ width: '100%', height: '121px' }} className="d-flex align-items-center justify-content-center ">
                        <p style={{ width: '200px'}} className={`aCT-wire-frame-loading fCT-wireFrame-hC cCT-layout-I uCT-f-radius`}></p>
                    </div>
                </div>
            </div>
        )
    }
    return reviewCardWireFrameTemp()
}