import './mobile-down-navbar-layout.sass'
import Icon from 'view/features/shared/Icon/Icon'
import { useMediaQuery } from 'react-responsive';
export default function MobileDownNavBarLayout() {
    const isMobileSmall = useMediaQuery({ query: '(max-width: 300px)' });
    const mobileDownNavBarLayoutTemp = () => {
        return (
            <div className='container-fluid d-flex CT-nav-bg-color align-items-center justify-content-center CT-Holder-h-Navbar px-4'>
                {/* Third Section */}
                <div className=" align-items-center justify-content-center d-flex w-100">
                    {/* menu */}
                    <nav className={`d-block ${isMobileSmall ? 'mx-4' : 'mx-5'}`}>
                        <Icon iconName='iCT-burger-Menu' iconSize='iCT-sB' spaces='iCT-sE mx-4~' radiusSize='rounded-circle' />
                    </nav>
                    {/* List Your Property BTN */}
                    <a href="#" className={`${isMobileSmall ? 'mx-4' : 'mx-5'} uCT-f-radius-sC border-0 shadow btn CT-BTN-Holder cCT-Gradian-Linear-Main cCT-white-text d-flex align-items-center justify-content-center`}>
                        <i className="iCT-add-white-mobile uCT-img-cover iCT-sB"></i>
                    </a>
                    {/* Holder Icons */}
                    <div className={`d-flex ${isMobileSmall ? 'mx-4' : 'mx-5'} align-items-center`}>
                        {/* Personal Information DropMenu */}
                        <ul className="navbar-nav">
                            <li className="nav-item dropdown">
                                {/* Image Profile */}
                                <div className="nav-link uCT-click d-flex align-items-center p-0 h-100" href="#" data-toggle="dropdown">
                                    <i className="imCT-Profile uCT-img-100 CT-Border-3 rounded-circle iCT-sF"></i>
                                </div>
                                {/* DropMenu */}
                                <div className="dropdown-menu CT-profile-menu" aria-labelledby="navbarDropdown">
                                    <a className="dropdown-item" href="#">Settings</a>
                                    <a className="dropdown-item" href="#">Information</a>
                                </div>
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
        )
    }
    return mobileDownNavBarLayoutTemp()
}
